import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import UserDepartmans from './UserDepartmans/UserDepartmans';
import UserTable from './UserTable/UserTable';

export default function UserSettings() {
  const breadcrumbs = [
    { label: <IntlMessages id={'settings'} />, link: '/' },
    { label: <IntlMessages id={'usersettings'} />, isActive: true },
  ];
  let [reloadValues, setReloadValues] = useState('');

  return (
    <div>
      <PageContainer heading={<IntlMessages id={'usersettings'} />} breadcrumbs={breadcrumbs}>
        <UserTable reloadValues={reloadValues} setReloadValues={setReloadValues} />
        <UserDepartmans reloadValues={reloadValues} />
      </PageContainer>
    </div>
  );
}
