import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { useDispatch } from 'react-redux';
import { Box, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core';
import { Button, Link } from '@mui/material';
import { AuhMethods } from '../../../../services/auth';
import ContentLoader from '../../ContentLoader';
import { alpha, makeStyles } from '@material-ui/core/styles';
import CmtImage from '../../../../@coremat/CmtImage';
import Typography from '@material-ui/core/Typography';
import { CurrentAuthMethod } from '../../../constants/AppConstants';
import AuthWrapper from './AuthWrapper';
import { useHistory } from 'react-router';
import { useEffect } from 'react';
import * as api from '../../../../api/api';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import LanguageSwitcher from '@jumbo/components/AppLayout/partials/LanguageSwitcher';
import IntlMessages from '@jumbo/utils/IntlMessages';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import GoogleChrome from '../../../../assets/Chrome_Logo.svg';
import Firefox from '../../../../assets/Firefox_Logo.svg';
const useStyles = makeStyles(theme => ({
  authThumb: {
    backgroundColor: alpha(theme.palette.primary.main, 0.12),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '50%',
      order: 2,
    },
  },

  authContent: {
    padding: 30,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: props => (props.variant === 'default' ? '50%' : '100%'),
      order: 1,
    },
    [theme.breakpoints.up('xl')]: {
      padding: 50,
    },
  },
  titleRoot: {
    marginBottom: 14,
    color: theme.palette.text.primary,
  },
  textFieldRoot: {
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: alpha(theme.palette.common.dark, 0.12),
    },
  },
  formcontrolLabelRoot: {
    '& .MuiFormControlLabel-label': {
      [theme.breakpoints.down('xs')]: {
        fontSize: 12,
      },
    },
  },
}));
const SignIn = ({ method = CurrentAuthMethod, variant = 'default', wrapperVariant = 'default' }) => {
  let history = useHistory();
  const [param, setParam] = useState({
    name: '',
    password: '',
    type: 'console',
  });
  const dispatch = useDispatch();
  const [loginState, setLoginState] = useState();
  let data = [];
  var myDate = new Date();
  myDate.setDate(myDate.getDate() + 1);
  data.push(myDate);

  const classes = useStyles({ variant });

  const onSubmit = () => {
    api
      .getLogin(param)
      .then(resp => {
        if (resp.status === 200) {
          setLoginState(resp.status);
          localStorage.setItem('company', resp.data.data?.company || '');
          localStorage.setItem('username', param.name || '');
          localStorage.setItem('token', resp.data.data.token);
          var tomorrow = new Date();
          tomorrow.setDate(tomorrow.getDate() + 1);
          localStorage.setItem('exptime',tomorrow.getTime());
          if (resp.data.data?.company) {
            if(document.title.charAt(7) === "-"){
              document.title = document.title.replace('-', `${resp.data.data?.company} -`);
            }else{
              var newTitle = `ESNEKS ${resp.data.data?.company} - V${process.env.REACT_APP_VERSION} - Admin Dashboard`
              document.title = newTitle;
            }
          }
          history.push('/dashboard');
        }
      })
      .catch(response => {
        NotificationManager.error(<IntlMessages id={'wrongpassword'} />, '', 3000);
      });
  };

  function addNotification() {
    onSubmit();
  }

  if (loginState === 200) {
    localStorage.setItem('login', loginState);
  }

  let [, setVersion] = useState('0.0.0');

  const [open, setOpen] = useState(true);

  const handleClose = () => {
    setOpen(false);
  };

  const browser = (function() {
    const test = function(regexp) {
      return regexp.test(window.navigator.userAgent);
    };
    switch (true) {
      case test(/edg/i):
        return 'Edge';
      case test(/opr/i) || test(/opera/i):
        return 'Opera';
      case test(/chrome/i) && !!window.chrome:
        return 'Chrome';
      case test(/MSIE/i):
        return 'Internet Explorer 10-';
      case test(/trident/i):
        return 'Internet Explorer 11';
      case test(/firefox/i):
        return 'Firefox';
      case test(/safari/i):
        return 'Safari';
      default:
        return 'Other';
    }
  })();

  useEffect(() => {
    if (browser === 'Chrome' || browser === 'Firefox') {
      handleClose();
    }
    api.getVersion().then(response => {
      setVersion(response.data.version);
    });
  }, [browser]);

  return (
    <AuthWrapper variant={wrapperVariant}>
      <NotificationContainer />

      {variant === 'default' ? (
        <Box className={classes.authThumb}>
          <CmtImage src={'/images/auth/login-img.png'} />
        </Box>
      ) : null}
      <Box className={classes.authContent}>
        <Box mb={1}>
          <CmtImage src={'/images/favicon.png'} />
        </Box>
        <Typography component="div" variant="h1" className={classes.titleRoot} style={{ fontFamily: 'Roboto' }}>
          <IntlMessages id={'login'} />
        </Typography>
        <form
          onSubmit={e => {
            e.preventDefault();
            addNotification(e);
          }}>
          <Box mb={1}>
            <TextField
              label={<IntlMessages id={'username'} />}
              fullWidth
              onChange={event => {
                setParam({
                  name: event.target.value,
                  password: param.password,
                  type: param.type,
                });
              }}
              defaultValue={param.name}
              margin="normal"
              variant="outlined"
              className={classes.textFieldRoot}
            />
          </Box>
          <Box mb={1}>
            <TextField
              type="password"
              label={<IntlMessages id={'password'} />}
              fullWidth
              id="pass"
              onChange={event => {
                setParam({
                  name: param.name,
                  password: event.target.value,
                  type: param.type,
                });
              }}
              defaultValue={param.password}
              margin="normal"
              variant="outlined"
              className={classes.textFieldRoot}
            />
          </Box>
          <div style={{ textAlign: 'center' }}>
            <IntlMessages id={'language'} />:
            <LanguageSwitcher />
          </div>
          <Box display="flex" alignItems="center" justifyContent="space-between" mb={5}>
            <Button
              type="submit"
              variant="contained"
              color="primary">
              <IntlMessages id={'login'} />
            </Button>
            <Link underline="none" href={`${window.location.protocol}//${window.location.hostname}:3005`}>
              <IntlMessages id={'userNagentLogin'} />
            </Link>
          </Box>
        </form>
        <p style={{ textAlign: 'center' }}>V{process.env.REACT_APP_VERSION}</p>
        <Box>
        <Typography style={{ textAlign: 'center', marginBottom: '1rem' }} component="div" variant="h5">
          <IntlMessages id={'chooseBrowser'} />
        </Typography>
        <GridContainer>
          <Grid item xs={12} md={6} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <Avatar alt="Google Chrome" src={GoogleChrome} sx={{ mr: 1 }} /> <b>Google Chrome</b>
          </Grid>
          <Grid item xs={12} md={6} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <Avatar alt="Mozilla Firefox" src={Firefox} sx={{ mr: 1 }} /> <b>Mozilla Firefox</b>
          </Grid>
        </GridContainer>
      </Box>
        {dispatch(AuhMethods[method].getSocialMediaIcons())}

        <ContentLoader />
      </Box>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description">
        <DialogTitle id="alert-dialog-title" style={{ textAlign: 'center' }}>
          <IntlMessages id={'changeBrowser'} />
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description" component="div">
            <Typography style={{ textAlign: 'justify', marginBottom: '1rem' }} component="div" variant="h5">
              <IntlMessages id={'chooseBrowser'} />
            </Typography>
            <GridContainer>
              <Grid item xs={12} md={6} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Avatar alt="Google Chrome" src={GoogleChrome} sx={{ mr: 1 }} /> <b>Google Chrome</b>
              </Grid>
              <Grid item xs={12} md={6} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Avatar alt="Mozilla Firefox" src={Firefox} sx={{ mr: 1 }} /> <b>Mozilla Firefox</b>
              </Grid>
            </GridContainer>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>
            <IntlMessages id={'close'} />
          </Button>
        </DialogActions>
      </Dialog>
    </AuthWrapper>
  );
};

export default SignIn;
