import React, { createRef } from 'react';
import { Bar, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis, Label } from 'recharts';
import { Button, Card, CardContent } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { useScreenshot, createFileName } from 'use-react-screenshot';
import { CloudDownload } from '@material-ui/icons';
import { useIntl } from 'react-intl';
const Chart = ({ response }) => {
  let datalist = [];
  datalist = response.map(data => ({
    count: data.count,
    hour: parseInt(data.hour_log.split(' ')[1].split(':')[0]),
  }));

  for (let i = 0; i < 24; i++) {
    const found = datalist.some(data => parseInt(data.hour) === i);
    if (!found) {
      datalist.push({ count: 0, hour: i });
    }
  }
  datalist.sort(function(a, b) {
    return parseInt(a.hour) - parseInt(b.hour);
  });
  var maximum = Math.max.apply(
    Math,
    datalist.map(function(o) {
      return o.count;
    }),
  );

  const ref = createRef(null);
  const [, takeScreenShot] = useScreenshot({
    type: 'image/jpeg',
    quality: 1.0,
  });

  const download = (image, { name = 'img', extension = 'jpg' } = {}) => {
    const a = document.createElement('a');
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const downloadScreenshot = () => takeScreenShot(ref.current).then(download);
  let curentLanguage = useIntl();

  return (
    <>
      <div style={{ textAlign: 'right' }}>
        <Button style={{ backgroundColor: '#3f51b5', color: 'white', marginBottom: '10px' }} onClick={downloadScreenshot}>
          <IntlMessages id={'dowlandimage'} /> <CloudDownload />
        </Button>
      </div>
      <Card>
        <CardContent ref={ref}>
          <ResponsiveContainer width="100%" height={200}>
            <BarChart data={datalist} margin={{ top: 0, right: 0, left: 0, bottom: 0 }}>
              <XAxis dataKey="hour">
                <Label
                  value={
                    curentLanguage.locale === 'en-EN'
                      ? 'Hour'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Stunden'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Saat'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Uur'
                      : 'Hour'
                  }
                  position="insideBottomRight"
                  offset={0}
                />
              </XAxis>
              <YAxis
                domain={[0, maximum]}
                dataKey="count"
                label={{
                  value:
                    curentLanguage.locale === 'en-EN'
                      ? 'Count'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Z√§hlen'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Adet'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Count'
                      : 'Count',
                  angle: -90,
                  position: 'insideLeft',
                }}
              />
              <CartesianGrid strokeDasharray="3 3" />
              <Tooltip
                labelStyle={{ color: 'black' }}
                itemStyle={{ color: 'black' }}
                cursor={false}
                content={data => {
                  return data.payload[0] ? (
                    <div style={{ backgroundColor: 'white' }}>
                      <p>
                        <IntlMessages id={'hour'} />: {data.payload[0].payload.hour}
                      </p>
                      <p>
                        <IntlMessages id={'call.time'} />: {data.payload[0].payload.count}
                      </p>
                    </div>
                  ) : null;
                }}
              />
              <Legend />
              <defs>
                <linearGradient id="color1" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#6200EE" stopOpacity={1} />
                  <stop offset="95%" stopColor="#B819D2" stopOpacity={1} />
                </linearGradient>
              </defs>

              <Bar dataKey="count" fill="url(#color1)" />
            </BarChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </>
  );
};

export default Chart;
