import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import CallTerminationReportFilter from './Filter';
import CallTerminationReportTable from './Table';

export default function CallTerminationReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'callterminationreport'} />, isActive: true },
  ];
  const [data, setData] = useState([]);
  const [limit,setLimit] = useState(10);
  const [offset,setOffset] = useState(0);
  const [page,setPage] = useState(0);
  const [totalCount,setTotalCount]=useState(10);
  return (
    <div>
      <PageContainer heading={<IntlMessages id={'callterminationreport'} />} breadcrumbs={breadcrumbs}>
        <CallTerminationReportFilter setData={setData} setTotalCount={setTotalCount} limit={limit} offset={offset}/>
        <CallTerminationReportTable 
        data={data}
        totalCount={totalCount}
        limit={limit}
        setLimit={setLimit}
        page={page}
        setPage={setPage}
        offset={offset}
        setOffset={setOffset}
        />
      </PageContainer>
    </div>
  );
}
