import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import React, { useState } from 'react';
import Filter from './Filter';
import DataTable from './Table';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';

function DroppedCall() {
  const [responseSummary, setResponseSummary] = useState([]);
  const [response, setResponse] = useState([]);

  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState({ count: 0 });

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'droppedcall'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'droppedcall'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Filter
            setResponse={setResponse}
            limit={limit}
            offset={offset}
            setCount={setCount}
            setResponseSummary={setResponseSummary}
          />
        </Grid>
        <Grid item xs={12}>
          <DataTable
            datalist={response}
            setResponse={setResponse}
            page={page}
            setPage={setPage}
            setLimit={setLimit}
            limit={limit}
            setOffset={setOffset}
            offset={offset}
            count={count}
            responseSummary={responseSummary}
          />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
}

export default DroppedCall;
