import React, { useState, useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import {
  MenuItem,
  Grid,
  CardContent,
  Card,
  CircularProgress,
  FormControl,
  InputLabel,
  Select,
} from '@material-ui/core';
import { getAgentNumber, getQueueNumber, getAgentSession, getBreakName, getAgentSessionClicked } from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import { Formik, Form } from 'formik';
import IntlMessages from '@jumbo/utils/IntlMessages';
import AgentSessionReportDateFilter from './DateFilter';
import dateFormat from 'dateformat';

function Filter({
  setAgentSession,
  qnum,
  agent,
  setQnum,
  setAgent,
  setClickedResponse,
  clickedAgent,
  responseBreak,
  setResponseBreak,
}) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const classes = useStyles();
  const dateDifference = param => {
    var today = new Date();
    var paramDate = new Date(param);
    var diff = today.getTime() - paramDate.getTime();
    diff = Math.round(diff / (1000 * 3600 * 24));
    return diff - 1;
  };
  
  const [params, setParams] = useState({
    agent: '',
    queue: '',
    break: '',
  });
  useEffect(() => {
    getQueueNumber().then(response => setQnum(JSON.parse(response.data.data)));
    getAgentNumber().then(response => setAgent(JSON.parse(response.data.data)));
    getBreakName().then(response => {
      setResponseBreak(JSON.parse(response.data.data));
    });
  }, [setQnum, setAgent, setResponseBreak]);

  let [loading, setLoading] = useState(true);
  let [startTime, setStartTime] = useState(dateFormat(new Date(),'yyyy-mm-dd '));
  let [endTime, setEndTime] = useState(dateFormat(new Date(),'yyyy-mm-dd '));

  useEffect(() => {
    setLoading(true);
    getAgentSession(params.agent, params.queue, params.break, startTime, endTime).then(response => {
      setAgentSession(JSON.parse(response.data.data));
      setLoading(false);
    });

    getAgentSessionClicked(clickedAgent, startTime, endTime).then(resp => {
      setClickedResponse(JSON.parse(resp.data.data));
    });
  }, [clickedAgent, params, endTime, startTime, setAgentSession, setClickedResponse]);

  let [queueSelect, setQueueSelect] = useState('');
  let [agentSelect, setAgentSelect] = useState('');
  const [breakSelect, setBreakSelect] = useState('');

  const handleChangeQueueSelect = event => {
    setQueueSelect(event.target.value);
  };
  const handleChangeAgentSelect = event => {
    setAgentSelect(event.target.value);
  };
  const handleBreakChange = event => {
    setBreakSelect(event.target.value);
  };
  useEffect(()=>{
    setParams({
      agent: agentSelect === 'all' ? '' : agentSelect,
      queue: queueSelect === 'all' ? '' : queueSelect,
      break: breakSelect === 'all' ? '' : breakSelect,
      paramFinish: dateDifference(startTime),
    });
  },[startTime,agentSelect,queueSelect,breakSelect])
  return (
    <div>
      <div style={{ marginBottom: '15px' }}>
        <AgentSessionReportDateFilter setStartTimeApi={setStartTime} setEndTimeApi={setEndTime} />
      </div>

      <Card className={classes.root}>
        <Formik initialValues={{ queue: '', agent: '' }} onSubmit={values => {}}>
          {({ values, handleChange, handleSubmit }) => (
            <>
              <Form onSubmit={handleSubmit}>
                <CardContent>
                  <GridContainer>
                    <Grid item md={3}>
                      <FormControl fullWidth variant="outlined">
                        <InputLabel id="queue-select-label">{<IntlMessages id={'queue.answer.report.queues'} />}</InputLabel>
                        <Select
                          labelId="queue-select-label"
                          id="queue-select"
                          value={queueSelect}
                          onChange={handleChangeQueueSelect}
                          label={<IntlMessages id={'queue.answer.report.queues'} />}>
                          <MenuItem value={'all'}>
                            <IntlMessages id={'all'} />
                          </MenuItem>
                          {qnum.map((data, index) => (
                            <MenuItem key={index} value={data.dn}>
                              {data.dn} - {data.display_name}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                    <Grid item md={3}>
                      <FormControl fullWidth variant="outlined">
                        <InputLabel id="agent-select-label">
                          <IntlMessages id={'internals.summary.agent'} />
                        </InputLabel>
                        <Select
                          labelId="agent-select-label"
                          id="agent-select"
                          value={agentSelect}
                          onChange={handleChangeAgentSelect}
                          label={<IntlMessages id={'internals.summary.agent'} />}>
                          <MenuItem value={'all'}>
                            <IntlMessages id={'all'} />
                          </MenuItem>
                          {agent.map((data, index) => (
                            <MenuItem key={index} value={data.dn}>
                              {data.dn} - {data.display_name}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                    <Grid item md={3}>
                      <FormControl fullWidth variant="outlined">
                        <InputLabel id="break-select-label">
                          <IntlMessages id={'breaks'} />
                        </InputLabel>
                        <Select
                          labelId="break-select-label"
                          id="break-select"
                          value={breakSelect}
                          onChange={handleBreakChange}
                          label={<IntlMessages id={'breaks'} />}>
                          <MenuItem value={'all'}>
                            <IntlMessages id={'all'} />
                          </MenuItem>
                          {responseBreak.map((data, index) => (
                            <MenuItem key={index} value={data.value}>
                              {data.name}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                  </GridContainer>
                </CardContent>
              </Form>
            </>
          )}
        </Formik>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
