import React, { useEffect, useState } from 'react';
import GridContainer from '../../../@jumbo/components/GridContainer';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import Grid from '@material-ui/core/Grid';
import Table from './Table';
import * as api from '../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
export default function AgentList() {
  const [response, setResponse] = useState([]);
  let [reloadValues, setReloadValues] = useState('');
  useEffect(() => {
    api.getAgentList({ token: localStorage.getItem('token') }).then(resp => {
      let responseStatus = resp.data.data;
      api.getAgentsRecordListenAuth().then(result => {
        let recordAuths = JSON.parse(result.data.data);
        for (let i = 0; i < responseStatus.length; i++) {
          for (let j = 0; j < recordAuths.length; j++) {
            if (responseStatus[i].agentNumber === recordAuths[j].agent_num) {
              responseStatus[i].recordAuth = recordAuths[j].record_listen === 'True';
            }
          }
        }
        setResponse(responseStatus);
      });
    });
  }, [reloadValues]);
  const createData = (num,id, displayname, status, recordAuth) => {
    return {num, id, displayname, status, recordAuth };
  };
  const rows = response.map((dataValue,index) =>
    createData(
      index+1,
      dataValue.agentNumber,
      dataValue.agentName,
      dataValue.agentState === 'True' ? 'Active' : 'Passive',
      dataValue.recordAuth,
    ),
  );
  const breadcrumbs = [
    { label: <IntlMessages id={'reports.callcenter'} />, link: '/' },
    { label: <IntlMessages id={'agent.status'} />, isActive: true },
  ];
  return (
    <PageContainer heading={<IntlMessages id={'agent.status'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Table rows={rows} setReloadValues={setReloadValues} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
}
