import React, { useState, useEffect } from 'react';
import {
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  Card,
  CardContent,
  CircularProgress,
} from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { getMontlyLog } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
function Filter({ setResponse }) {
  const today = new Date();
  const [selectedMonth, setSelectedMonth] = useState(1);
  const [select, setSelect] = useState(1);
  const [sum, setSum] = useState(0);
  let months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  let selectboxdata = [];

  for (var i = 6; i >= 0; i -= 1) {
    var date = new Date(today.getFullYear(), today.getMonth() - i, 1);

    selectboxdata.push({
      data: months[date.getMonth()] + ' ' + date.getFullYear(),
      year: date.getFullYear(),
      month: <IntlMessages id={`${months[date.getMonth()]}`} />,
    });
  }
  selectboxdata.reverse();

  const handleChange = event => {
    setSum(event.target.value - 1);
    setSelect(event.target.value);
  };
  const handleMonthChange = event => {
    setSelectedMonth(event.target.value);
    setSum(select - 1);
  };

  let [loading, setLoading] = useState(false);

  useEffect(() => {
      setLoading(true);
      getMontlyLog(selectedMonth, sum).then(response => {
        let resultArray = JSON.parse(response.data.data);
        for (let i = 0; i < resultArray.length; i++) {
          let tempArray = resultArray[i].date_trunc.split(' ')[0].split('.');
          resultArray[i].date_trunc = tempArray[2] + '-' + tempArray[1] + '-0' + tempArray[0];
        }
        setResponse(resultArray);
        setLoading(false);
      });
  }, [sum, selectedMonth, setResponse]);
  return (
    <div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl variant="outlined" style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">
                  <IntlMessages id={'filter.menu.date'} />
                </InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  label={<IntlMessages id={'filter.menu.date'} />}
                  value={selectedMonth}
                  onChange={handleMonthChange}>
                  <MenuItem value={' '}>
                    <em>None</em>
                  </MenuItem>
                  {selectboxdata.map((data, index) => (
                    <MenuItem key={index} value={selectboxdata.indexOf(data) + 1}>
                      {data.month} {data.year}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid item md={4}>
              <FormControl variant="outlined" style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">
                  * <IntlMessages id={'months.ago'} />
                </InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  label={'* ' + <IntlMessages id={'months.ago'} />}
                  value={select}
                  onChange={handleChange}>
                  <MenuItem value={1}>
                    1 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                  <MenuItem value={2}>
                    2 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                  <MenuItem value={3}>
                    3 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                  <MenuItem value={4}>
                    4 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                  <MenuItem value={5}>
                    5 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                  <MenuItem value={6}>
                    6 <IntlMessages id={'months.ago'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
