import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import React, { useEffect, useState } from 'react';
import Checkbox from '@mui/material/Checkbox';
import { addUserOwndedDeps, deleteUserOwndedDeps, getDepartmants, getSupervisors, getUserOwndedDeps } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function UserDepartmans({ reloadValues }) {
  let [users, setUsers] = useState([]);
  let [deps, setDeps] = useState([]);
  let [userOwnedDeps, setUserOwnedDeps] = useState([]);
  let [selectedUserId, setSelectedUserId] = useState(0);

  useEffect(() => {
    getSupervisors().then(result => {
      setUsers(JSON.parse(result.data.data));
    });
  }, [reloadValues]);

  useEffect(() => {
    getDepartmants().then(result => {
      setDeps(JSON.parse(result.data.data));
    });
  }, []);

  useEffect(() => {
    getUserOwndedDeps(selectedUserId).then(result => {
      let tempArr = [];
      let resArr = JSON.parse(result.data.data);
      for (let i = 0; i < resArr.length; i++) {
        tempArr.push(resArr[i].department_id);
      }
      setUserOwnedDeps(tempArr);
    });
  }, [selectedUserId]);

  const handleCheckboxClick = (depId, event) => {
    if (event.target.checked) {
      addUserOwndedDeps(selectedUserId, depId).then(result => {
        getUserOwndedDeps(selectedUserId).then(result => {
          let tempArr = [];
          let resArr = JSON.parse(result.data.data);
          for (let i = 0; i < resArr.length; i++) {
            tempArr.push(resArr[i].department_id);
          }
          setUserOwnedDeps(tempArr);
        });
      });
    } else {
      deleteUserOwndedDeps(selectedUserId, depId).then(result => {
        getUserOwndedDeps(selectedUserId).then(result => {
          let tempArr = [];
          let resArr = JSON.parse(result.data.data);
          for (let i = 0; i < resArr.length; i++) {
            tempArr.push(resArr[i].department_id);
          }
          setUserOwnedDeps(tempArr);
        });
      });
    }
  };

  return (
    <div style={{ marginTop: '20px' }}>
      <GridContainer>
        <Grid item md={6}>
          <Paper>
            <TableContainer sx={{ maxHeight: '500px' }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      <IntlMessages id={'supervisors'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {users.map(user => (
                    <TableRow key={user.id}>
                      <TableCell
                        style={{ backgroundColor: selectedUserId === user.id ? '#90E0EF' : 'white' }}
                        onClick={() => setSelectedUserId(user.id)}>
                        {user.name}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
        <Grid item md={6}>
          <Paper>
            <TableContainer sx={{ maxHeight: '500px' }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      <IntlMessages id={'departments'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {deps.map(dep => (
                    <TableRow key={dep.id}>
                      <TableCell>
                        <Checkbox
                          checked={userOwnedDeps.includes(dep.id)}
                          onChange={event => handleCheckboxClick(dep.id, event)}
                        />
                        {dep.name}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </GridContainer>
    </div>
  );
}
