import { FormControl, InputLabel, MenuItem, Select, Chip } from '@mui/material';
import React, { useState, useEffect } from 'react';
import { getTagtextByCallId, TagByID } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function Tag({call_id,tagsList}) {
  const [tagValue, setTagValue] = useState('');

  const handleChangeTag = event => {
    TagByID(event.target.value,call_id).then(() =>{
      setTagValue(event.target.value);
    });
  };
  useEffect(()=>{
    if(call_id !== ""){
      getTagtextByCallId(call_id).then(resp => {
        setTagValue(JSON.parse(resp.data.data)[0]?.tagtext || '');
    });
    }

  },[call_id]);

  return (
    <>
    {
      call_id === ""?(<div style={{textAlign:"center"}}><IntlMessages id={'nontaggable'} /></div>):(
        <>
        {
            <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label11"><IntlMessages id={'tag'} /></InputLabel>
            <Select
              labelId="demo-simple-select-label11"
              id="demo-simple-select222"
              value={tagValue}
              label={<IntlMessages id={'tag'} />}
              style={{ width: '100%' }}
              onChange={handleChangeTag}>
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
                {
                  tagsList.map((item,index)=>(
                  <MenuItem key={index} value={item.tagmassage}>
                    <Chip color="info" size="small" label={item.tagmassage} />
                  </MenuItem>
                    ))
                }
            </Select>
          </FormControl>
        }
        </> 
      )
    }
    </>
  );
}