import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import { useIntl } from 'react-intl';
import { getNewDate } from '@jumbo/utils/dateHelper';

export default function DataTable({ response, limit, setOffset, setPage, setCsvData, setSelectedDate }) {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  function addZero(param) {
    if (param < 10) {
      param = '0' + param;
    }
    return param;
  }

  function returnedTime(param1, param2) {
    let Param1Array = param1.split(':');
    let Param2Array = param2.split(':');
    if (param1.includes('.')) {
      Param1Array[0] = parseInt(Param1Array[0]) + parseInt(param1.split('.')[0]) * 24;
    }
    if (param2.includes('.')) {
      Param2Array[0] = parseInt(Param2Array[0]) + parseInt(param2.split('.')[0]) * 24;
    }

    let hour = parseInt(Param1Array[0]) + parseInt(Param2Array[0]);
    let minute = parseInt(Param1Array[1]) + parseInt(Param2Array[1]);
    let second = parseInt(Param1Array[2]) + parseInt(Param2Array[2]);
    return (
      addZero(hour + Math.floor(minute / 60)) +
      ':' +
      addZero((minute % 60) + Math.floor(second / 60)) +
      ':' +
      addZero(second % 60)
    );
  }

  function calculateAverage(totalCall, totalTime) {
    let timeArray;
    if (totalTime.includes('.')) {
      let tempHour = parseInt(totalTime.split('.')[0]) * 24 + parseInt(totalTime.split('.')[1].split(':')[0]);
      let tempTime = tempHour + ':' + totalTime.split('.')[1].split(':')[1] + ':' + totalTime.split('.')[1].split(':')[2];
      timeArray = tempTime.split(':');
    } else {
      timeArray = totalTime.split(':');
    }
    let time = parseInt(timeArray[0]) * 60 * 60 + parseInt(timeArray[1]) * 60 + parseInt(timeArray[2]);
    let averageSecond = time / (parseInt(totalCall) !== 0 ? parseInt(totalCall) : 1);

    return totalCall !== 0
      ? addZero(Math.floor(averageSecond / 3600)) +
          ':' +
          addZero(Math.floor((averageSecond % 3600) / 60)) +
          ':' +
          addZero(Math.floor((averageSecond % 3600) % 60))
      : '00:00:00';
  }

  const column = [
    {
      id: 'd',
      headerName: <IntlMessages id={'filter.menu.date'} />,
      width: '100px',
    },
    {
      id: 'totalcall',
      headerName: <IntlMessages id={'wallboard.total.call'} />,
      width: '100px',
    },
    {
      id: 'totalinbound',
      headerName: <IntlMessages id={'dashboard.inbound'} />,
      width: '100px',
    },
    {
      id: 'answeredinb',
      headerName: <IntlMessages id={'wallboard.answered'} />,
      width: '30px',
    },
    {
      id: 'durationcalls',
      headerName: <IntlMessages id={'total.time'} />,
      width: '30px',
    },
    {
      id: 'durationinbound',
      headerName: <IntlMessages id={'durationinbound'} />,
      width: '30px',
    },
    {
      id: 'totaloutbound',
      headerName: <IntlMessages id={'dashboard.outbound'} />,
      width: '30px',
    },
    {
      id: 'durationoutbound',
      headerName: <IntlMessages id={'durationoutbound'} />,
      width: '30px',
    },
    {
      id: 'averageTotal',
      headerName: <IntlMessages id={'Act'} />,
      width: '30px',
    },
    {
      id: 'averageInbound',
      headerName: <IntlMessages id={'avIn'} />,
      width: '30px',
    },
    {
      id: 'averageOutbound',
      headerName: <IntlMessages id={'avOut'} />,
      width: '30px',
    },
  ];
  const createData = (
    d,
    totalcall,
    totalinbound,
    answeredinb,
    durationcalls,
    durationinbound,
    totaloutbound,
    durationoutbound,
    averageTotal,
    averageInbound,
    averageOutbound,
  ) => {
    return {
      d,
      totalcall,
      totalinbound,
      answeredinb,
      durationcalls,
      durationinbound,
      totaloutbound,
      durationoutbound,
      averageTotal,
      averageInbound,
      averageOutbound,
    };
  };

  const rows = response.map(data =>
    createData(
      data.d !== 'Total Call' ? getNewDate(data.d.split(' ')[0]) : data.d,
      parseInt(data.totaloutbound) + parseInt(data.totalinbound),
      data.totalinbound,
      data.answeredinb,
      returnedTime(data.durationinbound, data.durationoutbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      data.durationinbound
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      data.totaloutbound,
      data.durationoutbound
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      calculateAverage(
        parseInt(data.totaloutbound) + parseInt(data.totalinbound),
        returnedTime(data.durationinbound, data.durationoutbound),
      )
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      calculateAverage(data.totalinbound, data.durationinbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      calculateAverage(data.totaloutbound, data.durationoutbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    ),
  );

  const csvData = response.map(data => ({
    date: data.d !== 'Total Call' ? data.d.split(' ')[0] : data.d,
    totalcall: parseInt(data.totaloutbound) + parseInt(data.totalinbound),
    totalinbound: data.totalinbound,
    answeredinb: data.answeredinb,
    durationcalls:
      returnedTime(data.durationinbound, data.durationoutbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',

    durationinbound:
      data.durationinbound
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    totaloutbound: data.totaloutbound,
    durationoutbound:
      data.durationoutbound
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    averageTotal: calculateAverage(
      parseInt(data.totaloutbound) + parseInt(data.totalinbound),
      returnedTime(data.durationinbound, data.durationoutbound),
    ),
    averageinbound:
      calculateAverage(data.totalinbound, data.durationinbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    averageoutbound:
      calculateAverage(data.totaloutbound, data.durationoutbound)
        .replace('.', 'd ')
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
  }));

  const classes = useStyles();

  let currentLanguage = useIntl();

  return (
    <div style={{ marginTop: '20px' }}>
      <div style={{ textAlign: 'right', marginBottom: '15px', marginRight: '25px' }}>
        <ExcelFile
          filename={
            currentLanguage.locale === 'en-US'
              ? 'Monthly_Details'
              : currentLanguage.locale === 'tr-TR'
              ? 'Aylik_Detaylar'
              : currentLanguage.locale === 'nl-NL'
              ? 'Maandelijkse details'
              : currentLanguage.locale === 'de-DE'
              ? 'Monatliche Details'
              : ''
          }
          element={
            <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
              <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
            </Button>
          }>
          <ExcelSheet data={csvData} name="Data">
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Date'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Tarih'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Datum'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Datum'
                  : ''
              }
              value="date"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Total Call Count'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Toplam Arama Adedi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Totaal aantal gesprekken'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Gesamtzahl der Anrufe'
                  : ''
              }
              value="totalcall"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Total Inbound'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Gelen Aramalar'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Totaal inkomend'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Gesamteingang'
                  : ''
              }
              value="totalinbound"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Answered Call Count'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Cevaplanmƒ±≈ü √áaƒürƒ± Adedi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Aantal beantwoorde oproepen'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Z√§hlung angenommener Anrufe'
                  : ''
              }
              value="answeredinb"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Duration'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'S√ºre'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Looptijd'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Dauer'
                  : ''
              }
              value="durationcalls"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Inbound Time'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Gelen √áaƒürƒ± S√ºresi Toplamƒ±'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Inkomende tijd'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Eingangszeit'
                  : ''
              }
              value="durationinbound"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Total Outbound'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Toplam Giden √áaƒürƒ±'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Totaal uitgaand'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Gesamt ausgehend'
                  : ''
              }
              value="totaloutbound"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Outbound Total Time'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Giden √áaƒürƒ± toplam s√ºresi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Uitgaande totale tijd'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Ausgehend Gesamtzeit'
                  : ''
              }
              value="durationoutbound"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Average Total'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Ortalama Toplam Arama'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Gemiddeld totaal'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Durchschnittliche Summe'
                  : ''
              }
              value="averageTotal"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Average Inbound'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Ortalama Gelen Arama S√ºresi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Gemiddelde inkomende'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Durchschnittlicher Eingang'
                  : ''
              }
              value="averageinbound"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Average Outbound'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Ortalama Gelen Arama S√ºresi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Gemiddeld uitgaand'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Durchschnittlicher ausgehender'
                  : ''
              }
              value="averageoutbound"
            />
          </ExcelSheet>
        </ExcelFile>
      </div>
      <TableContainer component={Paper} className={classes.root} style={{ maxHeight: '550px' }}>
        <Table stickyHeader className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
          <TableHead>
            <TableRow>
              {column.map((column, index) => (
                <TableCell
                  key={index}
                  style={{
                    width: column.width,
                    opacity: '.9',
                    backgroundColor: '#4E9F3D',
                    color: 'white',
                  }}>
                  {column.headerName}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => {
              return row.d !== 'Total Call' ? (
                <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                  {column.map((column, inds) => {
                    const value = row[column.id];

                    return (
                      <TableCell
                        onClick={() => setSelectedDate(row.d)}
                        key={inds}
                        style={{ padding: '0px', textAlign: 'center', cursor: 'pointer' }}>
                        {value}
                      </TableCell>
                    );
                  })}
                </TableRow>
              ) : null;
            })}

            {rows.map((row, index) => {
              return row.d === 'Total Call' ? (
                <TableRow
                  hover
                  role="checkbox"
                  tabIndex={-1}
                  key={index}
                  style={{ backgroundColor: '#4E9F3D', opacity: '.9' }}>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>
                    <IntlMessages id={'Total'} />
                  </TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.totalcall}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.totalinbound}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.answeredinb}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.durationcalls}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>
                    {row.durationinbound}
                  </TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.totaloutbound}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>
                    {row.durationoutbound}
                  </TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.averageTotal}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>{row.averageInbound}</TableCell>
                  <TableCell style={{ color: 'white', padding: '1px', textAlign: 'center' }}>
                    {row.averageOutbound}
                  </TableCell>
                </TableRow>
              ) : null;
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
