import React, { useState, useEffect } from 'react';
import { FormControl, Select, InputLabel, MenuItem, Card, CardContent, CircularProgress } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { getCallComparison, getCallComparisonDaily } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import trLocale from 'date-fns/locale/tr';
import enLocale from 'date-fns/locale/en-US';
import { useIntl } from 'react-intl';
import deLocale from 'date-fns/locale/de';
import nlLocale from 'date-fns/locale/nl';

function Filter({ setResponse, response, setGraphData, setResponseDaily }) {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  const showTime = date => {
    const date_ = new Date(date);
    return date_.getHours();
  };
  const today = new Date();
  const [endDate, setEndDate] = useState(formatDate(today));
  const [select, setSelect] = useState(1);
  const [, setParams] = useState({ date: formatDate(today), choice: '' });

  let [loading, setLoading] = useState(true);
  let array = [];

  useEffect(() => {
    setLoading(true);
    getCallComparisonDaily(formatDate(endDate), select).then(resp => {
      let tempResponse = JSON.parse(resp.data.data);
      for (let i = 0; i < tempResponse.length; i++) {
        tempResponse[i].day_log =
          tempResponse[i].day_log.split(' ')[0].split('-')[2] +
          '-' +
          tempResponse[i].day_log.split(' ')[0].split('-')[1] +
          '-' +
          tempResponse[i].day_log.split(' ')[0].split('-')[0] +
          'T00:00:00.000Z';
        tempResponse[i].act = {
          hours: tempResponse[i].act.split(':')[0].startsWith('0')
            ? tempResponse[i].act.split(':')[0].substring(1)
            : tempResponse[i].act.split(':')[0],
          minutes: tempResponse[i].act.split(':')[1].startsWith('0')
            ? tempResponse[i].act.split(':')[1].substring(1)
            : tempResponse[i].act.split(':')[1],
          seconds: tempResponse[i].act.split(':')[2].startsWith('0')
            ? tempResponse[i].act.split(':')[2].substring(1)
            : tempResponse[i].act.split(':')[2],
        };
        tempResponse[i].total = {
          hours:
            tempResponse[i].total.split(':')[0].length > 2
              ? parseInt(tempResponse[i].total.split(':')[0].split('.')[0]) * 24 +
                parseInt(tempResponse[i].total.split(':')[0].split('.')[1]).toString()
              : tempResponse[i].total.split(':')[0].startsWith('0')
              ? tempResponse[i].total.split(':')[0].substring(1)
              : tempResponse[i].total.split(':')[0],
          minutes: tempResponse[i].total.split(':')[1].startsWith('0')
            ? tempResponse[i].total.split(':')[1].substring(1)
            : tempResponse[i].total.split(':')[1],
          seconds: tempResponse[i].total.split(':')[2].startsWith('0')
            ? tempResponse[i].total.split(':')[2].substring(1)
            : tempResponse[i].total.split(':')[2],
        };
      }
      setResponseDaily(tempResponse);
      setGraphData(array);
      setLoading(false);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [endDate, select]);

  useEffect(() => {
    getCallComparison(formatDate(endDate), select).then(response => {
      let tempResponse = JSON.parse(response.data.data);
      for (let i = 0; i < tempResponse.length; i++) {
        tempResponse[i].day_log =
          tempResponse[i].day_log.split(' ')[0].split('-')[0] +
          '-' +
          tempResponse[i].day_log.split(' ')[0].split('-')[1] +
          '-' +
          tempResponse[i].day_log.split(' ')[0].split('-')[2] +
          'T' +
          tempResponse[i].day_log.split(' ')[1].split(':')[0] +
          ':00:00.000Z';
        tempResponse[i].act = {
          hours: tempResponse[i].act.split(':')[0].startsWith('0')
            ? tempResponse[i].act.split(':')[0].substring(1)
            : tempResponse[i].act.split(':')[0],
          minutes: tempResponse[i].act.split(':')[1].startsWith('0')
            ? tempResponse[i].act.split(':')[1].substring(1)
            : tempResponse[i].act.split(':')[1],
          seconds: tempResponse[i].act.split(':')[2].startsWith('0')
            ? tempResponse[i].act.split(':')[2].substring(1)
            : tempResponse[i].act.split(':')[2],
        };
        tempResponse[i].total = {
          hours: tempResponse[i].total.split(':')[0].startsWith('0')
            ? tempResponse[i].total.split(':')[0].substring(1)
            : tempResponse[i].total.split(':')[0],
          minutes: tempResponse[i].total.split(':')[1].startsWith('0')
            ? tempResponse[i].total.split(':')[1].substring(1)
            : tempResponse[i].total.split(':')[1],
          seconds: tempResponse[i].total.split(':')[2].startsWith('0')
            ? tempResponse[i].total.split(':')[2].substring(1)
            : tempResponse[i].total.split(':')[2],
        };
      }
      setResponse(tempResponse);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [endDate, select]);

  const respdata = response.map(data => ({
    DayLog: formatDate(data.day_log),
    Count: data.count,
    TotalHour: data.total.hours === undefined || data.total.hour === 0 ? 0 : data.total.hour,
    TotalMinutes: data.total.minutes === undefined || data.total.minutes === 0 ? 0 : data.total.minutes,
    TotalSeconds: data.total.seconds === undefined || data.total.seconds === 0 ? 0 : data.total.seconds,
    Hour: showTime(data.day_log),
    Act: data.act.seconds === undefined || data.act.seconds === 0 ? 0 : data.act.seconds,
  }));

  let labelArray = [];

  for (let i = select; i >= 0; i--) {
    var date = new Date(formatDate(endDate));
    date.setDate(date.getDate() - i);
    labelArray.push(formatDate(date));
  }
  labelArray.reverse();
  for (let i = 0; i < labelArray.length; i++) {
    if (!respdata.some(daylog => daylog.DayLog === labelArray[i])) {
      respdata.push({
        DayLog: labelArray[i],
        Count: 0,
        TotalHour: 0,
        TotalMinutes: 0,
        TotalSeconds: 0,
        Hour: 0,
        Act: 0,
      });
    }
  }

  for (let i = 0; i < labelArray.length; i++) {
    let group = [];
    let totalDataArray = [];
    let totalMins = [];
    let totalSeconds = [];
    let act = [];
    group = respdata.filter(function(el) {
      return !(el.DayLog !== labelArray[i]);
    });

    group.map(data => {
      for (let j = 0; j < 24; j++) {
        if (j === data.Hour) {
          totalDataArray[j] = data.Count;
          act[j] = data.Act;
          totalSeconds[j] = data.TotalSeconds;
          totalMins[j] = data.TotalMinutes;
        } else {
          if (typeof totalDataArray[j] === 'undefined') {
            totalDataArray[j] = 0;
            act[j] = 0;
            totalSeconds[j] = 0;
            totalMins[j] = 0;
          }
        }
      }
      array[i] = {
        date: labelArray[i],
        count: totalDataArray,
        totalSeconds: totalSeconds,
        totalMins: totalMins,
        act: act,
      };
      return data;
    });
  }

  const handleChange = event => {
    setSelect(event.target.value);
  };

  let curentLanguage = useIntl();

  useEffect(() => {
    setParams({ date: formatDate(endDate), choice: select });
    setGraphData(array);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [endDate, select]);
  return (
    <div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={3}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                locale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                {' '}
                <DatePicker
                  value={endDate}
                  onChange={newValue => setEndDate(newValue)}
                  renderInput={params => <TextField {...params} />}
                  inputVariant="outlined"
                  format="DD-MM-YYYY"
                  label={<IntlMessages id={'filter.menu.date'} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={3}>
              <FormControl variant="outlined" style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">
                  * <IntlMessages id={'days.ago'} />
                </InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  label="* days ago"
                  value={select}
                  onChange={handleChange}>
                  <MenuItem value={1}>
                    1 <IntlMessages id={'days.ago'} />
                  </MenuItem>
                  <MenuItem value={2}>
                    2 <IntlMessages id={'days.ago'} />
                  </MenuItem>
                  <MenuItem value={3}>
                    3 <IntlMessages id={'days.ago'} />
                  </MenuItem>
                  <MenuItem value={4}>
                    4 <IntlMessages id={'days.ago'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
