import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, Card, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { CloudDownload, ArrowForward, ArrowBack, Person, PhoneMissed, PhoneInTalk, Phone } from '@material-ui/icons';
import { DataGrid, GridToolbarFilterButton } from '@mui/x-data-grid';
import { useScreenshot, createFileName } from 'use-react-screenshot';
import {
  getInternalsSummaryLongestCalled,
  getInternalsSummaryLongestCaller,
  getInternalsSummaryLongestCalledAmount,
  getInternalsSummaryLongestCallerAmount,
  getInternalBreakTime,
} from 'api/api';
import React, { createRef, useEffect, useState } from 'react';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { localeText } from '@jumbo/utils/DataGridLocales';

export default function InternalsSummaryReportTable({ data, startTime, endTime, direction }) {
  let [selectedAgent, setSelectedAgent] = useState('');
  const handleAgentClick = agentNum => {
    setSelectedAgent(agentNum);
    handleOpenModal();
  };

  let [modalOpen, setModalOpen] = useState(false);
  const handleOpenModal = () => setModalOpen(true);
  const handleCloseModal = () => setModalOpen(false);
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '90%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  let [longestCalleds, setLongestCalleds] = useState([]);
  let [longestCallers, setLongestCallers] = useState([]);
  let [longestCalledAmount, setLongestAmount] = useState([]);
  let [longestCallerAmount, setLongestCallerAmount] = useState([]);
  let [longestBreakTime, setLongestBreakTime] = useState([]);
  useEffect(() => {
    if (selectedAgent !== '') {
      getInternalsSummaryLongestCalled(startTime, endTime, selectedAgent).then(result => {
        setLongestCalleds(JSON.parse(result.data.data));
      });
      getInternalsSummaryLongestCaller(startTime, endTime, selectedAgent).then(result => {
        setLongestCallers(JSON.parse(result.data.data));
      });
      getInternalsSummaryLongestCalledAmount(startTime, endTime, selectedAgent).then(result => {
        setLongestAmount(JSON.parse(result.data.data));
      });
      getInternalsSummaryLongestCallerAmount(startTime, endTime, selectedAgent).then(result => {
        setLongestCallerAmount(JSON.parse(result.data.data));
      });

      getInternalBreakTime(selectedAgent, startTime, endTime).then(result => {
        setLongestBreakTime(JSON.parse(result.data.data));
      });
    }
  }, [selectedAgent, startTime, endTime]);

  let csvData = data.map((dataUnit, index) => ({
    num: index + 1,
    details: dataUnit.id,
    id: dataUnit.id,
    display_name: dataUnit.display_name,
    total_inbound: parseInt(dataUnit.total_inbound),
    answered_inbound: parseInt(dataUnit.answered_inbound),
    unanswered_inbound: dataUnit.total_inbound - dataUnit.answered_inbound,
    total_outbound: parseInt(dataUnit.total_outbound),
    answered_outbound: parseInt(dataUnit.answered_outbound),
    unanswered_outbound: dataUnit.total_outbound - dataUnit.answered_outbound,
  }));
  let curentLanguage = useIntl();
  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
    },
    {
      field: 'details',
      headerName: <IntlMessages id={'details'} />,
      renderCell: params => (
        <span onClick={() => handleAgentClick(params.value)} style={{ cursor: 'pointer' }}>
          <VisibilityIcon style={{ color: '#FF5959', verticalAlign: '-5px' }} />{' '}
        </span>
      ),
    },
    {
      field: 'id',
      headerName: <IntlMessages id={'agentnumber'} />,
      width: 200,
      renderCell: params => (
        <span>
          <b style={{ color: '#406882' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'display_name',
      headerName: <IntlMessages id={'agentname'} />,
      width: 250,
      renderCell: params => (
        <span>
          <span style={{ verticalAlign: '-5px', color: '#5584AC' }}>
            <Person />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'total_inbound',
      headerName: <IntlMessages id={'totalinbound'} />,
      width: 225,
      hide: direction === 'outbound',
      renderCell: params => (
        <span style={{ color: '#5584AC' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowForward />
            <Phone />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'answered_inbound',
      headerName: <IntlMessages id={'answeredinbound'} />,
      width: 220,
      hide: direction === 'outbound',
      renderCell: params => (
        <span style={{ color: '#519259' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowForward />
            <PhoneInTalk />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'unanswered_inbound',
      headerName: <IntlMessages id={'unansweredinbound'} />,
      width: 260,
      hide: direction === 'outbound',
      renderCell: params => (
        <span style={{ color: '#A3423C' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowForward />
            <PhoneMissed />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'total_outbound',
      headerName: <IntlMessages id={'totaloutbound'} />,
      width: 225,
      hide: direction === 'inbound',
      renderCell: params => (
        <span style={{ color: '#5584AC' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowBack />
            <Phone />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'answered_outbound',
      headerName: <IntlMessages id={'answeredoutbound'} />,
      width: 220,
      hide: direction === 'inbound',
      renderCell: params => (
        <span style={{ color: '#519259' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowBack />
            <PhoneInTalk />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
    {
      field: 'unanswered_outbound',
      headerName: <IntlMessages id={'unansweredoutbound'} />,
      width: 260,
      hide: direction === 'inbound',
      renderCell: params => (
        <span style={{ color: '#A3423C' }}>
          <span style={{ verticalAlign: '-5px' }}>
            <ArrowBack />
            <PhoneMissed />
          </span>{' '}
          {params.value}
        </span>
      ),
    },
  ];

  const ref = createRef(null);
  const [, takeScreenShot] = useScreenshot({
    type: 'image/jpeg',
    quality: 1.0,
  });

  const download = (image, { name = selectedAgent, extension = 'jpg' } = {}) => {
    const a = document.createElement('a');
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const downloadScreenshot = () => takeScreenShot(ref.current).then(download);

  return (
    <>
      {data.length > 0 && (
        <div style={{ marginTop: '20px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'ƒ±nternal_summary_report'
                : curentLanguage.locale === 'tr-TR'
                ? 'dahili √∂zet raporu'
                : curentLanguage.locale === 'nl-NL'
                ? 'ƒ±intern samenvattend rapport'
                : curentLanguage.locale === 'de-DE'
                ? 'interner zusammenfassender Bericht'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={csvData} name="Data">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Agent Number'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Agent Numarasƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Agent nummer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Agentennummer'
                    : ''
                }
                value="id"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Agent Name'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Agent Adƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Naam agent'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Agentenname'
                    : ''
                }
                value="display_name"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Total Inbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Toplam Gelen'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Totaal inkomend'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Gesamteingang'
                    : ''
                }
                value="total_inbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Answered Inbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevaplanan Gelen'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Inkomend beantwoord'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Eingehend beantwortet'
                    : ''
                }
                value="answered_inbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Unanswered Inbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevapsƒ±z Gelen'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Onbeantwoord Inkomend'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Unbeantworteter Eingang'
                    : ''
                }
                value="unanswered_inbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Total Outbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Toplam Giden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Totaal uitgaand'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Gesamt ausgehend'
                    : ''
                }
                value="total_outbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Answered Outbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevaplanan Giden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Uitgaande beantwoording'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Ausgehend beantwortet'
                    : ''
                }
                value="answered_outbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Unanswered Outbound'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevapsƒ±z Giden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Onbeantwoord uitgaand'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Unbeantwortet ausgehend'
                    : ''
                }
                value="unanswered_outbound"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Card style={{ marginTop: '20px', height: '700px' }}>
        <DataGrid
          rows={csvData}
          columns={columns}
          pageSize={100}
          rowsPerPageOptions={[100]}
          components={{ Toolbar: GridToolbarFilterButton }}
          localeText={localeText}
        />
      </Card>

      <Modal
        open={modalOpen}
        onClose={handleCloseModal}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <div style={{ padding: '10px' }} ref={ref}>
            <GridContainer>
              <Grid item md={3} style={{ borderLeft: '1px solid #d1d1d1' }}>
                <p style={{ textAlign: 'center', fontWeight: '600', fontSize: '16px' }}>
                  <IntlMessages id={'internalssummary.longestcalledtime'} />
                </p>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'internalssummary.callednumber'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'internalssummary.time'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[0] !== undefined ? longestCalleds[0].en_uzun_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[0] !== undefined
                            ? longestCalleds[0].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[1] !== undefined ? longestCalleds[1].en_uzun_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[1] !== undefined
                            ? longestCalleds[1].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[2] !== undefined ? longestCalleds[2].en_uzun_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[2] !== undefined
                            ? longestCalleds[2].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[3] !== undefined ? longestCalleds[3].en_uzun_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[3] !== undefined
                            ? longestCalleds[3].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[4] !== undefined ? longestCalleds[4].en_uzun_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalleds[4] !== undefined
                            ? longestCalleds[4].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
              <Grid item md={3} style={{ borderLeft: '1px solid #d1d1d1' }}>
                <p style={{ textAlign: 'center', fontWeight: '600', fontSize: '16px' }}>
                  <IntlMessages id={'internalssummary.longestcallertime'} />
                </p>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'internalssummary.callernumber'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'internalssummary.time'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[0] !== undefined ? longestCallers[0].en_uzun_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[0] !== undefined
                            ? longestCallers[0].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[1] !== undefined ? longestCallers[1].en_uzun_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[1] !== undefined
                            ? longestCallers[1].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[2] !== undefined ? longestCallers[2].en_uzun_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[2] !== undefined
                            ? longestCallers[2].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[3] !== undefined ? longestCallers[3].en_uzun_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[3] !== undefined
                            ? longestCallers[3].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[4] !== undefined ? longestCallers[4].en_uzun_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallers[4] !== undefined
                            ? longestCallers[4].en_uzun_aranan_s√ºre
                                .toString()
                                .replace(':', 'h ')
                                .replace(':', 'm ') + 's'
                            : '-'}
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
              <Grid item md={3} style={{ borderLeft: '1px solid #d1d1d1' }}>
                <p style={{ textAlign: 'center', fontWeight: '600', fontSize: '16px' }}>
                  <IntlMessages id={'internalssummary.mostcalledamount'} />
                </p>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'internalssummary.callednumber'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'internalssummary.amount'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[0] !== undefined ? longestCalledAmount[0].en_cok_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[0] !== undefined ? longestCalledAmount[0].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[1] !== undefined ? longestCalledAmount[1].en_cok_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[1] !== undefined ? longestCalledAmount[1].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[2] !== undefined ? longestCalledAmount[2].en_cok_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[2] !== undefined ? longestCalledAmount[2].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[3] !== undefined ? longestCalledAmount[3].en_cok_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[3] !== undefined ? longestCalledAmount[3].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[4] !== undefined ? longestCalledAmount[4].en_cok_aranan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCalledAmount[4] !== undefined ? longestCalledAmount[4].count : '-'}
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
              <Grid item md={3} style={{ borderLeft: '1px solid #d1d1d1', borderRight: '1px solid #d1d1d1' }}>
                <p style={{ textAlign: 'center', fontWeight: '600', fontSize: '16px' }}>
                  <IntlMessages id={'internalssummary.mostcalleramount'} />
                </p>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'internalssummary.callernumber'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'internalssummary.amount'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[0] !== undefined ? longestCallerAmount[0].en_cok_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[0] !== undefined ? longestCallerAmount[0].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[1] !== undefined ? longestCallerAmount[1].en_cok_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[1] !== undefined ? longestCallerAmount[1].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[2] !== undefined ? longestCallerAmount[2].en_cok_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[2] !== undefined ? longestCallerAmount[2].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[3] !== undefined ? longestCallerAmount[3].en_cok_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[3] !== undefined ? longestCallerAmount[3].count : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[4] !== undefined ? longestCallerAmount[4].en_cok_arayan_numara : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestCallerAmount[4] !== undefined ? longestCallerAmount[4].count : '-'}
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
            </GridContainer>
            <GridContainer>
              <Grid item md={4}></Grid>
              <Grid item md={4} style={{ borderLeft: '1px solid #d1d1d1', borderRight: '1px solid #d1d1d1' }}>
                <p style={{ textAlign: 'center', fontWeight: '600', fontSize: '16px' }}>
                  <IntlMessages id={'internalssummary.longestbreaketimes'} />
                </p>
                <TableContainer>
                  <Table>
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'internalssummary.callernumber'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'internalssummary.time'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[0] !== undefined ? longestBreakTime[0].name : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[0] !== undefined ? longestBreakTime[0].to_char : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[1] !== undefined ? longestBreakTime[1].name : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[1] !== undefined ? longestBreakTime[1].to_char : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[2] !== undefined ? longestBreakTime[2].name : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[2] !== undefined ? longestBreakTime[2].to_char : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[3] !== undefined ? longestBreakTime[3].name : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[3] !== undefined ? longestBreakTime[3].to_char : '-'}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[4] !== undefined ? longestBreakTime[4].name : '-'}
                        </TableCell>
                        <TableCell style={{ padding: '7px' }}>
                          {longestBreakTime[4] !== undefined ? longestBreakTime[4].to_char : '-'}
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
              <Grid item md={4}></Grid>
            </GridContainer>
          </div>
          <div style={{ textAlign: 'right', marginTop: '10px' }}>
            <Button style={{ backgroundColor: '#2978B5', color: 'white', marginRight: '10px' }} onClick={downloadScreenshot}>
              <IntlMessages id={'dowlandimage'} />
            </Button>
            <Button style={{ backgroundColor: '#2978B5', color: 'white' }} onClick={() => handleCloseModal()}>
              <IntlMessages id={'close'} />
            </Button>
          </div>
        </Box>
      </Modal>
    </>
  );
}
