import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import {
  Card,
  CardContent,
  CircularProgress,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from '@material-ui/core';
import { getAgentNumber, getInternalsSummary } from 'api/api';
import React, { useEffect, useState } from 'react';
import InternalSummaryDateFilter from './DateFilter';

export default function InternalsSummaryReportFilter({ setData, startTime, setStartTime, endTime, setEndTime, direction, setDirection }) {
  let [agents, setAgents] = useState([]);
  let [seletAgent, setSelectAgent] = useState("");
  const handleChangeAgentSelect = event => {
    setSelectAgent(event.target.value === 'all' ? '' : event.target.value);
  };

  useEffect(() => {
    getAgentNumber().then(response => {
      setAgents(JSON.parse(response.data.data));
    });
  }, []);

  let [loading, setLoading] = useState(false);
  
  useEffect(() => {
        setLoading(true);
        getInternalsSummary(startTime, endTime, seletAgent).then(result => {
          let filtered = JSON.parse(result.data.data).filter((item)=> (
            direction === "all" ? item :
            direction === "inbound" ? parseInt(item.total_inbound) > 0 : parseInt(item.total_outbound) > 0
          ));
          setData(filtered);
          setLoading(false);
        });
  }, [startTime, endTime, seletAgent, setData,direction]);

  return (
    <div>
      <NotificationContainer />
      <div style={{ marginBlock: '10px' }}>
        <InternalSummaryDateFilter setStartTimeApi={setStartTime} setEndTimeApi={setEndTime} />
      </div>

      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'internals.summary.agent'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={seletAgent}
                  onChange={handleChangeAgentSelect}
                  label={<IntlMessages id={'internals.summary.agent'} />}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {agents.map((agent, index) => (
                    <MenuItem key={index} value={agent.dn}>
                      {agent.dn} - {agent.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="direction-select-label">
                  <IntlMessages id={'direction'} />
                </InputLabel>
                <Select
                  labelId="direction-select-label"
                  id="direction-select"
                  value={direction}
                  onChange={(e)=> setDirection(e.target.value)}
                  label={<IntlMessages id={'direction'} />}>
                  <MenuItem value='all'>
                    <IntlMessages id={'alltimes'} />
                  </MenuItem>
                  <MenuItem value="inbound">
                    <IntlMessages id={'dashboard.inbound'} />
                    </MenuItem>
                  <MenuItem value="outbound">
                    <IntlMessages id={'dashboard.outbound'} />
                    </MenuItem>

                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
      {loading && (
        <div style={{ marginTop: '20px', textAlign: 'center' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}
