import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState, useEffect } from 'react';
import QualityFilter from './Filter';
import QualityTable from './Table';
import { getQualityReport } from '../../../api/api';
export default function QualityReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'survey.report'} />, isActive: true },
  ];
  const [data, setData] = useState([]);
  const [filtered, setFiltered] = useState([]);
  useEffect(() => {
    getQualityReport().then(resp => {
      setData(JSON.parse(resp.data.data));
    });
  }, []);

  return (
    <PageContainer heading={<IntlMessages id={'survey.report'} />} breadcrumbs={breadcrumbs}>
      <QualityFilter data={data} setFiltered={setFiltered} />
      <QualityTable filtered={filtered} />
    </PageContainer>
  );
}
