import React, { useState } from 'react';
import Filter from './Filter';
import Table from './Table';
import GridContainer from '@jumbo/components/GridContainer';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import { Grid } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
export const AbandonedQueues = () => {
  const [response, setResponse] = useState([]);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [queuelist, setQueueList] = useState([]);
  const [responseCount, setResponseCount] = useState(10);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'abandoned.queues'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'abandoned.queues'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Filter
            setResponse={setResponse}
            setOffset={setOffset}
            setPage={setPage}
            setQueueList={setQueueList}
            queuelist={queuelist}
            limit={limit}
            offset={offset}
            setResponseCount={setResponseCount}
          />
        </Grid>
        <Grid item xs={12}>
          <Table
            datalist={response}
            setResponse={setResponse}
            offset={offset}
            setOffset={setOffset}
            setLimit={setLimit}
            limit={limit}
            page={page}
            setPage={setPage}
            responseCount={responseCount}
          />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};
