import { Box, Grid, Tab, Tabs, Typography } from '@mui/material';
import { getCallDetail } from 'api/api';
import React, { useEffect, useState } from 'react';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import DateRangeIcon from '@mui/icons-material/DateRange';
import CallIcon from '@mui/icons-material/Call';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import CallEndIcon from '@mui/icons-material/CallEnd';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import {
  Timeline,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent,
  TimelineSeparator,
} from '@material-ui/lab';
import GridContainer from '@jumbo/components/GridContainer';
import PersonIcon from '@mui/icons-material/Person';
import IntlMessages from '@jumbo/utils/IntlMessages';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}>
      {value === index && (
        <Box sx={{ p: 3 }}>
          <>{children}</>
        </Box>
      )}
    </div>
  );
}
function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function CallLogCallDetail({ callId }) {
  let [data, setData] = useState([]);
  const detailContainerStyle={borderBottom:"1px solid rgba(0, 0, 0, 0.87)", margin:"10px 0", width:"100%"};

  useEffect(() => {
    getCallDetail(callId).then(resp => {
      setData(JSON.parse(resp.data.data));
      setTotalTime(
        new Date(JSON.parse(resp.data.data)[JSON.parse(resp.data.data).length - 1]?.end_time).getTime() -
          new Date(JSON.parse(resp.data.data)[0]?.start_time).getTime(),
      );
    });
  }, [callId]);

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  let [totalTime, setTotalTime] = useState(0);

  const secondToTime = ms => {
    let seconds = ms / 1000;
    const hours = parseInt(seconds / 3600);
    seconds = seconds % 3600;
    const minutes = parseInt(seconds / 60);
    seconds = seconds % 60;
    let hourString = hours.toString().length === 1 ? '0' : '';
    let minutesString = minutes.toString().length === 1 ? '0' : '';
    let secondsString = seconds.toString().length === 1 ? '0' : '';
    let string = hourString + hours + 'h ' + minutesString + minutes + 'm ' + secondsString + seconds + 's ';
    return string;
  };

  let [totalRingTime, setTotalRingTime] = useState(0);
  let [totalTalkTime, setTotalTalkTime] = useState(0);

  useEffect(() => {
    let tempRingTime = 0;
    let tempTalkTime = 0;
    for (let i = 0; i < data.length; i++) {
      if (data[i].ring_time !== '') {
        tempRingTime += parseInt(data[i].ring_time.split(' ')[0]?.substring(0, 2)) * 60 * 60 * 1000;
        tempRingTime += parseInt(data[i].ring_time.split(' ')[1]?.substring(0, 2)) * 60 * 1000;
        tempRingTime += parseInt(data[i].ring_time.split(' ')[2]?.substring(0, 2)) * 1000;
      }
      if (data[i].duration !== '') {
        tempTalkTime += parseInt(data[i].duration.split(' ')[0]?.substring(0, 2)) * 60 * 60 * 1000;
        tempTalkTime += parseInt(data[i].duration.split(' ')[1]?.substring(0, 2)) * 60 * 1000;
        tempTalkTime += parseInt(data[i].duration.split(' ')[2]?.substring(0, 2)) * 1000;
      }
    }
    setTotalRingTime(tempRingTime);
    setTotalTalkTime(tempTalkTime);
  }, [data]);

  return (
    <div>
      <Grid
        container
        spacing={0}
        style={{ background: 'linear-gradient(0deg, rgba(138,182,214,1) 0%, rgba(41,120,181,1) 100%)' }}>
        <Grid item xs={4}>
          <div
            style={{
              width: '100%',
              background: 'rgb(173,206,116)',
              height: '200px',
              marginRight: '0px',
              textAlign: 'center',
            }}>
            <PersonOutlineOutlinedIcon
              style={{
                verticalAlign: '-5px',
                fontSize: 80,
                padding: '10px',
                marginTop: '50px',
                backgroundColor: '#89a658',
                borderRadius: '50%',
                color: 'white',
              }}
            />
            <div style={{ textAlign: 'center', marginTop: '10px' }}>
              <b style={{ fontSize: '16px' }}>{data[0]?.callers}</b>
            </div>
          </div>
        </Grid>
        <Grid item xs={4}>
          <div style={{ width: '100%', height: '200px' }}>
            <div
              style={{
                width: 0,
                height: 0,
                borderTop: '100px solid transparent',
                borderLeft: '200px solid',
                borderLeftColor: 'rgb(173,206,116)',
                borderBottom: '100px solid transparent',
              }}></div>
          </div>
        </Grid>
        <Grid item xs={4}>
          <div
            style={{
              width: '100%',
              backgroundColor: 'linear-gradient(0deg, rgba(138,182,214,1) 0%, rgba(41,120,181,1) 100%)',
              height: '200px',
              textAlign: 'center',
            }}>
            <PersonOutlineOutlinedIcon
              style={{
                verticalAlign: '-5px',
                fontSize: 80,
                padding: '10px',
                marginTop: '50px',
                backgroundColor: '#2978B5',
                borderRadius: '50%',
                color: 'white',
              }}
            />
            <div style={{ textAlign: 'center', marginTop: '10px' }}>
              <b style={{ fontSize: '16px' }}>{data[0]?.calleds}</b>
            </div>
          </div>
        </Grid>
      </Grid>
      <hr style={{ marginTop: '0px', height: '5px', color: 'gray' }} />
      <div>
        <Box sx={{ width: '100%' }}>
          <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
            <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
              <Tab
                style={{ borderRight: '2px solid gray' }}
                label={<IntlMessages id={'CallLog.detail.callinfo'} />}
                {...a11yProps(0)}
              />
              <Tab
                style={{ borderRight: '2px solid gray' }}
                label={<IntlMessages id={'CallLog.datail.detail'} />}
                {...a11yProps(1)}
              />
            </Tabs>
          </Box>
          <TabPanel value={value} index={0}>
            <Timeline position="alternate">
              <TimelineItem>
                <TimelineOppositeContent align="right" variant="body2" color="text.secondary" style={{ marginTop: '10px' }}>
                  <Typography variant="h6" component="span" style={{ color: '#2978B5' }}>
                    <DateRangeIcon style={{ verticalAlign: '-5px' }} /> <b>{data[0]?.start_time}</b>
                  </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineConnector />
                  <TimelineDot color="primary">
                    <DateRangeIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent sx={{ py: '12px', px: 2 }}></TimelineContent>
              </TimelineItem>
              {data.map((item, index) => (
                <TimelineItem key={index}>
                  <TimelineOppositeContent
                    align="right"
                    variant="body2"
                    color="text.secondary"
                    style={{ marginTop: '80px' }}>
                    <Typography variant="h6" component="span" style={{ marginTop: '45px' }}>
                      <b>{item.calleds}</b>
                    </Typography>
                    <Typography>{item.states}</Typography>
                  </TimelineOppositeContent>
                  <TimelineSeparator>
                    <TimelineConnector />
                    <TimelineDot color="secondary">
                      <CallIcon />
                    </TimelineDot>
                    <TimelineConnector />
                  </TimelineSeparator>
                  <TimelineContent sx={{ py: '12px', px: 2 }}>
                    <Typography style={{ marginTop: '20px' }}>
                        <IntlMessages id={'CallLog.datail.starttime'} />
                    </Typography>
                    <Typography>
                      <span style={{ padding: '6px', backgroundColor: '#A6CF98', borderRadius: '10px', color: '#557C55' }}>
                        <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{item.start_time.split(' ')[1]}</b>
                      </span>
                    </Typography>
                    <Typography style={{ marginTop: '5px' }}>
                        <IntlMessages id={'CallLog.datail.ringtime'} />
                    </Typography>
                    <Typography>
                      <span style={{ padding: '6px', backgroundColor: '#EEC373', borderRadius: '10px', color: '#EA5C2B' }}>
                        <NotificationsActiveIcon style={{ verticalAlign: '-5px' }} /> <b>{item.ring_time}</b>
                      </span>
                    </Typography>
                    <Typography style={{ marginTop: '5px' }}>
                        <IntlMessages id={'CallLog.datail.talktime'} />
                    </Typography>
                    <Typography>
                      <span style={{ padding: '6px', backgroundColor: '#6998AB', borderRadius: '10px', color: '#1A374D' }}>
                        <PhoneInTalkIcon style={{ verticalAlign: '-5px' }} /> <b>{item.duration}</b>
                      </span>
                    </Typography>
                    <Typography style={{ marginTop: '5px' }}>
                        <IntlMessages id={'CallLog.datail.endtime'} />
                    </Typography>
                    <Typography>
                      <span style={{ padding: '6px', backgroundColor: '#FF7272', borderRadius: '10px', color: '#9B0000' }}>
                        <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{item.end_time.split(' ')[1]}</b>
                      </span>
                    </Typography>
                  </TimelineContent>
                </TimelineItem>
              ))}
              <TimelineItem>
                <TimelineOppositeContent align="right" variant="body2" color="text.secondary" style={{ marginTop: '10px' }}>
                  <Typography variant="h6" component="span" style={{ color: '#EA4C4C' }}>
                    <DateRangeIcon style={{ verticalAlign: '-5px' }} /> <b>{data[data.length - 1]?.end_time}</b>
                  </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineConnector />
                  <TimelineDot color="secondary">
                    <CallEndIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent sx={{ py: '12px', px: 2 }}></TimelineContent>
              </TimelineItem>
            </Timeline>
          </TabPanel>
          <TabPanel value={value} index={1}>
          <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>Call Id:</b>
              </Grid>
              <Grid item md={6}>
                <p style={{ fontSize: '16px' }}>{callId}</p>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.from'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <PersonIcon style={{ verticalAlign: '-5px', color: '#086E7D' }} />{' '}
                <span style={{ fontSize: '16px' }}>{data[0]?.callers}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.to'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <PersonIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
                <span style={{ fontSize: '16px' }}>{data[0]?.calleds}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.duration'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <AccessTimeIcon style={{ verticalAlign: '-5px', color: 'gray' }} />{' '}
                <span style={{ fontSize: '16px', color: 'gray' }}>{secondToTime(totalTime)}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.talktime'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#406882' }} />{' '}
                <span style={{ fontSize: '16px', color: '#406882' }}>{secondToTime(totalTalkTime)}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.ringtime'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#EF2F88' }} />{' '}
                <span style={{ fontSize: '16px', color: '#EF2F88' }}>{secondToTime(totalRingTime)}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.date'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <DateRangeIcon style={{ verticalAlign: '-5px', color: '#1A374D' }} />{' '}
                <span style={{ fontSize: '16px' }}>{data[0]?.start_time.split(' ')[0]}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.starttime'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <AccessTimeIcon style={{ verticalAlign: '-5px', color: 'gray' }} />{' '}
                <span style={{ fontSize: '16px', color: 'gray' }}>{data[0]?.start_time.split(' ')[1]}</span>
              </Grid>
            </GridContainer>
            <GridContainer style={detailContainerStyle}>
              <Grid item md={6}>
                <b style={{ fontSize: '16px' }}>
                  <IntlMessages id={'CallLog.datail.endtime'} />:
                </b>
              </Grid>
              <Grid item md={6}>
                <AccessTimeIcon style={{ verticalAlign: '-5px', color: 'gray' }} />{' '}
                <span style={{ fontSize: '16px', color: 'gray' }}>{data[data.length - 1]?.end_time.split(' ')[1]}</span>
              </Grid>
            </GridContainer>
          </TabPanel>
        </Box>
      </div>
    </div>
  );
}