import React, { useEffect, useState } from 'react';
import {
  getTeamsUsers,
  getCsOnlineVoiceRoutingPolicy,
  getCsTenantDialplan,
  getCsTeamsCallingPolicy,
  changeCsTeamsCallingPolicy,
  changeCsVoiceRoutingPolicy,
  getUserDetailsByUserId,
  changeCsTenantDiaplan,
} from 'api/api';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import {
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  Grid,
  Accordion,
  AccordionSummary,
  Typography,
  AccordionDetails,
  Button,
  AccordionActions,
  Card,
} from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { Logout } from 'services/auth/logout';
import { useHistory } from 'react-router';

export default function Users() {
  let [users, setUsers] = useState([]);
  let [selecetedUser, setSelectedUser] = useState('');
  let [selectedUserDetails, setSelectedUserDetails] = useState({
    onlineVoiceRoutingPolicy: '-',
    tenantDialPlan: '-',
    teamsCallingPolicy: '-',
    userPrincibleName: '-',
  });

  let [voiceRoutingPolicys, setVoiceRoutingPolicys] = useState([]);
  let [selectedvoiceRoutingPolicy, setSelectedvoiceRoutingPolicy] = useState('');

  let [tenantDiaplans, setTenantDiaplan] = useState([]);
  let [selectedTenantDiaplans, setSelectedTenantDiaplans] = useState('');

  let [teamsCallingPolicys, setTeamsCallingPolicy] = useState([]);
  let [selectedTeamsCallingPolicy, setSelectedTeamsCallingPolicy] = useState('');

  const history = useHistory();

  useEffect(() => {
    getTeamsUsers({ token: localStorage.getItem('token') })
      .then(result => {
        setUsers(result.data.data);
        getCsOnlineVoiceRoutingPolicy({
          token: localStorage.getItem('token'),
        }).then(res => {
          setVoiceRoutingPolicys(res.data.data);
          getCsTenantDialplan({
            token: localStorage.getItem('token'),
          }).then(res2 => {
            setTenantDiaplan(res2.data.data);
            getCsTeamsCallingPolicy({
              token: localStorage.getItem('token'),
            }).then(res3 => {
              setTeamsCallingPolicy(res3.data.data);
            });
          });
        });
      })
      .catch(resp => {
        if (resp.response.status === 401) {
          Logout();
          history.push('/signin');
        }
      });
  }, [history]);

  useEffect(() => {
    if (selecetedUser !== '') {
      getUserDetailsByUserId({
        token: localStorage.getItem('token'),
        userId: selecetedUser,
      }).then(result => {
        setSelectedUserDetails(result.data.data);
      });
    }
  }, [selecetedUser]);

  const handleChangeUserEmail = event => {
    setSelectedUser(event.target.value);
  };
  const handleChangeVoiceRoutingPolicy = event => {
    setSelectedvoiceRoutingPolicy(event.target.value);
  };
  const handleChangeTenantDiaplans = event => {
    setSelectedTenantDiaplans(event.target.value);
  };
  const handleChangeTeamsCallingPolicy = event => {
    setSelectedTeamsCallingPolicy(event.target.value);
  };

  const handleSubmitChangeTeamsCallingPolicy = () => {
    if (selecetedUser === '') {
      NotificationManager.error('User must be selected', '', 3000);
    } else if (selectedTeamsCallingPolicy === '') {
      NotificationManager.error('Teams Calling Policy must be selected', '', 3000);
    } else if (selecetedUser !== '' && selectedTeamsCallingPolicy !== '') {
      changeCsTeamsCallingPolicy({
        token: localStorage.getItem('token'),
        userId: selecetedUser,
        policyName: selectedTeamsCallingPolicy,
      })
        .then(result => {
          NotificationManager.success('Successfuly changed', '', 3000);
        })
        .catch(result => {});
    }
  };
  const handleSubmitChangeVoiceRoutingPolicy = () => {
    if (selecetedUser === '') {
      NotificationManager.error('User must be selected', '', 3000);
    } else if (selectedvoiceRoutingPolicy === '') {
      NotificationManager.error('Voice Routing Policy must be selected', '', 3000);
    } else if (selecetedUser !== '' && selectedvoiceRoutingPolicy !== '') {
      changeCsVoiceRoutingPolicy({
        token: localStorage.getItem('token'),
        userId: selecetedUser,
        voiceRoutingPolicy: selectedvoiceRoutingPolicy,
      })
        .then(result => {
          NotificationManager.success('Successfuly changed', '', 3000);
        })
        .catch(result => {});
    }
  };
  const handleSubmitChangeTenantDiaplan = () => {
    if (selecetedUser === '') {
      NotificationManager.error('User must be selected', '', 3000);
    } else if (selectedTenantDiaplans === '') {
      NotificationManager.error('Tenant Diaplan must be selected', '', 3000);
    } else if (selecetedUser !== '' && selectedTenantDiaplans !== '') {
      changeCsTenantDiaplan({
        token: localStorage.getItem('token'),
        userId: selecetedUser,
        tenantDiaplan: selectedTenantDiaplans,
      })
        .then(result => {
          NotificationManager.success('Successfuly changed', '', 3000);
        })
        .catch(result => {});
    }
  };

  return (
    <PageContainer>
      <NotificationContainer />
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMore />} aria-controls="panel1a-content" id="panel1a-header">
          <Typography>Change Teams Calling Policy</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <GridContainer>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">User Email</InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  value={selecetedUser}
                  onChange={handleChangeUserEmail}>
                  <MenuItem value="">
                    <em>User Email</em>
                  </MenuItem>
                  {users.map(user => (
                    <MenuItem key={user.id} value={user.id}>
                      {user.userName}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="teamsCallingPolicy-label">Teams Calling Policy</InputLabel>
                <Select
                  labelId="teamsCallingPolicy-label"
                  id="teamsCallingPolicy"
                  value={selectedTeamsCallingPolicy}
                  onChange={handleChangeTeamsCallingPolicy}>
                  <MenuItem value="">
                    <em>Teams Calling Policy</em>
                  </MenuItem>
                  {teamsCallingPolicys.map((teamsCallingPolicy, index) => (
                    <MenuItem key={index} value={teamsCallingPolicy.callingPolicy}>
                      {teamsCallingPolicy.callingPolicy}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </AccordionDetails>
        <AccordionActions>
          <Button variant="contained" onClick={handleSubmitChangeTeamsCallingPolicy}>
            Change
          </Button>
        </AccordionActions>
      </Accordion>
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMore />} aria-controls="panel1a-content" id="panel1a-header">
          <Typography>Change Voice Routing Policy</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <GridContainer>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">User Email</InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  value={selecetedUser}
                  onChange={handleChangeUserEmail}>
                  <MenuItem value="">
                    <em>User Email</em>
                  </MenuItem>
                  {users.map(user => (
                    <MenuItem key={user.id} value={user.id}>
                      {user.userName}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="voiceRoutingPolicy-label">Voice Routing Policy</InputLabel>
                <Select
                  labelId="voiceRoutingPolicy-label"
                  id="voiceRoutingPolicy"
                  value={selectedvoiceRoutingPolicy}
                  onChange={handleChangeVoiceRoutingPolicy}>
                  <MenuItem value="">
                    <em>VoiceRoutingPolicy</em>
                  </MenuItem>
                  {voiceRoutingPolicys.map((voiceRoutingPolicy, index) => (
                    <MenuItem key={index} value={voiceRoutingPolicy.policyName}>
                      {voiceRoutingPolicy.policyName}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </AccordionDetails>
        <AccordionActions>
          <Button variant="contained" onClick={handleSubmitChangeVoiceRoutingPolicy}>
            Change
          </Button>
        </AccordionActions>
      </Accordion>
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMore />} aria-controls="panel1a-content" id="panel1a-header">
          <Typography>Change Tenant Diaplan</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <GridContainer>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">User Email</InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  value={selecetedUser}
                  onChange={handleChangeUserEmail}>
                  <MenuItem value="">
                    <em>User Email</em>
                  </MenuItem>
                  {users.map(user => (
                    <MenuItem key={user.id} value={user.id}>
                      {user.userName}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={6}>
              <FormControl style={{ width: '100%' }}>
                <InputLabel id="tenantDiaplan-label">Tenant Diaplan</InputLabel>
                <Select
                  labelId="tenantDiaplan-label"
                  id="tenantDiaplan"
                  value={selectedTenantDiaplans}
                  onChange={handleChangeTenantDiaplans}>
                  <MenuItem value="">
                    <em>TenantDiaplan</em>
                  </MenuItem>
                  {tenantDiaplans.map((tenantDiaplan, index) => (
                    <MenuItem key={index} value={tenantDiaplan.teanantDiaPlan}>
                      {tenantDiaplan.teanantDiaPlan}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </AccordionDetails>
        <AccordionActions>
          <Button variant="contained" onClick={handleSubmitChangeTenantDiaplan}>
            Change
          </Button>
        </AccordionActions>
      </Accordion>
      <div style={{ textAlign: 'center', marginTop: '10px' }}>
        Deƒüi≈üim i≈ülemlerinin sonuca yansƒ±masƒ± biraz zaman alabilir.
      </div>
      <Card style={{ marginTop: '20px' }}>
        <p style={{ textAlign: 'center', fontSize: '20px', padding: '10px' }}>Selected User Details</p>
        <div style={{ padding: '10px' }}>
          <GridContainer>
            <Grid item md={6}>
              <p>User Email</p>
              <div
                style={{
                  borderColor: 'black',
                  borderStyle: 'solid',
                  borderWidth: '1px',
                  padding: '10px',
                  borderRadius: '5px',
                }}>
                {selectedUserDetails.userPrincibleName}
              </div>
            </Grid>
            <Grid item md={6}>
              <p>Teams Calling Policy</p>
              <div
                style={{
                  borderColor: 'black',
                  borderStyle: 'solid',
                  borderWidth: '1px',
                  padding: '10px',
                  borderRadius: '5px',
                }}>
                {selectedUserDetails.teamsCallingPolicy}
                <b> </b>
              </div>
            </Grid>
          </GridContainer>
          <GridContainer>
            <Grid item md={6}>
              <p>Voice Routing Policy</p>
              <div
                style={{
                  borderColor: 'black',
                  borderStyle: 'solid',
                  borderWidth: '1px',
                  padding: '10px',
                  borderRadius: '5px',
                }}>
                {selectedUserDetails.onlineVoiceRoutingPolicy}
                <b> </b>
              </div>
            </Grid>
            <Grid item md={6}>
              <p>Tenant Diaplan</p>
              <div
                style={{
                  borderColor: 'black',
                  borderStyle: 'solid',
                  borderWidth: '1px',
                  padding: '10px',
                  borderRadius: '5px',
                }}>
                {selectedUserDetails.tenantDialPlan}
                <b> </b>
              </div>
            </Grid>
          </GridContainer>
        </div>
      </Card>
    </PageContainer>
  );
}
