import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import * as api from '../../../../api/api';
import Paper from '@material-ui/core/Paper';
import { lighten, makeStyles } from '@material-ui/core/styles';
import IntlMessages from '@jumbo/utils/IntlMessages';
import PersonIcon from '@mui/icons-material/Person';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const QueueList = ({ setQueueAmaunt }) => {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  const [response, setResponse] = useState([]);

  const classes = useStyles();

  const reloadValues = () => {
    if (window.location.pathname === '/wallboard') {
      api.getQueueList().then(resp => {
        setResponse(resp.data.data);
      });
      setQueueAmauntFunction();
    }
    var queueListTimeout = setTimeout(reloadValues, 5000);
    if (window.location.pathname !== '/wallboard') {
      clearTimeout(queueListTimeout);
    }
  };

  const setQueueAmauntFunction = () => {
    let queAmaunt = 0;
    for (let i = 0; i < response.length; i++) {
      if (response[i].waitingNumber !== '-') {
        queAmaunt++;
      }
    }
    setQueueAmaunt(queAmaunt);
  };

  useEffect(() => {
    reloadValues();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <TableContainer
      style={{ maxHeight: '32vh', boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}
      component={Paper}
      className={classes.root}>
      <Table stickyHeader className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#EA4C4C',
                color: 'white',
              }}>
              <IntlMessages id={'wallboard.queue.waiting'} />
            </TableCell>
            <TableCell
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#EA4C4C',
                color: 'white',
              }}>
              <IntlMessages id={'wallboard.queue'} />
            </TableCell>
            <TableCell
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#EA4C4C',
                color: 'white',
              }}>
              <IntlMessages id={'queue'} />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {response.length >= 1
            ? response.map((row, index) => {
                return row.waitingNumber !== '-' ? (
                  <TableRow style={{ backgroundColor: '#FFFD95' }} hover role="checkbox" tabIndex={-1} key={index}>
                    <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                      <PersonIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{row.waitingNumber}</b>
                    </TableCell>
                    <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                      <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#DB6B97' }} />{' '}
                      <b>
                        {(row.waitingTime !== '-' ? row.waitingTime.substring(0, 8) : '00:00:00') !== undefined
                          ? (row.waitingTime !== '-' ? row.waitingTime.substring(0, 8) : '00:00:00')
                              .toString()
                              .replace(':', 'h ')
                              .replace(':', 'm ') + 's'
                          : null}
                      </b>
                    </TableCell>
                    <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                      <b>{row.queuName}</b> <b>{'(' + row.queueNumber + ')'}</b>
                    </TableCell>
                  </TableRow>
                ) : null;
              })
            : null}
        </TableBody>
        <TableBody>
          {response.map((row, index) => {
            return row.waitingNumber === '-' ? (
              <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#406882' }} />{' '}
                  <b>
                    <IntlMessages id={'no.waiting'} />
                  </b>
                </TableCell>
                <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#DB6B97' }} />{' '}
                  <b>
                    {(row.waitingTime !== '-' ? row.waitingTime.substring(0, 8) : '00:00:00') !== undefined
                      ? (row.waitingTime !== '-' ? row.waitingTime.substring(0, 8) : '00:00:00')
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
                <TableCell style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <b>{row.queuName}</b> <b>{'(' + row.queueNumber + ')'}</b>
                </TableCell>
              </TableRow>
            ) : null;
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
export default QueueList;
