import React, { useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Typography from '@mui/material/Typography';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import LinearProgress from '@material-ui/core/LinearProgress';
import { getDashboardTops } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { QueryBuilder } from '@material-ui/icons';

export default function DashboardTop5TalkTime() {
  const convertTimeToApiString = timeString => {
    return timeString.split('.')[2] + '-' + timeString.split('.')[1] + '-' + timeString.split('.')[0];
  };
  let [startTime, setStartTime] = useState(
    convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 7)).toLocaleDateString('tr-TR')),
  );
  let [endTime, setEndTime] = useState(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
  let string = `with recursive age as (
    select
    dn,
    display_name
    from
    users_view
  ), inb as (
    select 
    dst_dn,
	sum(dst_end_time-dst_answer_time) as durationInb,
    count(distinct(call_id)) as cntInbound
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and dst_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
	and dst_answer_time is not null  
    group by dst_dn
  ),outb as (
    select
    src_dn,
	sum(dst_end_time - dst_answer_time) as duration,  
    count(distinct(call_id)) as cntOutbound
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and src_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
	and dst_answer_time is not null
    group by src_dn
  )
    select
    age.display_name,
	age.dn,
	sum(outb.duration+inb.durationInb)
    from age
    left outer join inb on inb.dst_dn = age.dn
    left outer join outb on outb.src_dn = age.dn
	where outb.duration+inb.durationInb is not null
	group by age.display_name,age.dn
	order by sum(outb.duration+inb.durationInb) desc
	limit 5`;

  let [data, setData] = useState([]);

  useEffect(() => {
    getDashboardTops(string).then(response => {
      setData(JSON.parse(response.data.data));
    });
  }, [startTime, endTime, string]);

  const totalseconds = items => {
    let total = 0;
    for (let i = 0; i < items.length; i++) {
      if (items[i]?.sum.includes('.')) {
        total += parseInt(items[i]?.sum.split('.')[0]) * 24 * 60 * 60;
        total +=
          parseInt(items[i]?.sum.split(':')[0]) * 60 * 60 +
          parseInt(items[i]?.sum.split(':')[1]) * 60 +
          parseInt(items[i]?.sum.split(':')[2]);
      } else {
        total +=
          parseInt(items[i]?.sum.split(':')[0]) * 60 * 60 +
          parseInt(items[i]?.sum.split(':')[1]) * 60 +
          parseInt(items[i]?.sum.split(':')[2]);
      }
    }
    return total;
  };

  const checkDots = timeString => {
    if (timeString === undefined) {
      return '';
    }
    if (timeString?.includes('.')) {
      let tempHour = parseInt(timeString.split('.')[0]) * 24 + parseInt(timeString.split('.')[1].split(':')[0]);
      return tempHour + 'h ' + timeString.split(':')[1] + 'm ' + timeString.split(':')[2] + 's';
    } else {
      return timeString?.split(':')[0] + 'h ' + timeString?.split(':')[1] + 'm ' + timeString?.split(':')[2] + 's';
    }
  };

  let [selectedTime, setSelectedTime] = useState('weekly');

  const handleClick = selectTimeString => {
    setSelectedTime(selectTimeString);
    switch (selectTimeString) {
      case 'weekly':
        setStartTime(
          convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 7)).toLocaleDateString('tr-TR')),
        );
        setEndTime(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
        break;
      case 'daily':
        setStartTime(
          convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 0)).toLocaleDateString('tr-TR')),
        );
        setEndTime(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
        break;
      case 'mountly':
        setStartTime(
          convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 30)).toLocaleDateString('tr-TR')),
        );
        setEndTime(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
        break;
      case 'yearly':
        setStartTime(
          convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 365)).toLocaleDateString('tr-TR')),
        );
        setEndTime(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
        break;
      default:
        setStartTime(
          convertTimeToApiString(new Date(new Date().setDate(new Date().getDate() - 7)).toLocaleDateString('tr-TR')),
        );
        setEndTime(convertTimeToApiString(new Date().toLocaleDateString('tr-TR')));
        break;
    }
  };

  return (
    <div>
      <Card>
        <CardContent style={{ height: '300px' }}>
          <Typography variant="h5" component="div">
            <IntlMessages id={'top5talktime'} />
          </Typography>
          <hr style={{ opacity: '.5', marginTop: '5px', marginBottom: '5px' }} />
          {data.length === 0 && (
            <div style={{ textAlign: 'center', fontWeight: '800', fontSize: '20px' }}>
              <IntlMessages id={'norecord'} />
            </div>
          )}
          {data.length > 0 && (
            <>
              {data.length >= 1 && (
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={7}>
                      <p>
                        {data[0]?.display_name} ({data[0]?.dn})
                      </p>
                    </Grid>
                    <Grid item md={5}>
                      <div style={{ textAlign: 'right' }}>
                        <QueryBuilder style={{ fontSize: '17px', color: '#ff72b3', verticalAlign: '-3px' }} />{' '}
                        <b>{checkDots(data[0]?.sum)}</b>
                      </div>
                    </Grid>
                  </GridContainer>
                  <LinearProgress
                    variant="determinate"
                    color="primary"
                    value={Math.floor((totalseconds([data[0]]) / totalseconds([data[0]])) * 100)}
                    style={{ height: '10px', borderRadius: '10px' }}
                  />
                </div>
              )}
              {data.length >= 2 && (
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={7}>
                      <p>
                        {data[1]?.display_name} ({data[1]?.dn})
                      </p>
                    </Grid>
                    <Grid item md={5}>
                      <div style={{ textAlign: 'right' }}>
                        <QueryBuilder style={{ fontSize: '17px', color: '#ff72b3', verticalAlign: '-3px' }} />{' '}
                        <b>{checkDots(data[1]?.sum)}</b>
                      </div>
                    </Grid>
                  </GridContainer>
                  <LinearProgress
                    variant="determinate"
                    color="primary"
                    value={Math.floor((totalseconds([data[1]]) / totalseconds([data[0]])) * 100)}
                    style={{ height: '10px', borderRadius: '10px' }}
                  />
                </div>
              )}
              {data.length >= 3 && (
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={7}>
                      <p>
                        {data[2]?.display_name} ({data[2]?.dn})
                      </p>
                    </Grid>
                    <Grid item md={5}>
                      <div style={{ textAlign: 'right' }}>
                        <QueryBuilder style={{ fontSize: '17px', color: '#ff72b3', verticalAlign: '-3px' }} />{' '}
                        <b>{checkDots(data[2]?.sum)}</b>
                      </div>
                    </Grid>
                  </GridContainer>
                  <LinearProgress
                    variant="determinate"
                    color="primary"
                    value={Math.floor((totalseconds([data[2]]) / totalseconds([data[0]])) * 100)}
                    style={{ height: '10px', borderRadius: '10px' }}
                  />
                </div>
              )}
              {data.length >= 4 && (
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={7}>
                      <p>
                        {data[3]?.display_name} ({data[3]?.dn})
                      </p>
                    </Grid>
                    <Grid item md={5}>
                      <div style={{ textAlign: 'right' }}>
                        <QueryBuilder style={{ fontSize: '17px', color: '#ff72b3', verticalAlign: '-3px' }} />{' '}
                        <b>{checkDots(data[3]?.sum)}</b>
                      </div>
                    </Grid>
                  </GridContainer>
                  <LinearProgress
                    variant="determinate"
                    color="primary"
                    value={Math.floor((totalseconds([data[3]]) / totalseconds([data[0]])) * 100)}
                    style={{ height: '10px', borderRadius: '10px' }}
                  />
                </div>
              )}
              {data.length >= 5 && (
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={7}>
                      <p>
                        {data[4]?.display_name} ({data[4]?.dn})
                      </p>
                    </Grid>
                    <Grid item md={5}>
                      <div style={{ textAlign: 'right' }}>
                        <QueryBuilder style={{ fontSize: '17px', color: '#ff72b3', verticalAlign: '-3px' }} />{' '}
                        <b>{checkDots(data[4]?.sum)}</b>
                      </div>
                    </Grid>
                  </GridContainer>
                  <LinearProgress
                    variant="determinate"
                    color="primary"
                    value={Math.floor((totalseconds([data[4]]) / totalseconds([data[0]])) * 100)}
                    style={{ height: '10px', borderRadius: '10px' }}
                  />
                </div>
              )}
            </>
          )}
        </CardContent>
        <CardActions></CardActions>
        <ButtonGroup color="primary" variant="contained" aria-label="outlined info button group" style={{ width: '100%' }}>
          <Button
            style={{ width: '25%',textTransform:'none' }}
            size="small"
            disabled={selectedTime === 'daily' ? true : false}
            onClick={() => handleClick('daily')}>
            <IntlMessages id={'daily'} />
          </Button>
          <Button
            style={{ width: '25%',textTransform:'none' }}
            size="small"
            disabled={selectedTime === 'weekly' ? true : false}
            onClick={() => handleClick('weekly')}>
            <IntlMessages id={'weekly'} />
          </Button>
          <Button
            style={{ width: '25%',textTransform:'none' }}
            size="small"
            disabled={selectedTime === 'mountly' ? true : false}
            onClick={() => handleClick('mountly')}>
            <IntlMessages id={'mountly'} />
          </Button>
          <Button
            style={{ width: '25%',textTransform:'none' }}
            size="small"
            disabled={selectedTime === 'yearly' ? true : false}
            onClick={() => handleClick('yearly')}>
            <IntlMessages id={'yearly'} />
          </Button>
        </ButtonGroup>
      </Card>
    </div>
  );
}
