import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import IntlMessages from '@jumbo/utils/IntlMessages';
import dateFormat from 'dateformat';
export default function CallTerminationReportTable({ data, totalCount, limit, setLimit, page, setPage, offset, setOffset }) {
  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));
  const classes = useStyles();

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  return (
    <>
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table" size="small">
            <TableHead>
              <TableRow>
                <TableCell>
                  <b>#</b>
                </TableCell>
                <TableCell>
                  <IntlMessages id={'filter.menu.startdate'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'filter.menu.enddate'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'dailysummary.modal.caller'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'dailysummary.modal.called'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'callterminator'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.length > 0 &&
                data.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>
                      <b>{index + 1 + offset}</b>
                    </TableCell>
                    <TableCell>{dateFormat(item.start_time, 'yyyy-mm-dd HH:MM:ss')}</TableCell>
                    <TableCell>{dateFormat(item.end_time, 'yyyy-mm-dd HH:MM:ss')}</TableCell>
                    <TableCell>
                      {item.src_display_name} ({item.src_dn})
                    </TableCell>
                    <TableCell>
                      {item.dst_display_name} ({item.dst_dn})
                    </TableCell>
                    <TableCell>
                      <b>
                        {item.act === '5'
                          ? `${item.src_display_name} (${item.src_dn})`
                          : `${item.dst_display_name} (${item.dst_dn})`}
                      </b>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={totalCount}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={data.length < limit || data.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
}
