import { Button, FormControl, InputLabel, OutlinedInput } from '@mui/material';
import React, { useState } from 'react';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { updateDepartmant } from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function UpdateDepartmanModal({ selectedDep, setReloadValues, updateHandleClose }) {
  let [name, setName] = useState(selectedDep.name);
  const handleChangeName = event => {
    setName(event.target.value);
  };

  const handleSubmit = () => {
    if (name === undefined || name === '') {
      NotificationManager.error('Departman adƒ± doƒüru formatta deƒüil', '', 3000);
    } else {
      updateDepartmant(selectedDep.id, name).then(result => {
        NotificationManager.success('Kullanƒ±cƒ± Eklendi', '', 3000);
        setReloadValues(name);
        updateHandleClose();
      });
    }
  };

  return (
    <div>
      <NotificationContainer />
      <FormControl sx={{ m: 1, width: '100%' }} variant="outlined">
        <InputLabel htmlFor="name">
          <IntlMessages id={'departmentname'} />
        </InputLabel>
        <OutlinedInput id="name" type="text" value={name} onChange={handleChangeName} label="Departmant Name" />
      </FormControl>
      <div>
        <Button sx={{ width: '100%' }} color="info" variant="contained" onClick={() => handleSubmit()}>
          <IntlMessages id={'update'} />
        </Button>
      </div>
    </div>
  );
}
