import React from 'react';
import CmtAdvCardContent from '../../../../@coremat/CmtAdvCard/CmtAdvCardContent';
import CmtAdvCard from '../../../../@coremat/CmtAdvCard';
import CmtCardHeader from '../../../../@coremat/CmtCard/CmtCardHeader';
import { alpha, makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import CmtList from '../../../../@coremat/CmtList';
import DateRange from '@material-ui/icons/DateRange';
import SiteTrafficGraph from './HeaderChart';
import { PhoneCallback, PhoneMissed, PhoneForwarded } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Item from './HeaderChartItem';
import clsx from 'clsx';
const useStyles = makeStyles(theme => ({
  scrollbarRoot: {
    maxHeight: 350,
  },
  searchAction: {
    position: 'relative',
    width: 38,
    height: 38,
  },
  searchActionBar: {
    position: 'absolute',
    right: 0,
    top: 2,
    zIndex: 1,
  },
  cardContentRoot: {
    '& .MuiGrid-container': {
      alignItems: 'center',
    },
  },
  subTitleRoot: {
    fontSize: 12,
    color: theme.palette.text.secondary,
  },
  badgeRoot: {
    fontSize: 14,
    letterSpacing: 0.25,
    backgroundColor: alpha(theme.palette.success.main, 0.15),
    color: theme.palette.success.main,
    padding: '2px 10px',
    borderRadius: 30,
  },
  optionList: {
    display: 'flex',
    flexWrap: 'wrap',
    marginBottom: 24,

    '& > *:not(:last-child)': {
      marginRight: 20,
      [theme.breakpoints.up('md')]: {
        marginRight: 40,
      },
    },
  },
}));

const SiteTraffic = props => {
  const handleChange = a => {
    if (a === 'weekly') {
      if (props.date.getDay() === 0) {
        props.setParam(7);
      } else {
        props.setParam(props.date.getDay());
      }
    } else if (a === 'monthly') {
      props.setParam(props.date.getDate());
    } else if (a === 'yearly') {
      props.setParam(calculateYearOfTheDay(props.date));
    }
  };

  function calculateYearOfTheDay(param) {
    var start = new Date(param.getFullYear(), 0, 0);
    var diff = param - start;
    var oneday = 1000 * 60 * 60 * 24;
    var day = Math.floor(diff / oneday);
    return day;
  }
  const actions = [
    {
      label: <IntlMessages id={'select.menu.weekly'} />,
      icon: <DateRange />,
      onClick: () => {
        handleChange('weekly');
      },
    },
    {
      label: <IntlMessages id={'select.menu.monthly'} />,
      icon: <DateRange />,
      onClick: () => {
        handleChange('monthly');
      },
    },
    {
      label: <IntlMessages id={'select.menu.yearly'} />,
      icon: <DateRange />,
      onClick: () => {
        handleChange('yearly');
      },
    },
  ];
  const list = [
    {
      icon: <PhoneCallback />,
      label: <IntlMessages id={'dashboard.inbound'} />,
      bgColor: '#C8BDFA',
      color: '#7F39FB',
    },
    {
      icon: <PhoneMissed />,
      label: <IntlMessages id={'dashboard.missed'} />,
      bgColor: '#ADD8E6',
      color: '#0795F4',
    },
    {
      icon: <PhoneForwarded />,
      label: <IntlMessages id={'dashboard.outbound'} />,
      bgColor: '#FBF3D2',
      color: '#FF8C00',
    },
  ];
  const classes = useStyles();
  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <CmtAdvCard>
        <CmtCardHeader
          className={clsx(classes.headerRoot, 'pt-4')}
          title={<IntlMessages id={'dashboard.statistics'} />}
          actionsPos="top-corner"
          actions={actions}
          style={{ height: '10px' }}></CmtCardHeader>
        <CmtAdvCardContent className={classes.cardContentRoot}>
          <CmtList
            className={classes.optionList}
            data={list}
            style={{ height: '25px' }}
            renderRow={(item, index) => <Item key={index} item={item} />}
          />
          <Box>
            <SiteTrafficGraph inbound={props.inbound} outbound={props.outbound} missed={props.missed} />
          </Box>
        </CmtAdvCardContent>
      </CmtAdvCard>
    </div>
  );
};

export default SiteTraffic;
