import React from 'react';
import { Area, AreaChart, ResponsiveContainer, Tooltip } from 'recharts';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  tooltip: {
    position: 'relative',
    borderRadius: 6,
    padding: '4px 12px',
    backgroundColor: '#FA744F',
    color: theme.palette.common.white,
  },
}));

const MissedCallGraph = props => {
  const classes = useStyles();

  return (
    <ResponsiveContainer className="card-img-bottom overflow-hidden" width="100%" height={50}>
      <AreaChart data={props.data} margin={{ top: 0, right: 0, left: 0, bottom: 0 }}>
        <Tooltip
          labelStyle={{ color: 'black' }}
          cursor={false}
          content={data => {
            return data.payload[0] ? (
              <div className={classes.tooltip}>
                {data.payload[0].payload.start_time}({data.payload[0].payload.count})
              </div>
            ) : null;
          }}
        />
        <defs>
          <linearGradient id="color5" x1="0" y1="0" x2="0" y2="1">
            <stop offset="5%" stopColor="#F79071" stopOpacity={1} />
            <stop offset="95%" stopColor="#FA744F" stopOpacity={1} />
          </linearGradient>
        </defs>
        <Area
          dataKey="count"
          type="monotone"
          strokeWidth={0}
          stackId="2"
          stroke="#FA744F"
          fill="url(#color5)"
          fillOpacity={1}
        />
      </AreaChart>
    </ResponsiveContainer>
  );
};

export default MissedCallGraph;
