import React, { createRef } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { Button, Card, CardContent } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { useScreenshot, createFileName } from 'use-react-screenshot';
import { CloudDownload } from '@material-ui/icons';
import { useIntl } from 'react-intl';
import dateFormat from 'dateformat';
const Chart = ({ response }) => {
  let datalist = [{ hour: '', count: 0 }];
  datalist = response.map(item => ({
    hour: dateFormat(item.timezone, 'HH:MM'),
    count: parseInt(item.acc),
  }));
  const ref = createRef(null);
  const [, takeScreenShot] = useScreenshot({
    type: 'image/jpeg',
    quality: 1.0,
  });

  const download = (image, { name = 'img', extension = 'jpg' } = {}) => {
    const a = document.createElement('a');
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const downloadScreenshot = () => takeScreenShot(ref.current).then(download);
  let curentLanguage = useIntl();

  return (
    <>
      <div style={{ textAlign: 'right' }}>
        <Button style={{ backgroundColor: '#3f51b5', color: 'white', marginBottom: '10px' }} onClick={downloadScreenshot}>
          <IntlMessages id={'dowlandimage'} /> <CloudDownload />
        </Button>
      </div>
      <Card>
        <CardContent ref={ref}>
          <ResponsiveContainer width="100%" height={200}>
            <LineChart data={datalist} margin={{ top: 0, right: 0, left: 0, bottom: 0 }}>
              <XAxis dataKey="hour" label={{
                  value:
                  curentLanguage.locale === 'en-EN'
                  ? 'Hour'
                  : curentLanguage.locale === 'de-DE'
                  ? 'Stunden'
                  : curentLanguage.locale === 'tr-TR'
                  ? 'Saat'
                  : curentLanguage.locale === 'nl-NL'
                  ? 'Uur'
                  : 'Hour',
                  position: 'insideBottom',
                  offset:0
                }}>
              </XAxis>
              <YAxis
                allowDecimals={false}
                dataKey="count"
                label={{
                  value:
                    curentLanguage.locale === 'en-EN'
                      ? 'Count'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Z√§hlen'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Adet'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Count'
                      : 'Count',
                  angle: -90,
                  position: 'insideLeft',
                }}
              />
              <CartesianGrid strokeDasharray="3 3" />
              <Tooltip
                labelStyle={{ color: 'black' }}
                itemStyle={{ color: 'black' }}
                cursor={false}
                content={<CustomTooltip/>}
              />
              <Legend verticalAlign="top" />
              <Line dataKey="count" dot={false} stroke="#3b65ff" />
            </LineChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </>
  );
};
const CustomTooltip = ({ active, payload }) => {
  if (active && payload && payload.length) {
    return (
      <div style={{ backgroundColor: 'white' }}>
        <p>
          <IntlMessages id={'hour'} />: {payload[0]?.payload.hour}
        </p>
        <p>
          <IntlMessages id={'call.time'} />: {payload[0]?.payload.count}
        </p>
      </div>
    );
  }

  return null;
};
export default Chart;
