import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
  },
  table: {
    minWidth: 650,
  },
}));
function createData(level, cnt) {
  return { level, cnt };
}

function DataTable({ response }) {
  const classes = useStyles();

  let total = 0;
  for (var i = 0; i < response.length; i++) {
    total = total + parseInt(response[i].cnt);
  }
  function percent(cnt) {
    var result = 0;
    result = parseInt(cnt) / total;
    return (parseFloat(result) * 100).toFixed(2);
  }
  const rows = response.map(data => createData(data.ring, percent(data.cnt)));

  const csvData = rows.map(row => ({
    level: row.level,
    cnt: `%${row.cnt === 'NaN' ? '00.00' : row.cnt}`,
  }));

  let currentLanguage = useIntl();
  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        <ExcelFile
          filename={
            currentLanguage.locale === 'en-EN'
              ? 'Service Level'
              : currentLanguage.locale === 'tr-TR'
              ? 'Servis Seviyesi'
              : currentLanguage.locale === 'nl-NL'
              ? 'Service Level'
              : currentLanguage.locale === 'de-DE'
              ? 'Service Level'
              : ''
          }
          element={
            <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
              <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
            </Button>
          }>
          <ExcelSheet data={csvData} name="Data">
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Seconds'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Saniye'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Seconden'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Sekunden'
                  : ''
              }
              value="level"
            />
            <ExcelColumn
              label={
                currentLanguage.locale === 'en-EN'
                  ? 'Service Level'
                  : currentLanguage.locale === 'tr-TR'
                  ? 'Servis Seviyesi'
                  : currentLanguage.locale === 'nl-NL'
                  ? 'Service Level'
                  : currentLanguage.locale === 'de-DE'
                  ? 'Service Level'
                  : ''
              }
              value="cnt"
            />
          </ExcelSheet>
        </ExcelFile>
      </div>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell style={{ width: '100px' }} align="center">
                <IntlMessages id={'seconds'} />
              </TableCell>
              <TableCell style={{ width: '100px' }} align="center">
                <IntlMessages id={'service.level'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => (
              <TableRow key={row.level}>
                <TableCell align="center">{row.level}</TableCell>
                <TableCell align="center">%{row.cnt === 'NaN' ? '00.00' : row.cnt}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}

export default DataTable;
