import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, Card, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import React from 'react';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';

export default function Detail({ totalAgent, totalDid }) {
  let currentLanguage = useIntl();
  return (
    <>
      <div style={{ marginTop: '20px' }}>
        <GridContainer>
          <Grid item md={6}>
            <div style={{ marginBottom: '10px', textAlign: 'right' }}>
              {totalDid.length > 0 && (
                <ExcelFile
                  filename={currentLanguage.locale === 'en-US' ? 'DID_top_5' : 'DID_top_5'}
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={totalDid} name="Data">
                    <ExcelColumn
                      label={currentLanguage.locale === 'en-US' ? 'DID number' : 'DID Numarasƒ±'}
                      value="src_did"
                    />
                    <ExcelColumn
                      label={currentLanguage.locale === 'en-US' ? 'Total Call Count' : 'Toplam Arama Adedi'}
                      value="calldid"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
            <Card>
              <TableContainer style={{ width: '100%' }}>
                <Table style={{ width: '100%' }}>
                  <TableHead>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        <IntlMessages id={'mountlydetails.didnumber'} />
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        <IntlMessages id={'mountlydetails.totalcall'} />
                      </TableCell>
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[0]?.src_did !== undefined ? totalDid[0]?.src_did : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[0]?.calldid !== undefined ? totalDid[0]?.calldid : '-'}{' '}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[1]?.src_did !== undefined ? totalDid[1]?.src_did : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[1]?.calldid !== undefined ? totalDid[1]?.calldid : '-'}{' '}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[2]?.src_did !== undefined ? totalDid[2]?.src_did : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[2]?.calldid !== undefined ? totalDid[2]?.calldid : '-'}{' '}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[3]?.src_did !== undefined ? totalDid[3]?.src_did : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[3]?.calldid !== undefined ? totalDid[3]?.calldid : '-'}{' '}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[4]?.src_did !== undefined ? totalDid[4]?.src_did : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalDid[4]?.calldid !== undefined ? totalDid[4]?.calldid : '-'}{' '}
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              </TableContainer>
            </Card>
          </Grid>
          <Grid item md={6}>
            <div style={{ marginBottom: '10px', textAlign: 'right' }}>
              {totalAgent.length > 0 && (
                <ExcelFile
                  filename={currentLanguage.locale === 'en-US' ? 'Agent_top_5' : 'Agent_top_5'}
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={totalAgent} name="Data">
                    <ExcelColumn
                      label={currentLanguage.locale === 'en-US' ? 'Agent Number' : 'Agent Numarasƒ±'}
                      value="dst_display_name"
                    />
                    <ExcelColumn
                      label={currentLanguage.locale === 'en-US' ? 'Total Call Count' : 'Toplam Arama Adedi'}
                      value="agentcall"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
            <Card>
              <TableContainer style={{ width: '100%' }}>
                <Table style={{ width: '100%' }}>
                  <TableHead>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        <IntlMessages id={'mountlydetails.agentnumber'} />
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        <IntlMessages id={'mountlydetails.totalcall'} />
                      </TableCell>
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[0]?.dst_display_name !== undefined ? totalAgent[0]?.dst_display_name : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[0]?.agentcall !== undefined ? totalAgent[0]?.agentcall : '-'}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[1]?.dst_display_name !== undefined ? totalAgent[1]?.dst_display_name : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[1]?.agentcall !== undefined ? totalAgent[1]?.agentcall : '-'}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[2]?.dst_display_name !== undefined ? totalAgent[2]?.dst_display_name : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[2]?.agentcall !== undefined ? totalAgent[2]?.agentcall : '-'}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[3]?.dst_display_name !== undefined ? totalAgent[3]?.dst_display_name : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[3]?.agentcall !== undefined ? totalAgent[3]?.agentcall : '-'}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[4]?.dst_display_name !== undefined ? totalAgent[4]?.dst_display_name : '-'}
                      </TableCell>
                      <TableCell style={{ textAlign: 'center' }}>
                        {totalAgent[4]?.agentcall !== undefined ? totalAgent[4]?.agentcall : '-'}
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              </TableContainer>
            </Card>
          </Grid>
        </GridContainer>
      </div>
    </>
  );
}
