import React, { useState, useEffect } from 'react';
import {getCallScoresByCallId } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function Score({call_id, reloadValues}) {
    const [callScore, setCallScore] = useState('-1');
    useEffect(()=>{
        getCallScoresByCallId(call_id).then(resp => {
            setCallScore(JSON.parse(resp.data.data)[0]?.rating || '-1');
        });
        return () => {setCallScore('-1')}
    },[call_id, reloadValues]);
  return (
      <>
      {
          callScore !== '-1' ? (
            <div>
            {
                GetScoreName(callScore)
            }
            {` (${callScore})`}
            </div>
          ) : (
            <small style={{"margin":"0 4px"}}><IntlMessages id={'noscore'} /></small>
          )
      }
      </>
  );
}

function GetScoreName(rating){
    switch (rating) {
        case "5":
            return <IntlMessages id={'score.rating5'} />
        case "4":
            return <IntlMessages id={'score.rating4'} />
        case "3":
            return <IntlMessages id={'score.rating3'} />
        case "2":
            return <IntlMessages id={'score.rating2'} />
        case "1":
            return <IntlMessages id={'score.rating1'} />
        default:
            return <IntlMessages id={'noscore'} /> 
    }
}