import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import DateRangeIcon from '@mui/icons-material/DateRange';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { CloudDownload } from '@material-ui/icons';
import { useIntl } from 'react-intl';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';

const DataTable = ({ response }) => {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));
  function createData(date, count, total, act) {
    return { date, count, total, act };
  }

  function addZero(param) {
    if (param < 10) {
      param = '0' + param;
    }
    return param;
  }
  const rows = response.map(data => createData(data.day_log, data.count, data.total, data.act));

  let data = rows.map(unit => ({
    date: unit.date.split('T')[0],
    count: unit.count,
    total:
      unit.total.hours === undefined
        ? unit.total.minutes === undefined
          ? unit.total.seconds === undefined
            ? '00:00:00'
            : '00:00:' + addZero(unit.total.seconds)
          : unit.total.seconds === undefined
          ? '00:' + addZero(unit.total.minutes) + ':00'
          : '00:' + addZero(unit.total.minutes) + ':' + addZero(unit.total.seconds)
        : unit.total.minutes === undefined
        ? unit.total.seconds === undefined
          ? addZero(unit.total.hours) + ':00:00'
          : addZero(unit.total.hours) + ':00:' + addZero(unit.total.seconds)
        : unit.total.seconds === undefined
        ? addZero(unit.total.hours) + ':' + addZero(unit.total.minutes) + ':00'
        : addZero(unit.total.hours) + ':' + addZero(unit.total.minutes) + ':' + addZero(unit.total.seconds),
    act:
      unit.act !== undefined
        ? unit.act.hours === undefined
          ? unit.act.minutes === undefined
            ? unit.act.seconds === undefined
              ? '00:00:00'
              : '00:00:' + addZero(unit.act.seconds)
            : unit.act.seconds === undefined
            ? '00:' + addZero(unit.act.minutes) + ':00'
            : '00:' + addZero(unit.act.minutes) + ':' + addZero(unit.act.seconds)
          : unit.act.minutes === undefined
          ? unit.act.seconds === undefined
            ? addZero(unit.act.hours) + ':00:00'
            : addZero(unit.act.hours) + ':00:' + addZero(unit.act.seconds)
          : unit.act?.seconds === undefined
          ? addZero(unit.act.hours) + ':' + addZero(unit.act.minutes) + ':00'
          : addZero(unit.act.hours) + ':' + addZero(unit.act.minutes) + ':' + addZero(unit.act.seconds)
        : '00:00:00',
  }));

  const classes = useStyles();

  let total = {
    day: 0,
    count: 0,
    totalTime: 0,
    averageTime: 0,
  };

  function timeToSecond(param) {
    let second = parseInt(param.hours) * 3600 + parseInt(param.minutes) * 60 + parseInt(param.seconds);
    return second;
  }
  for (let i = 0; i < rows.length; i++) {
    total.count += parseInt(rows[i].count);
    total.day = rows.length;
    total.totalTime += timeToSecond(rows[i].total);
    total.averageTime += timeToSecond(rows[i].act);
  }

  function secondToTime(param, day) {
    param = param / parseInt(day);
    let string =
      addZero(Math.floor(param / 3600)) +
      ':' +
      addZero(Math.floor((param % 3600) / 60)) +
      ':' +
      addZero(Math.floor((param % 3600) % 60));
    return string;
  }

  let currentLanguage = useIntl();

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {data.length > 0 && (
          <ExcelFile
            filename={
              currentLanguage.locale === 'en-EN'
                ? 'Daily log report'
                : currentLanguage.locale === 'tr-TR'
                ? 'Gunluk yuk raporu'
                : currentLanguage.locale === 'nl-NL'
                ? 'Dagelijks logboekrapport'
                : currentLanguage.locale === 'de-DE'
                ? 'T√§glicher Protokollbericht'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={data} name="Data">
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="date"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Call Count'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Arama Adedi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Gesprekstelling'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Anrufz√§hler'
                    : ''
                }
                value="count"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Duration'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'S√ºre'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Looptijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Dauer'
                    : ''
                }
                value="total"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Average Call Time'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Ortalama √áaƒürƒ± S√ºresi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Gemiddelde beltijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliche Anrufzeit'
                    : ''
                }
                value="act"
              />
            </ExcelSheet>
          </ExcelFile>
        )}
      </div>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow style={{ backgroundColor: '#2978B5' }}>
              <TableCell>
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'filter.menu.date'} />
                </b>
              </TableCell>
              <TableCell align="right">
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'call.time'} />
                </b>
              </TableCell>
              <TableCell align="right">
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'wallboard.call.time'} />
                </b>
              </TableCell>
              <TableCell align="right">
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'Act'} />
                </b>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row,index) => (
              <TableRow key={index}>
                <TableCell component="th" scope="row">
                  <DateRangeIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
                  <b style={{ fontFamily: 'Roboto' }}>{row.date.split('T')[0]}</b>
                </TableCell>
                <TableCell align="right">
                  <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{row.count}</b>
                </TableCell>
                <TableCell align="right" style={{ color: '#FF87CA' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                  <b>
                    {(row.total.hours === undefined
                      ? row.total.minutes === undefined
                        ? row.total.seconds === undefined
                          ? '00:00:00'
                          : '00:00:' + addZero(row.total.seconds)
                        : row.total.seconds === undefined
                        ? '00:' + addZero(row.total.minutes) + ':00'
                        : '00:' + addZero(row.total.minutes) + ':' + addZero(row.total.seconds)
                      : row.total.minutes === undefined
                      ? row.total.seconds === undefined
                        ? addZero(row.total.hours) + ':00:00'
                        : addZero(row.total.hours) + ':00:' + addZero(row.total.seconds)
                      : row.total.seconds === undefined
                      ? addZero(row.total.hours) + ':' + addZero(row.total.minutes) + ':00'
                      : addZero(row.total.hours) + ':' + addZero(row.total.minutes) + ':' + addZero(row.total.seconds)
                    )
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </b>
                </TableCell>
                <TableCell align="right" style={{ color: '#FF87CA' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                  <b style={{ color: '#FF87CA' }}>
                    {(row.act !== undefined
                      ? row.act.hours === undefined
                        ? row.act.minutes === undefined
                          ? row.act.seconds === undefined
                            ? '00:00:00'
                            : '00:00:' + addZero(row.act.seconds)
                          : row.act.seconds === undefined
                          ? '00:' + addZero(row.act.minutes) + ':00'
                          : '00:' + addZero(row.act.minutes) + ':' + addZero(row.act.seconds)
                        : row.act.minutes === undefined
                        ? row.act.seconds === undefined
                          ? addZero(row.act.hours) + ':00:00'
                          : addZero(row.act.hours) + ':00:' + addZero(row.act.seconds)
                        : row.act?.seconds === undefined
                        ? addZero(row.act.hours) + ':' + addZero(row.act.minutes) + ':00'
                        : addZero(row.act.hours) + ':' + addZero(row.act.minutes) + ':' + addZero(row.act.seconds)
                      : '00:00:00'
                    )
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </b>
                </TableCell>
              </TableRow>
            ))}
            <TableRow style={{ backgroundColor: '#80ED99' }}>
              <TableCell>
                <DateRangeIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
                <b>
                  {total.day + ' '} <IntlMessages id={'day'} />{' '}
                </b>
              </TableCell>
              <TableCell align="right">
                <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{total.count}</b>
              </TableCell>
              <TableCell align="right" style={{ color: 'black' }}>
                <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                <b>
                  {secondToTime(total.totalTime, 1)
                    .toString()
                    .replace(':', 'h ')
                    .replace(':', 'm ') + 's'}
                </b>
              </TableCell>
              <TableCell align="right" style={{ color: 'black' }}>
                <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                <b>
                  {secondToTime(total.averageTime, total.day)
                    .toString()
                    .replace(':', 'h ')
                    .replace(':', 'm ') + 's'}
                </b>
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataTable;
