import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import { Button, Toolbar, Typography } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { CloudDownload } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
const AnsweredCalls = ({
  answeredcalls,
  setAnsweredCalls,
  setAnsweredLimit,
  setPage,
  page,
  limit,
  setOffset,
  answeredCallCount,
  offset,
}) => {
  const columns = [
    {
      id: 'num',
      label: '#',
    },
    { id: 'cnt', label: <IntlMessages id={'call.time'} />, minWidth: 60, align: 'center' },
    { id: 'src_display_name', label: <IntlMessages id={'caller'} />, minWidth: 60, align: 'center' },
  ];

  function createData(num, cnt, src_display_name) {
    return { num, cnt, src_display_name };
  }

  const rows = answeredcalls.map((data, index) => createData(index + 1 + offset, data.cnt, data.src_display_name));

  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));
  const classes = useStyles();

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setAnsweredLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  let curentLanguage = useIntl();

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDowlandButtonClick = () => {
    setAnsweredLimit(answeredCallCount);
    setPage(0);
    setOffset(0);
    setAnsweredCalls([]);
    handleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  return (
    <>
      {rows.length > 0 && (
        <div style={{ textAlign: 'right', marginBottom: '10px' }}>
          <Button onClick={() => handleDowlandButtonClick()} style={{ backgroundColor: '#3f51b5', color: 'white' }}>
            <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          </Button>
        </div>
      )}
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          {rows.length < 1 && (
            <div style={{ textAlign: 'center' }}>
              <CircularProgress />
              <p>Veriler hazirlanƒ±yor...</p>
            </div>
          )}
          <div style={{ textAlign: 'center' }}>
            {rows.length < 1 && (
              <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            )}
            {rows.length > 0 && (
              <ExcelFile
                filename={curentLanguage.locale === 'en-US' ? 'Repetetive_answered_calls' : 'Cevaplanmis_m√ºkerrer_arama'}
                element={
                  <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                    <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                  </Button>
                }>
                <ExcelSheet data={rows} name="data">
                  <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Call Count' : 'Arama Sayƒ±sƒ±'} value="cnt" />
                  <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Caller' : 'Arayan'} value="src_display_name" />
                </ExcelSheet>
              </ExcelFile>
            )}
          </div>
        </Box>
      </Modal>
      <Paper className={classes.root}>
        <Toolbar>
          <Typography>
            <IntlMessages id={'wallboard.answered'} />
          </Typography>
        </Toolbar>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table" size="small">
            <TableHead>
              <TableRow>
                {columns.map(column => (
                  <TableCell key={column.id} align={column.align} style={{ minWidth: column.minWidth }}>
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row, index) => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                    {columns.map(column => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 100, answeredCallCount].sort((a, b) => a - b)}
          component="div"
          count={answeredCallCount}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={
            answeredcalls.length < limit || answeredcalls.length === 0 ? { disabled: true } : { disabled: false }
          }
          rowsPerPage={limit}
          backIconButtonText={String(rows.length)}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
};

export default AnsweredCalls;
