import React, { useState, useEffect } from 'react';
import { getSurveyScoresByCallId } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function SurveyScore({ call_id }) {
  const [surveyScore, setSurveyScore] = useState('-1');
  useEffect(() => {
    getSurveyScoresByCallId(call_id).then(resp => {
      setSurveyScore(JSON.parse(resp.data.data)[0]?.supervisorId || '-1');
    });
    return () => {
      setSurveyScore('-1');
    };
  }, [call_id]);
  return (
    <>
      {surveyScore !== '-1' ? (
        <div>{surveyScore}</div>
      ) : (
        <small style={{ margin: '0 4px' }}>
          <IntlMessages id={'nosurvey'} />
        </small>
      )}
    </>
  );
}
