import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import CallTable from './AllCalls';
import Model from './Model';
import AnsweredCalls from './AnsweredCalls';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Modal from '@mui/material/Modal';
import Backdrop from '@mui/material/Backdrop';
import { Box, Button } from '@mui/material';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import CounterCard from '@jumbo/components/Common/CounterCard';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { CloudDownload } from '@material-ui/icons';

const RepetetiveCalls = () => {
  const [limit, setLimit] = useState(10);
  const [answeredLimit, setAnsweredLimit] = useState(10);
  const [allCallOffset, setAllCallOffset] = useState(0);
  const [answeredCallsOffset, setAnsweredCallOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [pageAnswered, setPageAnswered] = useState(0);
  const [allcalls, setallcalls] = useState([]);
  const [answeredCalls, setAnsweredCalls] = useState([]);
  const [callerNumber, setCallerNumber] = useState('');
  const [responseCaller, setResponseCaller] = useState([]);
  const [open, setOpen] = useState(false);
  const [allCallCount, setAllCallCount] = useState(10);
  const [answeredCallCount, setAnsweredCallCount] = useState(10);
  let curentLanguage = useIntl();

  const handleClose = event => {
    setOpen(false);
  };

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'repetetive.calls'} />, isActive: true },
  ];

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 1000,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  return (
    <PageContainer heading={<IntlMessages id={'repetetive.calls'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item md={12} xl={12} xs={12}>
          <Filter
            setResponseCaller={setResponseCaller}
            setAnsweredCallCount={setAnsweredCallCount}
            setallcalls={setallcalls}
            callerNumber={callerNumber}
            answeredLimit={answeredLimit}
            limit={limit}
            allCallOffset={allCallOffset}
            answeredCallsOffset={answeredCallsOffset}
            setAnsweredCalls={setAnsweredCalls}
            setAllCallCount={setAllCallCount}
          />
        </Grid>
        <Grid item md={12} xl={12} xs={12}>
          <GridContainer>
            <Grid item md={6}>
              <div style={{ textAlign: 'center' }}>
                <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
                  <CounterCard
                    icon={<AddIcCallIcon />}
                    number={allCallCount}
                    numberProps={{
                      fontSize: 25,
                    }}
                    label={<IntlMessages id={'allcalls'} />}
                    labelProps={{
                      fontSize: 13,
                    }}
                    backgroundColor={['#6166B3 -18.96%', '#544179 108.17%']}
                    gradientDirection="180deg"
                  />
                </div>
              </div>
            </Grid>
            <Grid item md={6}>
              <div style={{ textAlign: 'center' }}>
                <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
                  <CounterCard
                    icon={<PhoneInTalkIcon />}
                    number={answeredCallCount}
                    numberProps={{
                      fontSize: 25,
                    }}
                    label={<IntlMessages id={'wallboard.answered'} />}
                    labelProps={{
                      fontSize: 13,
                    }}
                    backgroundColor={['#6166B3 -18.96%', '#544179 108.17%']}
                    gradientDirection="180deg"
                  />
                </div>
              </div>
            </Grid>
          </GridContainer>
        </Grid>
        <Grid item md={12} xl={12} xs={12}>
          <CallTable
            setOpen={setOpen}
            responseCaller={responseCaller}
            offset={allCallOffset}
            setOffset={setAllCallOffset}
            allcalls={allcalls}
            setallcalls={setallcalls}
            setLimit={setLimit}
            limit={limit}
            setCallerNumber={setCallerNumber}
            setPage={setPage}
            page={page}
            allCallCount={allCallCount}
          />
        </Grid>
        <Grid item md={12} xl={12} xs={12}>
          <AnsweredCalls
            answeredcalls={answeredCalls}
            setAnsweredCalls={setAnsweredCalls}
            answeredCallCount={answeredCallCount}
            setAnsweredLimit={setAnsweredLimit}
            setPage={setPageAnswered}
            page={pageAnswered}
            limit={answeredLimit}
            setOffset={setAnsweredCallOffset}
            offset={answeredCallsOffset}
          />
        </Grid>
      </GridContainer>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Box sx={style}>
          <Model responseCaller={responseCaller} />
          <div style={{ textAlign: 'right', marginTop: '10px' }}>
            <ExcelFile
              filename={curentLanguage.locale === 'en-US' ? 'Repetetive_calls_Detail' : 'M√ºkerrer_arama_Detay'}
              element={
                <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              }>
              <ExcelSheet data={responseCaller} name="responseCaller">
                <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Caller' : 'Arayan'} value="src_display_name" />
                <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'DID Number' : 'DID Numarasƒ±'} value="src_did" />
                <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Called' : 'Hedef DN'} value="dst_display_name" />
                <ExcelColumn
                  label={curentLanguage.locale === 'en-US' ? 'Talk Time' : 'Konu≈üma S√ºresi'}
                  value="talkingtime"
                />
                <ExcelColumn
                  label={curentLanguage.locale === 'en-US' ? 'Waiting Time' : 'Bekleme S√ºresi'}
                  value="waitingtime"
                />
              </ExcelSheet>
            </ExcelFile>
            <Button style={{ backgroundColor: '#3f51b5', color: 'white', marginLeft: '10px' }} onClick={() => handleClose()}>
              <IntlMessages id={'close'} />
            </Button>
          </div>
        </Box>
      </Modal>
    </PageContainer>
  );
};

export default RepetetiveCalls;
