import React from 'react';
import { CryptoCard } from '../../../../@jumbo/components/Common';
import PhoneForwardedIcon from '@material-ui/icons/PhoneForwarded';
import OutboundGraph from './OutboundGraph';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, Menu, MenuItem } from '@material-ui/core';
import { DateRange, Dehaze } from '@material-ui/icons';

const OutboundCall = props => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleChange = a => {
    if (a === 'weekly') {
      if (props.date.getDay() === 0) {
        props.setParam(7);
        handleClose();
      } else {
        props.setParam(props.date.getDay());
        handleClose();
      }
    } else if (a === 'monthly') {
      props.setParam(props.date.getDate());
      handleClose();
    } else if (a === 'yearly') {
      props.setParam(calculateYearOfTheDay(props.date));
      handleClose();
    }
  };

  function calculateYearOfTheDay(param) {
    var start = new Date(param.getFullYear(), 0, 0);
    var diff = param - start;
    var oneday = 1000 * 60 * 60 * 24;
    var day = Math.floor(diff / oneday);
    return day;
  }




  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <CryptoCard
        title={
          <div>
            <IntlMessages id={'dashboard.outbound'} />
            <span>
              <Button
                id="basic-button"
                aria-controls="basic-menu"
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}>
                <Dehaze />
              </Button>
              <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                  'aria-labelledby': 'basic-button',
                }}>
                <MenuItem onClick={() => handleChange('weekly')}>
                  <DateRange />
                  <IntlMessages id="select.menu.weekly" />
                </MenuItem>
                <MenuItem onClick={() => handleChange('monthly')}>
                  <DateRange />
                  <IntlMessages id="select.menu.monthly" />
                </MenuItem>

                <MenuItem onClick={() => handleChange('yearly')}>
                  <DateRange />
                  <IntlMessages id="select.menu.yearly" />
                </MenuItem>
              </Menu>
            </span>
          </div>
        }
        amount={props.total}
        progress={{ icon: <PhoneForwardedIcon />, color: '#E00930' }}>
        <OutboundGraph data={props.data} />
      </CryptoCard>
    </div>
  );
};

export default OutboundCall;
