import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { addDepOwnedQueues, deleteDepOwnedQueues, getDepartmants, getDepOwnedQueues, getQueues } from 'api/api';
import React, { useEffect, useState } from 'react';
import Checkbox from '@mui/material/Checkbox';
import IntlMessages from '@jumbo/utils/IntlMessages';
import TagTable from '../TagTable/TagTable';

export default function DepartmentQueues({ reloadValues }) {
  let [deps, setDeps] = useState([]);
  let [queues, setQueues] = useState([]);
  let [depOwnedQueues, setDepOwnedQueues] = useState([]);
  let [selectedDepId, setSelectedDepId] = useState(0);

  useEffect(() => {
    getDepartmants().then(result => {
      setDeps(JSON.parse(result.data.data));
    });
  }, [reloadValues]);
  useEffect(() => {
    getQueues().then(result => {
      setQueues(JSON.parse(result.data.data));
    });
  }, []);
  useEffect(() => {
    getDepOwnedQueues(selectedDepId).then(result => {
      let tempArr = [];
      let resArr = JSON.parse(result.data.data);
      for (let i = 0; i < resArr.length; i++) {
        tempArr.push(resArr[i].queue_id);
      }
      setDepOwnedQueues(tempArr);
    });
  }, [selectedDepId]);

  const handleCheckboxClick = (queueId, event) => {
    if (event.target.checked) {
      addDepOwnedQueues(selectedDepId, queueId).then(res => {
        getDepOwnedQueues(selectedDepId).then(result => {
          let tempArr = [];
          let resArr = JSON.parse(result.data.data);
          for (let i = 0; i < resArr.length; i++) {
            tempArr.push(resArr[i].queue_id);
          }
          setDepOwnedQueues(tempArr);
        });
      });
    } else {
      deleteDepOwnedQueues(selectedDepId, queueId).then(res => {
        getDepOwnedQueues(selectedDepId).then(result => {
          let tempArr = [];
          let resArr = JSON.parse(result.data.data);
          for (let i = 0; i < resArr.length; i++) {
            tempArr.push(resArr[i].queue_id);
          }
          setDepOwnedQueues(tempArr);
        });
      });
    }
  };

  return (
    <div style={{ marginTop: '20px' }}>
      <GridContainer>
        <Grid item md={6}>
          <Paper>
            <TableContainer sx={{ maxHeight: '500px' }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      <IntlMessages id={'departments'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {deps.map(dep => (
                    <TableRow key={dep.id}>
                      <TableCell
                        style={{ backgroundColor: selectedDepId === dep.id ? '#90E0EF' : 'white' }}
                        onClick={() => setSelectedDepId(dep.id)}>
                        {dep.name}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>

          <TagTable />
        </Grid>
        <Grid item md={6}>
          <Paper>
            <TableContainer sx={{ maxHeight: '500px' }}>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      <IntlMessages id={'queue.answer.report.queues'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {queues.map(queue => (
                    <TableRow key={queue.id}>
                      <TableCell>
                        <Checkbox
                          checked={depOwnedQueues.includes(queue.id)}
                          onChange={event => handleCheckboxClick(queue.id, event)}
                        />

                        {queue.display_name}
                        <b> ({queue.dn})</b>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </GridContainer>
    </div>
  );
}
