import React from 'react';
import { Area, AreaChart, ResponsiveContainer, Tooltip } from 'recharts';

import useStyles from './InboundStyle';

const InboundGraph = props => {
  const classes = useStyles();

  return (
    <ResponsiveContainer className={classes.root} width="100%" height={50}>
      <AreaChart data={props.data} margin={{ top: 0, right: 0, left: 0, bottom: 0 }}>
        <Tooltip
          labelStyle={{ color: 'black' }}
          cursor={false}
          content={data => {
            return data.payload[0] ? (
              <div className={classes.tooltip}>
                {data.payload[0].payload.start_time} ({data.payload[0].payload.count})
              </div>
            ) : null;
          }}
        />
        <defs>
          <linearGradient id="color3" x1="0" y1="0" x2="0" y2="1">
            <stop offset="5%" stopColor="#6166B3" stopOpacity={1} />
            <stop offset="95%" stopColor="#544179" stopOpacity={1} />
          </linearGradient>
        </defs>
        <Area
          dataKey="count"
          strokeWidth={2}
          stackId="2"
          stroke="#6200EE"
          fill="url(#color3)"
          fillOpacity={1}
          type={'monotone'}
        />
      </AreaChart>
    </ResponsiveContainer>
  );
};

export default InboundGraph;
