import React, { useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import { Box, InputLabel, MenuItem, FormControl, Select } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import { DateTimePicker } from '@material-ui/pickers';
import Typography from '@material-ui/core/Typography';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { getAgentNumberV16 } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function Filter({ selectedStartTime, selectedEndTime, setSelectedEndTime, setSelectedStartTime, setParams }) {
  const [agent, setAgent] = React.useState('All');
  const [menuItems, setMenuItems] = React.useState([]);
  const handleChange = event => {
    setAgent(event.target.value);
  };

  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':00';
  };
  useEffect(() => {
    getAgentNumberV16().then(response => setMenuItems(response));
  }, []);

  const classes = useStyles();
  const handleClick = () => {
    setParams({
      startdate: formatDate(selectedStartTime),
      enddate: formatDate(selectedEndTime),
      agent: agent,
    });
  };

  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
          <IntlMessages id={'filter'} />
        </Typography>
        <GridContainer>
          <Grid item md={4}>
            <DateTimePicker
              ampm={false}
              disableFuture
              inputVariant="outlined"
              format="DD-MM-YYYY HH:mm"
              value={selectedStartTime}
              onChange={setSelectedStartTime}
              label={<IntlMessages id={'filter.menu.startdate'} />}
            />
          </Grid>
          <Grid item md={4}>
            <DateTimePicker
              ampm={false}
              disableFuture
              inputVariant="outlined"
              format="DD-MM-YYYY HH:mm"
              value={selectedEndTime}
              onChange={setSelectedEndTime}
              label={<IntlMessages id={'filter.menu.enddate'} />}
            />
          </Grid>
          <Grid item md={4}>
            <Box sx={{ minWidth: 120 }}>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Agent</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={agent}
                  label="Agent"
                  onChange={handleChange}>
                  <MenuItem value="All">
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {menuItems.map((data, index) => (
                    <MenuItem value={data.dn} key={index}>
                      {data.dn} {data.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
          </Grid>
        </GridContainer>
      </CardContent>
      <CardActions>
        <Button
          variant="contained"
          onClick={() => {
            handleClick();
          }}
          size="small">
          <IntlMessages id={'filter.menu.search'} />
        </Button>
      </CardActions>
    </Card>
  );
}
