import React, { createRef } from 'react';
import CmtCard from '@coremat/CmtCard';
import CmtCardHeader from '@coremat/CmtCard/CmtCardHeader';
import CmtCardContent from '@coremat/CmtCard/CmtCardContent';
import Chart from './graph';

import makeStyles from '@material-ui/core/styles/makeStyles';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { useScreenshot, createFileName } from 'use-react-screenshot';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';

const Graph = ({ response }) => {
  const useStyles = makeStyles({
    cardRoot: {
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      position: 'relative',
      '& .Cmt-card-content': {
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        position: 'relative',
        zIndex: 3,
      },
    },
  });
  const classes = useStyles();

  const ref = createRef(null);
  const [, takeScreenShot] = useScreenshot({
    type: 'image/jpeg',
    quality: 1.0,
  });

  const download = (image, { name = 'img', extension = 'jpg' } = {}) => {
    const a = document.createElement('a');
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const downloadScreenshot = () => takeScreenShot(ref.current).then(download);

  return (
    <>
      <div style={{ textAlign: 'right' }}>
        <Button style={{ backgroundColor: '#3f51b5', color: 'white', marginBottom: '10px' }} onClick={downloadScreenshot}>
          <IntlMessages id={'dowlandimage'} /> <CloudDownload style={{ marginLeft: '5px' }} />
        </Button>
      </div>
      <div ref={ref}>
        <CmtCard className={classes.cardRoot}>
          <CmtCardHeader className="pt-5" title={<IntlMessages id={'monthly.log'} />}></CmtCardHeader>
          <CmtCardContent>
            <Chart pieData={response} />
          </CmtCardContent>
        </CmtCard>
      </div>
    </>
  );
};

export default Graph;
