import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, TextField } from '@mui/material';
import { setBreakTime } from 'api/api';
import React, { useState } from 'react';

export default function BreaksChangeBreakTimeModal({ logouttype, period, handleBreakTimeModalClose }) {
  let [newTime, setNewTime] = useState(period);
  const handleSubmit = () => {
    setBreakTime(newTime, logouttype).then(resp => {
      handleBreakTimeModalClose();
    });
  };
  return (
    <div style={{ textAlign: 'center' }}>
      <TextField
        label={<IntlMessages id={'edit.breaks.breaktime'} />}
        variant="outlined"
        value={newTime}
        onChange={event => setNewTime(event.target.value)}
      />
      <div style={{ marginTop: '10px' }}>
        <Button variant="contained" onClick={() => handleSubmit()}>
          <IntlMessages id={'edit.breaks.change'} />
        </Button>
        <Button variant="contained" style={{ marginLeft: '10px' }} onClick={() => handleBreakTimeModalClose()}>
          <IntlMessages id={'close'} />
        </Button>
      </div>
    </div>
  );
}
