import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Accordion, AccordionDetails, AccordionSummary, Grid, TextField, Typography } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import { InputLabel, MenuItem, Select, FormControl } from '@mui/material';
import React, { useState, useEffect } from 'react';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import StarRoundedIcon from '@mui/icons-material/StarRounded';
import LocalOfferRoundedIcon from '@mui/icons-material/LocalOfferRounded';
import { getTagTypes, getQueueNumber } from 'api/api';
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import Checkbox from '@mui/material/Checkbox';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';

export default function CallLogtherFilters({
  setDirectionApi,
  setSourceFilterText,
  setDestinationFilterText,
  setQueueDn,
  setAnswerStatus,
  setScore,
  setTagFilter,
}) {
  useEffect(() => {
    getTagTypes().then(resp => setTagsList(JSON.parse(resp.data.data)));
    getQueueNumber().then(que => setQueues(JSON.parse(que.data.data)));
  }, []);
  let [queues, setQueues] = useState([]);
  let [tagsList, setTagsList] = useState([]);
  let [filtersOpen, setFiltersOpen] = useState(true);

  let [directionSelect, setDirectionSelect] = useState('');
  const handleDirectionSelect = event => {
    setDirectionSelect(event.target.value);
    setDirectionApi(event.target.value);
  };
  let [sourceFilterSelect, setSourceFilterSelect] = useState('');
  let [sourceFilterTextValue, setSourceFilterTextValue] = useState('');
  const handleChangeSourceFilterSelect = event => {
    setSourceFilterSelect(event.target.value);
    setSourceFilterText(event.target.value);
    setSourceFilterTextValue('');
  };
  const handleChangeSourceFilterText = event => {
    setSourceFilterText(sourceFilterSelect + '====' + event.target.value);
    setSourceFilterTextValue(event.target.value);
  };
  let [targetFilterSelect, setTargetFilterSelect] = useState('');
  let [targetFilterTextValue, setTargetFilterTextValue] = useState('');
  const handleChangeTargetFilterSelect = event => {
    setTargetFilterSelect(event.target.value);
    setTargetFilterTextValue('');
    setDestinationFilterText(event.target.value);
  };
  const handleChangeDestinationFilterText = event => {
    setDestinationFilterText(targetFilterSelect + '====' + event.target.value);
    setTargetFilterTextValue(event.target.value);
  };

  let [queueSelect, setQueueSelect] = useState('');
  const handleChangeQueueSelect = event => {
    if (event.target.value === 'all') {
      setQueueSelect('');
      setQueueDn('');
    } else {
      setQueueSelect(event.target.value);
      setQueueDn(event.target.value);
    }
  };
  let [stateSelect, setStateSelect] = useState('');
  const handleChangeStateSelect = event => {
    setStateSelect(event.target.value);
    setAnswerStatus(event.target.value);
  };

  let [advancedFilter, setAdvancedFilter] = useState([]);
  const handleChangeAdvancedFilterSelect = e => {
    const {
      target: { value },
    } = e;
    setAdvancedFilter(typeof value === 'string' ? value.split(',') : value);
  };

  let [scoreSelect, setScoreSelect] = useState('');
  const handleScoreSelect = e => {
    setScoreSelect(e.target.value);
    setScore(e.target.value);
  };
  let [tagSelect, setTagSelect] = useState('');
  const handleTagSelect = e => {
    setTagSelect(e.target.value);
    setTagFilter(e.target.value);
  };
  useEffect(() => {
    if (!advancedFilter.includes('Score')) {
      setScoreSelect('');
      setScore('');
    }
    if (!advancedFilter.includes('Tag')) {
      setTagSelect('');
      setTagFilter('');
    }
    if (!advancedFilter.includes('Queue')) {
      setQueueDn('');
      setQueueSelect('');
    }
  }, [advancedFilter, setScore, setTagFilter, setQueueDn]);
  return (
    <div style={{ marginTop: '20px' }}>
      <Accordion expanded={filtersOpen}>
        <AccordionSummary expandIcon={<ExpandMore />} onClick={() => setFiltersOpen(!filtersOpen)}>
          <Typography>
            <IntlMessages id={'filters'} />
          </Typography>
        </AccordionSummary>
        <AccordionDetails style={{ paddingRight: '20px', paddingLeft: '20px' }}>
          <GridContainer>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <CompareArrowsIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'directionfilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'direction'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={directionSelect}
                  label={<IntlMessages id={'direction'} />}
                  onChange={handleDirectionSelect}>
                  <MenuItem value="all">
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value="inb">
                    <IntlMessages id={'incomingcall'} />
                  </MenuItem>
                  <MenuItem value="out">
                    <IntlMessages id={'outgoingcall'} />
                  </MenuItem>
                  <MenuItem value="agent">
                    <IntlMessages id={'agentedcall'} />
                  </MenuItem>
                  <MenuItem value="ques">
                    <IntlMessages id={'queue'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={3}>
              <div style={{ color: '#064635' }}>
                <FilterListIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.sorcefilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.sorcefilter'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={sourceFilterSelect}
                  label={<IntlMessages id={'CallLog.filter.sorcefilter'} />}
                  onChange={handleChangeSourceFilterSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'dnOrDns'}>
                    <IntlMessages id={'CallLog.filter.internalorinternals'} />
                  </MenuItem>
                  <MenuItem value={'numberfilter'}>
                    <IntlMessages id={'CallLog.filter.numberfilter'} />
                  </MenuItem>
                  <MenuItem value={'didno'}>
                    <IntlMessages id={'mountlydetails.didnumber'} />
                  </MenuItem>
                </Select>
              </FormControl>
              {(sourceFilterSelect === 'dnOrDns' ||
                sourceFilterSelect === 'numberfilter' ||
                sourceFilterSelect === 'didno') && (
                <div style={{ marginTop: '5px' }}>
                  <TextField
                    style={{ width: '100%' }}
                    label={
                      sourceFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.internalorinternals'} />
                      ) : sourceFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.numberfilter'} />
                      ) : sourceFilterSelect === 'didno' ? (
                        <IntlMessages id={'mountlydetails.didnumber'} />
                      ) : (
                        ''
                      )
                    }
                    helperText={
                      sourceFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.ep.did'} />
                      ) : sourceFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.ep.number'} />
                      ) : (
                        ''
                      )
                    }
                    variant="outlined"
                    onChange={handleChangeSourceFilterText}
                    value={sourceFilterTextValue}
                  />
                </div>
              )}
            </Grid>
            <Grid item md={3}>
              <div style={{ color: '#064635' }}>
                <FilterListIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.destinationfilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.destinationfilter'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={targetFilterSelect}
                  label={<IntlMessages id={'CallLog.filter.destinationfilter'} />}
                  onChange={handleChangeTargetFilterSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'dnOrDns'}>
                    <IntlMessages id={'CallLog.filter.internalorinternals'} />
                  </MenuItem>
                  <MenuItem value={'numberfilter'}>
                    <IntlMessages id={'CallLog.filter.numberfilter'} />
                  </MenuItem>
                  <MenuItem value={'didno'}>
                    <IntlMessages id={'mountlydetails.didnumber'} />
                  </MenuItem>
                </Select>
              </FormControl>
              {(targetFilterSelect === 'dnOrDns' ||
                targetFilterSelect === 'numberfilter' ||
                targetFilterSelect === 'didno') && (
                <div style={{ marginTop: '5px' }}>
                  <TextField
                    style={{ width: '100%' }}
                    label={
                      targetFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.internalorinternals'} />
                      ) : targetFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.numberfilter'} />
                      ) : targetFilterSelect === 'didno' ? (
                        <IntlMessages id={'mountlydetails.didnumber'} />
                      ) : (
                        ''
                      )
                    }
                    helperText={
                      targetFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.ep.did'} />
                      ) : targetFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.ep.number'} />
                      ) : (
                        ''
                      )
                    }
                    variant="outlined"
                    onChange={handleChangeDestinationFilterText}
                    value={targetFilterTextValue}
                  />
                </div>
              )}
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <QuestionAnswerIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'stateFilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'stateFilter'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={stateSelect}
                  label={<IntlMessages id={'stateFilter'} />}
                  onChange={handleChangeStateSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'answered'}>
                    <IntlMessages id={'CallLog.filter.answered'} />
                  </MenuItem>
                  <MenuItem value={'nonanswered'}>
                    <IntlMessages id={'CallLog.filter.unanswered'} />
                  </MenuItem>
                  <MenuItem value={'missed'}>
                    <IntlMessages id={'missedcall'} />
                  </MenuItem>
                  <MenuItem value={'transfered'}>
                    <IntlMessages id={'dailysummary.transfer'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <FilterAltIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.filter3'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.filter3'} />
                </InputLabel>
                <Select
                  multiple
                  labelId="source-l"
                  id="demo-simple-select"
                  value={advancedFilter}
                  label={<IntlMessages id={'CallLog.filter.filter3'} />}
                  onChange={handleChangeAdvancedFilterSelect}
                  renderValue={() => ""}>
                  <MenuItem value={'Score'}>
                    <Checkbox checked={advancedFilter.indexOf('Score') > -1} />
                    <StarRoundedIcon style={{ marginRight: '2px' }} fontSize="medium" />
                    <IntlMessages id={'score'} />
                  </MenuItem>
                  <MenuItem value={'Tag'}>
                    <Checkbox checked={advancedFilter.indexOf('Tag') > -1} />
                    <LocalOfferRoundedIcon style={{ marginRight: '2px' }} fontSize="medium" />
                    <IntlMessages id={'tag'} />
                  </MenuItem>
                  <MenuItem value={'Queue'}>
                    <Checkbox checked={advancedFilter.indexOf('Queue') > -1} />
                    <ContactPhoneIcon style={{ marginRight: '2px' }} fontSize="medium" />
                    <IntlMessages id={'queueFilter'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            {advancedFilter.includes('Score') && (
              <Grid item md={3}>
                <div style={{ color: '#064635' }}>
                  <StarRoundedIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                  <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                    <IntlMessages id={'score'} />
                  </b>
                </div>
                <hr style={{ opacity: '.5', marginTop: '8px' }} />
                <FormControl fullWidth style={{ marginTop: '8px' }}>
                  <InputLabel id="score">
                    <IntlMessages id={'score'} />
                  </InputLabel>
                  <Select
                    labelId="score"
                    label={<IntlMessages id={'score'} />}
                    value={scoreSelect}
                    onChange={handleScoreSelect}>
                    <MenuItem value="">
                      <IntlMessages id={'CallLog.filter.all'} />
                    </MenuItem>
                    <MenuItem value="5">5</MenuItem>
                    <MenuItem value="4">4</MenuItem>
                    <MenuItem value="3">3</MenuItem>
                    <MenuItem value="2">2</MenuItem>
                    <MenuItem value="1">1</MenuItem>
                    <MenuItem value="0">0</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            )}
            {advancedFilter.includes('Tag') && (
              <Grid item md={3}>
                <div style={{ color: '#064635' }}>
                  <LocalOfferRoundedIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                  <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                    <IntlMessages id={'tag'} />
                  </b>
                </div>
                <hr style={{ opacity: '.5', marginTop: '8px' }} />
                <FormControl fullWidth style={{ marginTop: '8px' }}>
                  <InputLabel id="tag">
                    <IntlMessages id={'tag'} />
                  </InputLabel>
                  <Select labelId="tag" label={<IntlMessages id={'tag'} />} value={tagSelect} onChange={handleTagSelect}>
                    <MenuItem value="">
                      <IntlMessages id={'CallLog.filter.all'} />
                    </MenuItem>
                    {tagsList.map((item, index) => (
                      <MenuItem key={index} value={item.tagmassage}>
                        {item.tagmassage}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            )}
            {advancedFilter.includes('Queue') && (
              <Grid item md={2}>
                <div style={{ color: '#064635' }}>
                  <ContactPhoneIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                  <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                    <IntlMessages id={'queueFilter'} />
                  </b>
                </div>
                <hr style={{ opacity: '.5', marginTop: '8px' }} />
                <FormControl fullWidth style={{ marginTop: '8px' }}>
                  <InputLabel id="queue-label">
                    <IntlMessages id={'queueFilter'} />
                  </InputLabel>
                  <Select
                    labelId="queue-label"
                    value={queueSelect}
                    label={<IntlMessages id={'queueFilter'} />}
                    onChange={handleChangeQueueSelect}>
                    <MenuItem value="all">
                      <IntlMessages id={'CallLog.filter.all'} />
                    </MenuItem>
                    {queues.map(que => (
                      <MenuItem key={que.id} value={que.dn}>
                        {que.dn} - {que.display_name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            )}
          </GridContainer>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
