import React, { useEffect, useRef, useState, useContext } from 'react';
import Stack from '@mui/material/Stack';
import Slider from '@mui/material/Slider';
import VolumeDown from '@mui/icons-material/VolumeDown';
import VolumeUp from '@mui/icons-material/VolumeUp';
import WaveSurfer from 'wavesurfer.js';
import PauseRounded from '@mui/icons-material/PauseRounded';
import PlayArrowRounded from '@mui/icons-material/PlayArrowRounded';
import IconButton from '@mui/material/IconButton';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import { WaveFile } from 'wavefile';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';

const formWaveSurferOptions = ref => ({
  container: ref,
  waveColor: '#9cceff',
  progressColor: '#1976d2',
  cursorColor: '#1976d2',
  barWidth: 2,
  barRadius: 3,
  barHeight: 1,
  barGap: 2,
  responsive: true,
});

export default function AudioWaveForm({ buffer }) {
  const waveformRef = useRef(null);
  const wavesurfer = useRef(null);
  const [playing, setPlay] = useState(false);
  const [volume, setVolume] = useState(0.5);
  const [loading, setLoading] = useState(true);

  const [duration, setDuration] = useState('0:00');
  const [currentTime, setCurrentTime] = useState('0:00');

  const { setRecordLoading } = useContext(AppContext);
  useEffect(() => {
    setPlay(false);

    const options = formWaveSurferOptions(waveformRef.current);
    wavesurfer.current = WaveSurfer.create(options);

    if (window.appsettings.appUrl === '78.47.177.25' || window.appsettings.appUrl === '185.141.110.253') {
      wavesurfer.current.loadBlob(new Blob([buffer], { type: 'audio/wav' }));
    } else {
      let wav = new WaveFile(new Uint8Array(buffer));
      wav.fromIMAADPCM();
      let newBuf = wav.toBuffer();
      wavesurfer.current.loadBlob(new Blob([newBuf], { type: 'audio/wav' }));
    }
    wavesurfer.current.on('ready', function() {
      setRecordLoading(false);
      setLoading(false);
      document.body.style.cursor = 'default';
      if (wavesurfer.current) {
        wavesurfer.current.setVolume(0.5);
        setDuration(formatDuration(Math.round(wavesurfer.current.getDuration())));
        setCurrentTime(formatDuration(Math.round(wavesurfer.current.getCurrentTime())));
      }
    });
    wavesurfer.current.on('audioprocess', function() {
      if (wavesurfer.current.isPlaying()) {
        setDuration(formatDuration(Math.round(wavesurfer.current.getDuration())));
        setCurrentTime(formatDuration(Math.round(wavesurfer.current.getCurrentTime())));
      }
    });
    return () => wavesurfer.current.destroy();
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [buffer]);

  const handlePlayPause = () => {
    setPlay(!playing);
    wavesurfer.current.playPause();
  };

  const onVolumeChange = e => {
    const { target } = e;
    const newVolume = +target.value;

    if (newVolume) {
      setVolume(newVolume);
      wavesurfer.current.setVolume(newVolume || 1);
    }
  };
  function formatDuration(value) {
    const minute = Math.floor(value / 60);
    const secondLeft = value - minute * 60;
    return `${minute}:${secondLeft <= 9 ? `0${secondLeft}` : secondLeft}`;
  }
  const TinyText = styled(Typography)({
    fontSize: '1rem',
    opacity: 0.6,
    fontWeight: 500,
    letterSpacing: 0.2,
  });
  return (
    <>
      {loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress />
        </Box>
      )}
      <div id="waveform" ref={waveformRef} />
      <Stack spacing={2} direction="column" alignItems="center" justifyContent="center">
        <TinyText>
          {currentTime}/{duration}
        </TinyText>
        <IconButton aria-label={!playing ? 'play' : 'pause'} onClick={handlePlayPause} disabled={loading}>
          {!playing ? (
            <PlayArrowRounded sx={{ fontSize: '3rem' }} htmlColor="#000" />
          ) : (
            <PauseRounded sx={{ fontSize: '3rem' }} htmlColor="#000" />
          )}
        </IconButton>
        <Stack width={300} spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
          <VolumeDown />
          <Slider
            disabled={loading}
            id="volume"
            name="volume"
            aria-label="Volume"
            value={volume}
            onChange={onVolumeChange}
            min={0.01}
            max={1}
            step={0.025}
          />
          <VolumeUp />
        </Stack>
      </Stack>
    </>
  );
}
