import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React from 'react';
import AgentCdrRportFilter from './Filter';
import AgentCdrReportTable from './Table';

export default function AgentCdrReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'Agent CDR Report'} />, isActive: true },
  ];
  return (
    <div>
      <PageContainer heading={<IntlMessages id={'Agent CDR Report'} />} breadcrumbs={breadcrumbs}>
        <AgentCdrRportFilter />
        <AgentCdrReportTable />
      </PageContainer>
    </div>
  );
}
