import React, { useState, useEffect } from 'react';
import { Card, CardContent, CircularProgress, FormControl, Grid, InputLabel, MenuItem, Select } from '@material-ui/core';
import AbondenedCallsDateFilter from './DateFilter';
import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getQueue, getAgentNumber } from 'api/api';

function Filter({ setColor, color, queue, setQueue, agent, setAgent, setStartDate, setEndDate }) {
  let [loading] = useState(false);

  const colors = [
    {
      name: 'Green',
      value: <IntlMessages id={'returned'} />,
    },
    {
      name: 'Red',
      value: <IntlMessages id={'notreturned'} />,
    },
  ];

  const handleChangeColor = event => {
    setColor(event.target.value);
  };

  const handleChangeQueue = event => {
    setQueue(event.target.value);
  };
  const handleChangeAgent = event => {
    setAgent(event.target.value);
  };
  const [queues, setQueues] = useState([]);

  const [agents, setAgents] = useState([]);
  useEffect(() => {
    getQueue().then(resp => setQueues(JSON.parse(resp.data.data)));
    getAgentNumber().then(resp => setAgents(JSON.parse(resp.data.data)));
  }, []);

  return (
    <>
      <div>
        <AbondenedCallsDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
        {loading && (
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <CircularProgress />
          </div>
        )}
      </div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'returnfilter'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={color}
                  onChange={handleChangeColor}
                  label={<IntlMessages id={'returnfilter'} />}>
                  <MenuItem value={''}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {colors.map((color, index) => (
                    <MenuItem key={index} value={color.name}>
                      {color.value}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'queue'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={queue}
                  onChange={handleChangeQueue}
                  label={<IntlMessages id={'queue'} />}>
                  <MenuItem value={''}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {queues.map((queue, index) => (
                    <MenuItem key={index} value={queue.dn}>
                      {queue.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'internals.summary.agent'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={agent}
                  onChange={handleChangeAgent}
                  label={<IntlMessages id={'internals.summary.agent'} />}>
                  <MenuItem value={''}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {agents.map((data, index) => (
                    <MenuItem key={index} value={data.dn}>
                      {data.dn} - {data.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
    </>
  );
}

export default Filter;
