import GridContainer from '@jumbo/components/GridContainer';
import { Button, Card, CardActions, CardContent, Grid } from '@material-ui/core';
import { DateTimePicker } from '@material-ui/pickers';
import React from 'react';

export default function AgentCdrRportFilter() {
  return (
    <div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={3}>
              <DateTimePicker
                style={{ width: '100%' }}
                ampm={false}
                disableFuture
                inputVariant="outlined"
                format="DD-MM-YYYY HH:mm"
                label="Start Time"
              />
            </Grid>
            <Grid item md={3}>
              <DateTimePicker
                style={{ width: '100%' }}
                ampm={false}
                disableFuture
                inputVariant="outlined"
                format="DD-MM-YYYY HH:mm"
                label="End Time"
              />
            </Grid>
          </GridContainer>
        </CardContent>

        <CardActions>
          <Button>Search</Button>
        </CardActions>
      </Card>
    </div>
  );
}
