import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Accordion, AccordionDetails, AccordionSummary, Grid, Typography } from '@material-ui/core';
import { DateRange, ExpandMore } from '@material-ui/icons';
import React, { useEffect, useState } from 'react';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import trLocale from 'date-fns/locale/tr';
import enLocale from 'date-fns/locale/en-US';
import DatePicker from '@mui/lab/DatePicker';
import TextField from '@mui/material/TextField';
import { useIntl } from 'react-intl';
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material';
import dateFormat from 'dateformat';
import deLocale from 'date-fns/locale/de';
import nlLocale from 'date-fns/locale/nl';

export default function QualityReportDateFilter({ setStartTimeApi, setEndTimeApi }) {
  let [dateFilterOpen, setDateFilterOpen] = useState(true);
  let [timeFilterValue, setTimeFilterValue] = useState('today');
  let [startDate, setStartDate] = useState(new Date().setHours(0, 0, 0));
  let [endDate, setEndDate] = useState(new Date().setHours(23, 59, 59));

  const handleChangeTimeFilterValue = event => {
    setTimeFilterValue(event.target.value);
  };

  useEffect(() => {
    setStartTimeApi(dateFormat(startDate, 'yyyy-mm-dd HH:MM:ss'));
    setEndTimeApi(dateFormat(endDate, 'yyyy-mm-dd HH:MM:ss'));
  }, [startDate, endDate, setStartTimeApi, setEndTimeApi]);
  useEffect(() => {
    let start = new Date();
    let end = new Date();
    switch (timeFilterValue) {
      case 'yesterday':
        start.setDate(start.getDate() - 1);
        start.setHours(0, 0, 0);
        end.setDate(end.getDate() - 1);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'lastweek':
        start.setDate(start.getDate() - 7);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'lastmount':
        start.setDate(start.getDate() - 30);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'last3mount':
        start.setDate(start.getDate() - 90);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'last6mouth':
        start.setDate(start.getDate() - 180);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'today':
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'thisweek':
        start.setDate(start.getDate() - start.getDay() + 1);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'thismounth':
        start.setDate(1);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'thisyear':
        start.setDate(1);
        start.setMonth(0);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'all':
        start.setDate(1);
        start.setMonth(0);
        start.setFullYear(2000);
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;
      case 'special':
        start.setHours(0, 0, 0);
        end.setHours(23, 59, 59);
        setStartDate(start);
        setEndDate(end);
        break;

      default:
        break;
    }
  }, [timeFilterValue]);

  let curentLanguage = useIntl();

  return (
    <div>
      <Accordion expanded={dateFilterOpen}>
        <AccordionSummary expandIcon={<ExpandMore />} onClick={() => setDateFilterOpen(!dateFilterOpen)}>
          <Typography style={{ marginLeft: '10px' }}>
            <IntlMessages id={'datefilter'} />
          </Typography>
        </AccordionSummary>
        <AccordionDetails style={{ paddingLeft: '20px', paddingRight: '20px' }}>
          <GridContainer>
            <Grid item md={3}>
              <div style={{ color: '#064635' }}>
                <DateRange style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'dateoption'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px', marginBottom: '8px' }} />
              <FormControl fullWidth>
                <InputLabel id="tarihhhhh">
                  <IntlMessages id={'datefilter'} />
                </InputLabel>
                <Select
                  labelId="tarihhhhh"
                  id="demo-simple-select"
                  value={timeFilterValue}
                  label="Tarih Filtresi"
                  onChange={handleChangeTimeFilterValue}>
                  <MenuItem value={'yesterday'}>
                    <IntlMessages id={'yesterday'} />
                  </MenuItem>
                  <MenuItem value={'lastweek'}>
                    <IntlMessages id={'last7days'} />
                  </MenuItem>
                  <MenuItem value={'lastmount'}>
                    <IntlMessages id={'last30days'} />
                  </MenuItem>
                  <MenuItem value={'last3mount'}>
                    <IntlMessages id={'last90days'} />
                  </MenuItem>
                  <MenuItem value={'last6mouth'}>
                    <IntlMessages id={'last180days'} />
                  </MenuItem>
                  <MenuItem value={'today'}>
                    <IntlMessages id={'fronthebeginninfofthisday'} />
                  </MenuItem>
                  <MenuItem value={'thisweek'}>
                    <IntlMessages id={'fronthebeginninfofthisweek'} />
                  </MenuItem>
                  <MenuItem value={'thismounth'}>
                    <IntlMessages id={'fronthebeginninfofthismounth'} />
                  </MenuItem>
                  <MenuItem value={'thisyear'}>
                    <IntlMessages id={'fronthebeginninfofthisyear'} />
                  </MenuItem>
                  <MenuItem value={'special'}>
                    <IntlMessages id={'custom'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>

            {timeFilterValue === 'special' && (
              <Grid item md={5}>
                <div style={{ color: '#064635' }}>
                  <DateRange style={{ verticalAlign: '-5px' }} fontSize="medium" />
                  <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                    <IntlMessages id={'customselect'} />
                  </b>
                </div>
                <hr style={{ opacity: '.5', marginTop: '8px' }} />
                <div style={{ marginTop: '10px' }}>
                  <GridContainer>
                    <Grid item md={6}>
                      <LocalizationProvider
                        dateAdapter={AdapterDateFns}
                        locale={
                          curentLanguage.locale === 'en-EN'
                            ? enLocale
                            : curentLanguage.locale === 'nl-NL'
                            ? nlLocale
                            : curentLanguage.locale === 'tr-TR'
                            ? trLocale
                            : curentLanguage.locale === 'de-DE'
                            ? deLocale
                            : enLocale
                        }>
                        {' '}
                        <DatePicker
                          value={startDate}
                          onChange={newValue => setStartDate(newValue)}
                          renderInput={params => <TextField {...params} />}
                          format="YYYY-MM-DD HH:mm"
                          inputVariant="outlined"
                          maxDate={endDate}
                          label={<IntlMessages id={'filter.menu.startdate'} />}
                        />
                      </LocalizationProvider>
                    </Grid>
                    <Grid item md={6}>
                      <LocalizationProvider
                        dateAdapter={AdapterDateFns}
                        locale={
                          curentLanguage.locale === 'en-EN'
                            ? enLocale
                            : curentLanguage.locale === 'nl-NL'
                            ? nlLocale
                            : curentLanguage.locale === 'tr-TR'
                            ? trLocale
                            : curentLanguage.locale === 'de-DE'
                            ? deLocale
                            : enLocale
                        }>
                        {' '}
                        <DatePicker
                          value={endDate}
                          onChange={newValue => setEndDate(newValue)}
                          renderInput={params => <TextField {...params} />}
                          format="YYYY-MM-DD HH:mm"
                          inputVariant="outlined"
                          minDate={startDate}
                          maxDate={new Date()}
                          label={<IntlMessages id={'filter.menu.enddate'} />}
                        />
                      </LocalizationProvider>
                    </Grid>
                  </GridContainer>
                </div>
              </Grid>
            )}
          </GridContainer>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
