import React from 'react';
import { Redirect, Route, Switch } from 'react-router';
import { useLocation } from 'react-router-dom';
import Dashboard from './Pages/Dashboard';
import Error404 from './Pages/404';
import Login from './Auth/Login';
import AgentStatus from './Pages/AgentStatus';
import Register from './Auth/Register';
import CallListening from './Pages/CallListeningReport';
import CallManagement from './Pages/CallManagement';
import DroppedCall from './Pages/DroppedCall';
import DidReport from './Pages/DIDReport';
import ForgotPasswordPage from './Auth/ForgotPassword';
import { PrivateRouteLogin } from './Private/PrivateRouteLogin';
import { NotPrivateRoute } from './Private/NotPrivateRoute';
import ServiceLevel from './Pages/ServiceLevel';
import { AbandonedQueues } from './Pages/AbandonedQueues';
import RepetetiveCalls from './Pages/RepetetiveCalls';
import WallBoard from './Pages/WallBoard';
import DailyLog from './Pages/DailyLog';
import DailyAcc from './Pages/DailyAcc';
import CallComparison from './Pages/CallComparison';
import MonthlyLog from './Pages/MonthlyLog';
// import ReturnedDropCall from './Pages/ReturnedDropCall';
import AgentCDR from './Pages/AgentSessionReport';
import IYS from './Pages/IYS';
import Users from './Pages/MicrosoftTeams/Users';
import MonthlyDetails from './Pages/MonthlyDetails';
import QueueAnswerReport from './Pages/QueueAnswerReport';
import AbondenedCalls from './Pages/AbondenedCalls';
import CallTerminationReport from './Pages/CallTerminationReport';
import AgentCdrReport from './Pages/AgentCdrReport';
import DailySumaryReport from './Pages/DailySummaryReport';
import InternalsSummaryReport from './Pages/InternalsSummaryReport';
import CallLog from './Pages/CallLog';
import QualityReport from './Pages/QualityReport';
import './index.css';
import CallTransferReport from './Pages/CallTransferReport';
import EditBreaks from './Pages/EditBreaks';
import UserSettings from './Pages/UserSettings/UserSettings';
import DepartmansSettigns from './Pages/DepartmansSettingns/DepartmansSettigns';
import Account from './Pages/Account';


const Routes = () => {
  const location = useLocation();

  if (location.pathname === '' || location.pathname === '/') {
    return <Redirect to={'/dashboard'} />;
  }

  return (
    <React.Fragment>
      <Switch>
        <NotPrivateRoute path="/dashboard" component={Dashboard} />
        <NotPrivateRoute path="/reports/agentcdr" component={AgentCDR} />
        {/* <NotPrivateRoute path="/reports/returned-dropcall" component={ReturnedDropCall} /> */}
        <PrivateRouteLogin path="/signin" component={Login} />
        <NotPrivateRoute path="/wallboard" component={WallBoard} />
        <NotPrivateRoute path="/reports/calltransferreport" component={CallTransferReport} />
        <NotPrivateRoute path="/reports/callLog" component={CallLog} />
        <NotPrivateRoute path="/reports/calllistening" component={CallListening}/>
        <NotPrivateRoute path="/reports/callmanagement" component={CallManagement}/>
        <NotPrivateRoute path="/reports/servicelevel" component={ServiceLevel} />
        <NotPrivateRoute path="/reports/agentstatus" component={AgentStatus} />
        <NotPrivateRoute path="/reports/callcomparison" component={CallComparison} />
        <NotPrivateRoute path="/signup" component={Register} />
        <NotPrivateRoute path="/reports/repetetive-calls" component={RepetetiveCalls} />
        <NotPrivateRoute path="/forgot-password" component={ForgotPasswordPage} />
        <NotPrivateRoute path="/reports/abandoned-queues" component={AbandonedQueues} />
        <NotPrivateRoute path="/reports/dailylog" component={DailyLog} />
        <NotPrivateRoute path="/reports/dailyacc" component={DailyAcc} />
        <NotPrivateRoute path="/reports/didreport" component={DidReport} />
        <NotPrivateRoute path="/reports/monthlylog" component={MonthlyLog} />
        <NotPrivateRoute path="/reports/droppedcall" component={DroppedCall} />
        <NotPrivateRoute path="/settings/editbreaks" component={EditBreaks} />
        <NotPrivateRoute path="/settings/usersettings" component={UserSettings} />
        <NotPrivateRoute path="/settings/departmentsettings" component={DepartmansSettigns} />
        <NotPrivateRoute path="/iys/called-number" component={IYS} />
        <NotPrivateRoute path="/teams/users" component={Users} />
        <NotPrivateRoute path="/reports/monthlyDetails" component={MonthlyDetails} />
        <NotPrivateRoute path="/reports/quality-report" component={QualityReport}/>
        <NotPrivateRoute path="/reports/queueanswerreport" component={QueueAnswerReport} />
        <NotPrivateRoute path="/reports/abondenedcalls" component={AbondenedCalls} />
        <NotPrivateRoute path="/reports/calltermination" component={CallTerminationReport} />
        <NotPrivateRoute path="/reports/agentcdrreport" component={AgentCdrReport} />
        <NotPrivateRoute path="/reports/internalssummary" component={InternalsSummaryReport} />
        <NotPrivateRoute path="/reports/dailysummaryreport" component={DailySumaryReport} />
        <NotPrivateRoute path="/dashboard" component={Dashboard} />
        <NotPrivateRoute path="/account" component={Account}/>
        <PrivateRouteLogin path="/signin" component={Login} />
        <Route component={Error404} />
      </Switch>
    </React.Fragment>
  );
};

export default Routes;
