import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import CallListeningFilter from './Filter';
import CallListeningTable from './Table';

export default function CallListeningReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'calllistening'} />, isActive: true },
  ];

  let [data, setData] = useState([]);
  const [count, setCount] = useState(10);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  return (
    <div>
      <PageContainer heading={<IntlMessages id={'calllistening'} />} breadcrumbs={breadcrumbs}>
        <CallListeningFilter
          setData={setData}
          limit={limit}
          offset={offset}
          setPage={setPage}
          setCount={setCount}
        />
        <CallListeningTable
          count={count}
          data={data}
          setLimit={setLimit}
          setOffset={setOffset}
          setPage={setPage}
          limit={limit}
          offset={offset}
          page={page}
        />
      </PageContainer>
    </div>
  );
}
