import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import QueueList from './QueueList';
import { Grid } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import AgentConnection from './AgentConnection';
import AgentStatus from './AgentStatus/AgentStatus';
import CallDetail from './CallDetail';
import AgentDetail from './AgentDetail';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';

const WallBoard = () => {
  let [queueAmaunt, setQueueAmaunt] = useState(0);
  let [callAmaunt, setCallAmaunt] = useState(0);
  let [agentAmaunt, setAgentAmaunt] = useState(0);
  let [breakeAgentAmaunt, setBreakeAgentAmount] = useState(0);
  let [loginAgentAmaunt, setLoginAgentAmount] = useState(0);
  let [agentNum, setAgentNum] = useState('0');
  let [agentName, setAgentName] = useState('-');
  const breadcrumbs = [
    { label: <IntlMessages id={'sidebar.main'} />, link: '/' },
    { label: <IntlMessages id={'wallboard'} />, isActive: true },
  ];

  let [callDetailVisible, setCallDetailVisible] = useState(true);
  const handleSwitchChange = e => {
    setCallDetailVisible(e.target.checked);
  };
  return (
    <PageContainer heading={<IntlMessages id="wallboard" />} breadcrumbs={breadcrumbs}>
      <div>
        {callDetailVisible && (
          <CallDetail
            queueAmaunt={queueAmaunt}
            callAmaunt={callAmaunt}
            agentAmaunt={agentAmaunt}
            breakeAgentAmaunt={breakeAgentAmaunt}
            loginAgentAmaunt={loginAgentAmaunt}
          />
        )}
        <FormControlLabel
          control={<Switch checked={callDetailVisible} onChange={handleSwitchChange} />}
          label={<IntlMessages id="showCards" />}
        />
      </div>
      <GridContainer>
        <Grid item md={6} style={{ paddingTop: '22px' }}>
          <div style={{ marginRight: '5px', textAlign: 'center' }}>
            <div
              style={{
                textAlign: 'center',
                fontSize: '16px',
                backgroundColor: '#EA4C4C   ',
                width: '35%',
                marginLeft: 'auto',
                marginRight: 'auto',
                borderTopLeftRadius: '25px',
                borderTopRightRadius: '25px',
                color: 'white',
                fontWeight: '800',
              }}>
              <IntlMessages id={'wallboard.queuelist'} />
            </div>
            <QueueList queueAmaunt={queueAmaunt} setQueueAmaunt={setQueueAmaunt} />
          </div>
        </Grid>
        <Grid item md={6} style={{ paddingTop: '22px' }}>
          <div style={{ marginLeft: '5px', textAlign: 'center' }}>
            <div
              style={{
                textAlign: 'center',
                fontSize: '16px',
                backgroundColor: '#2978B5   ',
                width: '35%',
                marginLeft: 'auto',
                marginRight: 'auto',
                borderTopLeftRadius: '25px',
                borderTopRightRadius: '25px',
                color: 'white',
                fontWeight: '800',
              }}>
              <IntlMessages id={'wallboard.agent.connection'} />
            </div>
            <AgentConnection
              setCallAmaunt={setCallAmaunt}
              setAgentAmaunt={setAgentAmaunt}
              setAgentNum={setAgentNum}
              setAgentName={setAgentName}
            />
          </div>
        </Grid>
        <Grid item md={6} style={{ paddingTop: '0px' }}>
          <div style={{ marginRight: '5px', textAlign: 'center' }}>
            <div
              style={{
                textAlign: 'center',
                fontSize: '16px',
                backgroundColor: '#FA744F',
                width: '35%',
                marginLeft: 'auto',
                marginRight: 'auto',
                borderTopLeftRadius: '25px',
                borderTopRightRadius: '25px',
                color: 'white',
                fontWeight: '800',
              }}>
              <IntlMessages id={'agent.status'} />
            </div>
            <AgentStatus
              setBreakeAgentAmount={setBreakeAgentAmount}
              setLoginAgentAmount={setLoginAgentAmount}
              setAgentNum={setAgentNum}
              setAgentName={setAgentName}
            />
          </div>
        </Grid>
        <Grid item md={6} style={{ paddingTop: '0px' }}>
          <div style={{ marginLeft: '5px', textAlign: 'center' }}>
            <div
              style={{
                textAlign: 'center',
                fontSize: '16px',
                backgroundColor: '#ADCE74',
                width: '35%',
                marginLeft: 'auto',
                marginRight: 'auto',
                borderTopLeftRadius: '25px',
                borderTopRightRadius: '25px',
                color: 'white',
                fontWeight: '800',
              }}>
              <IntlMessages id={'wallboard.agent.detail'} />
            </div>
            <AgentDetail agentNum={agentNum} agentName={agentName} />
          </div>
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};
export default WallBoard;
