import GridContainer from '@jumbo/components/GridContainer';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Grid } from '@material-ui/core';
import React, { useState } from 'react';
import Filter from './Filter';
import Table from './Table';
import dateFormat from 'dateformat';

export default function CallTransferReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'calltransferreport'} />, isActive: true },
  ];

  const [selectedStartTime, setSelectedStartTime] = useState(dateFormat(new Date().setHours(0, 0, 0),'yyyy-mm-dd'));
  const [selectedEndTime, setSelectedEndTime] = useState(dateFormat(new Date().setHours(23, 59, 59),'yyyy-mm-dd'));
  const [agent, setAgent] = useState('');

  return (
    <div>
      <PageContainer heading={<IntlMessages id={'calltransferreport'} />} breadcrumbs={breadcrumbs}>
        <GridContainer>
          <Grid item xs={12}>
            <Filter
              agent={agent}
              setAgent={setAgent}
              selectedStartTime={selectedStartTime}
              selectedEndTime={selectedEndTime}
              setSelectedEndTime={setSelectedEndTime}
              setSelectedStartTime={setSelectedStartTime}
            />
          </Grid>
          <Grid item xs={12}>
            <Table agent={agent} selectedStartTime={selectedStartTime} selectedEndTime={selectedEndTime} />
          </Grid>
        </GridContainer>
      </PageContainer>
    </div>
  );
}
