import {
  Card,
  CardContent,
  Grid,
  FormControl,
  TextField,
  Select,
  InputLabel,
  MenuItem,
  CircularProgress,
} from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import {
  getQueueAnswerReport,
  getQueueAnswerReportSummary,
  getQueueNumber,
  getQueueAnswerReportSummaryTotal,
  getQueueAnswerReportCount,
} from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import IntlMessages from '@jumbo/utils/IntlMessages';
import QueueAnswerReportDateFilter from './DateFilter';

export default function Filter({ setData, rowsPerPage, page, setSummary, setSummaryTotal, setResponseCount }) {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  let [startDate, setStartDate] = useState(formatDate(new Date()));
  let [endDate, setEndDate] = useState(formatDate(new Date()));
  let [queueNum, setQueueNum] = useState('');
  let [src, setSrc] = useState('');
  let [order, setOrder] = useState('ASC');
  let [queues, setQueues] = useState([]);

  let [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    getQueueAnswerReport(startDate, endDate, queueNum, src, order, rowsPerPage, rowsPerPage * page).then(resp => {
      setData(JSON.parse(resp.data.data));
      setLoading(false);
    });

    getQueueAnswerReportCount(startDate, endDate, queueNum, src).then(resp => {
      setResponseCount(JSON.parse(resp.data.data)[0].summed);
    });

    getQueueAnswerReportSummary(startDate, endDate, queueNum).then(resp => {
      setSummary(JSON.parse(resp.data.data));
    });

    getQueueAnswerReportSummaryTotal(startDate, endDate, queueNum).then(resp => {
      setSummaryTotal(JSON.parse(resp.data.data));
    });
  }, [startDate, endDate, queueNum, src, order, rowsPerPage, page, setData, setResponseCount, setSummary, setSummaryTotal]);

  useEffect(() => {
    getQueueNumber().then(response => {
      setQueues(JSON.parse(response.data.data));
    });
  }, []);
  const handleChangeQueueNum = event => {
    setQueueNum(event.target.value);
  };
  const handleChangeOrder = event => {
    setOrder(event.target.value);
  };
  const handleChangeCaller = event => {
    setSrc(event.target.value);
  };

  return (
    <div>
      <NotificationContainer />
      <div style={{ marginBottom: '10px' }}>
        <QueueAnswerReportDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
      </div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl variant="outlined" style={{ width: '100%' }}>
                <InputLabel id="demo-simple-select-helper-label">
                  <IntlMessages id={'queue.answer.report.queues'} />
                </InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  label={<IntlMessages id={'queue.answer.report.queues'} />}
                  value={queueNum}
                  onChange={handleChangeQueueNum}>
                  <MenuItem value="">
                    <em>
                      <IntlMessages id={'all'} />
                    </em>
                  </MenuItem>
                  {queues.map(que => (
                    <MenuItem key={que.id} value={que.dn}>
                      {que.dn} - {que.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <FormControl variant="outlined" style={{ width: '100%' }}>
                <InputLabel id="distinct-label">
                  <IntlMessages id={'queue.answer.report.orderby'} />
                </InputLabel>
                <Select
                  label={<IntlMessages id={'queue.answer.report.orderby'} />}
                  labelId="distinct-label"
                  id="distinct"
                  value={order}
                  onChange={handleChangeOrder}>
                  <MenuItem value="ASC">
                    <IntlMessages id={'oldtonew'} />
                  </MenuItem>
                  <MenuItem value="DESC">
                    <IntlMessages id={'newtoold'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <TextField
                variant="outlined"
                id="standard-basic"
                label={<IntlMessages id={'callernumber'} />}
                onChange={handleChangeCaller}
              />
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}
