import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import VisibilityIcon from '@mui/icons-material/Visibility';
import PersonIcon from '@mui/icons-material/Person';
import TimerIcon from '@mui/icons-material/Timer';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import EventNoteIcon from '@mui/icons-material/EventNote';
import dateFormat from 'dateformat';

export default function DataTable({ qnum, agent, agentSession, setClickedAgent, setOpen }) {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));
  const classes = useStyles();

  for (let i = 0; i < agentSession.length; i++) {
    for (let z = 0; z < agent.length; z++) {
      if (agentSession[i].ag_num === agent[z].dn) {
        agentSession[i].ag_num = agentSession[i].ag_num + '-' + agent[z].display_name;
      }
    }
  }
  for (let i = 0; i < agentSession.length; i++) {
    for (let z = 0; z < qnum.length; z++) {
      if (agentSession[i].q_num === qnum[z].dn) {
        agentSession[i].q_num = qnum[z].display_name;
      }
    }
  }

  let csvData = agentSession.map(row => ({
    ag_num: row.ag_num,
    q_num: row.q_num,
    status: row.status,
    state_time:
      row.escapetime
        .toString()
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    last_state_date:
      new Date(row.lastlogout).getTime() > new Date(row.lastlogin).getTime()
        ? dateFormat(row.lastlogout, 'dd/mm/yyyy HH:MM:ss')
        : dateFormat(row.lastlogin, 'dd/mm/yyyy HH:MM:ss'),
  }));

  let currentLanguage = useIntl();
  const handleClick = ag_num => {
    setClickedAgent(ag_num.split('-')[0]);
    setOpen(true);
  };
  return (
    <>
      {agentSession.length > 0 && (
        <div style={{ marginBottom: '10px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              currentLanguage.locale === 'en-EN'
                ? 'Agent_Session_Report'
                : currentLanguage.locale === 'tr-TR'
                ? 'Agent_Oturum_Raporu'
                : currentLanguage.locale === 'nl-NL'
                ? 'Agentsessierapport'
                : currentLanguage.locale === 'de-DE'
                ? 'Agentensitzungsbericht'
                : ' '
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={csvData} name="Data">
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Agent Name'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Agent Adƒ±'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Naam agent'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Agentenname'
                    : ''
                }
                value="ag_num"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Queue Name'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Kuyruk Adƒ±'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Wachtrijnaam'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Warteschlangenname'
                    : ''
                }
                value="q_num"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'State'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Durum'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Staat'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Bundesland'
                    : ''
                }
                value="status"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'State Time'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Durum S√ºresi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Status Tijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Staatszeit'
                    : ''
                }
                value="state_time"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Last State Date'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Son Durum Tarihi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Laatste Staat Tijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Letzte Zustandszeit'
                    : ''
                }
                value="last_state_date"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell align="left">
                <b>#</b>
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'detail'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'agent.name'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'wallboard.queue.name'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'state'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'state.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'last.state.date'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {agentSession.map((row, index) => (
              <TableRow key={index}>
                <TableCell>
                  <b>{index + 1}</b>
                </TableCell>
                <TableCell style={{ textAlign: 'center', cursor: 'pointer' }} onClick={() => handleClick(row.ag_num)}>
                  <VisibilityIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} />
                </TableCell>
                <TableCell align="center">
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#35589A' }} />
                  <b>{row.ag_num.split('-')[1] + ' (' + row.ag_num.split('-')[0] + ')'}</b>
                </TableCell>
                <TableCell align="center">
                  <b>{row.q_num}</b>
                </TableCell>
                <TableCell align="center">
                  <TimerIcon style={{ verticalAlign: '-5px', color: '#F05454' }} />{' '}
                  <b style={{ color: '#F05454' }}>{row.status}</b>
                </TableCell>
                <TableCell align="center">
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b style={{ color: '#F2789F' }}>
                    {' '}
                    {row.escapetime
                      .replace('.', 'd ')
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </b>
                </TableCell>
                <TableCell align="center">
                  <EventNoteIcon style={{ verticalAlign: '-5px', color: '#240499' }} />{' '}
                  <b style={{ color: '#240499' }}>
                    {new Date(row.lastlogout).getTime() > new Date(row.lastlogin).getTime()
                      ? dateFormat(row.lastlogout, 'dd/mm/yyyy HH:MM:ss')
                      : dateFormat(row.lastlogin, 'dd/mm/yyyy HH:MM:ss')}
                  </b>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
