import * as React from 'react';

import { Table, TableHead, TableBody, TableContainer, TableCell, Paper, TableRow } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';

const DataTable = ({ response }) => {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();
    let seconds = d.getSeconds();
    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;
    if (seconds < 10) seconds = '0' + seconds;

    return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + seconds;
  };
  function createData(agent, called, date, key) {
    return { agent, called, date, key };
  }
  const rows = response.map(data => createData(data.agent, data.called, formatDate(data.issuetime), data.key));

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell align="center">
              <IntlMessages id={'agent.number'} />
            </TableCell>
            <TableCell align="center">
              <IntlMessages id={'called'} />
            </TableCell>
            <TableCell align="center">
              <IntlMessages id={'filter.menu.date'} />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell align="center">{row.agent}</TableCell>
              <TableCell align="center">{row.called}</TableCell>
              <TableCell align="center">{row.date}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default DataTable;
