import React, { useState } from 'react';
import { Button, Grid } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import Filter from './Filter';
import DataTable from './Table';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Model from './Model';
import { Modal } from '@mui/material';
import Backdrop from '@mui/material/Backdrop';
import { Box } from '@mui/material';

const AgentCDR = () => {
  const [agentSession, setAgentSession] = useState([]);
  const [qnum, setQnum] = useState([]);
  const [agent, setAgent] = useState([]);
  const [clickedAgent, setClickedAgent] = useState('');
  const [clickedResponse, setClickedResponse] = useState([]);
  const [open, setOpen] = useState(false);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'agent.session.report'} />, isActive: true },
  ];

  const handleClose = () => {
    setOpen(false);
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '90%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const [responseBreak, setResponseBreak] = useState([]);

  return (
    <PageContainer heading={<IntlMessages id={'agent.session.report'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item md={12} sm={12} lg={12}>
          <Filter
            responseBreak={responseBreak}
            setResponseBreak={setResponseBreak}
            setAgentSession={setAgentSession}
            qnum={qnum}
            agent={agent}
            setQnum={setQnum}
            setAgent={setAgent}
            setClickedResponse={setClickedResponse}
            clickedAgent={clickedAgent}
          />
        </Grid>
        <Grid item md={12} sm={12} lg={12}>
          <DataTable
            qnum={qnum}
            agent={agent}
            agentSession={agentSession}
            setClickedAgent={setClickedAgent}
            setOpen={setOpen}
          />
        </Grid>
      </GridContainer>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Box sx={style}>
          <Model clickedResponse={clickedResponse} responseBreak={responseBreak} />
          <div style={{ textAlign: 'right', marginTop: '10px' }}>
            <Button style={{ backgroundColor: '#2978B5', color: 'white' }} onClick={() => handleClose()}>
              <IntlMessages id={'close'} />
            </Button>
          </div>
        </Box>
      </Modal>
    </PageContainer>
  );
};

export default AgentCDR;
