import React, { useEffect, useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { Box, Button, CircularProgress, Collapse, Typography, IconButton } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { getNewDate } from '@jumbo/utils/dateHelper';
import { CloudDownload } from '@material-ui/icons';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
export default function DataTable({ params, color, queue, agent }) {
  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 580,
    },
  }));
  const classes = useStyles();
  const [loading, setLoading] = useState(true);

  const [agents, setAgents] = useState([]);
  const [queues, setQueues] = useState([]);
  useEffect(() => {
    api.getQueue().then(resp => {
      setQueues(JSON.parse(resp.data.data));
    });
    api.getAgentNumber().then(resp => setAgents(JSON.parse(resp.data.data)));
  }, []);

  const [response, setResponse] = useState([]);
  const [detail, setDetail] = useState([]);

  useEffect(() => {
    setLoading(true);
    let getDetails = (time, userpart) => {
      return new Promise((resolve, reject) => {
        api.getAbondenedCallDetails(time, userpart).then(resp => resolve(JSON.parse(resp.data.data)));
      });
    };

    api.getAbondenedCalls(params, queue, agent).then(call => {
      let calls = JSON.parse(call.data.data);
      let detailPromises = [];
      calls.forEach(item => detailPromises.push(getDetails(item.time_start, item.from_displayname)));
      Promise.all(detailPromises).then(details => {
        setDetail(details);
        setResponse(calls);
        setLoading(false);
      });
    });
  }, [params, queue, agent]);

  let res = [];
  for (let i = 0; i < response.length; i++) {
    res.push({
      from_userpart: response[i].from_displayname,
      timeofevent: getNewDate(response[i].time_start.split(' ')[0]) + ' ' + response[i].time_start.split(' ')[1],
      count: response[i].count,
      queue: queues.find(e => e.dn === response[i].q_num)?.display_name || '',
      agent: `${agents.find(a => a.dn === response[i].ag_num)?.display_name} (${response[i].ag_num})`,
    });
  }
  let curentLanguage = useIntl();

  function Row(props) {
    const { calls, index } = props;
    const [open, setOpen] = useState(false);
    let hasDetail = detail[index]?.length > 0;
    return (
      <React.Fragment>
        <TableRow
          style={{
            display: ((color === 'Green' && !hasDetail) || (color === 'Red' && hasDetail)) && 'none',
            backgroundColor: hasDetail ? '#4E9F3D' : '#FF5C58',
          }}
          sx={{
            '& > *': { borderBottom: 'unset' },
          }}>
          <TableCell align="left">
            <b>{index + 1}</b>
          </TableCell>
          <TableCell>
            <IconButton
              disabled={detail[index] !== undefined ? (detail[index].length !== 0 ? false : true) : true}
              aria-label="expand row"
              size="small"
              onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </TableCell>
          <TableCell align="center">{calls.timeofevent}</TableCell>
          <TableCell align="center">{calls.from_userpart}</TableCell>
          <TableCell align="center">{calls.agent}</TableCell>
          <TableCell align="center">{calls.queue}</TableCell>
          <TableCell align="center">{calls.count}</TableCell>
        </TableRow>
        {hasDetail && (
          <TableRow style={{ display: ((color === 'Green' && !hasDetail) || (color === 'Red' && hasDetail)) && 'none' }}>
            <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={7}>
              <Collapse in={open} timeout="auto" unmountOnExit>
                <Box sx={{ margin: 1 }}>
                  <Typography variant="h6" gutterBottom component="div">
                    <IntlMessages id={'abondenedcalls.detail'} />
                  </Typography>
                  <Table size="small" aria-label="Details">
                    <TableHead>
                      <TableRow>
                        <TableCell align="left">
                          <b>#</b>
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.diraction'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.srcdn'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.dststarttime'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.dstendtime'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.time'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {detail[index].map((deta, ins) => (
                        <TableRow key={ins}>
                          <TableCell component="th" scope="row" align="left">
                            <b>{ins + 1}</b>
                          </TableCell>
                          <TableCell>
                            {deta.direction === '-->' ? (
                              <span
                                style={{
                                  padding: '5px',
                                  backgroundColor: '#4E9F3D',
                                  color: '#125C13',
                                  borderRadius: '10px',
                                  fontWeight: '600',
                                }}>
                                <IntlMessages id={'incomingcall'} />
                              </span>
                            ) : (
                              <span
                                style={{
                                  padding: '5px',
                                  backgroundColor: '#FF7777',
                                  color: '#852747',
                                  borderRadius: '10px',
                                  fontWeight: '600',
                                }}>
                                <IntlMessages id={'outgoingcall'} />
                              </span>
                            )}
                          </TableCell>
                          <TableCell>{deta.src_dn}</TableCell>
                          <TableCell>{deta.dst_start_time}</TableCell>
                          <TableCell>{deta.dst_end_time}</TableCell>
                          <TableCell>{deta.calltime}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </Box>
              </Collapse>
            </TableCell>
          </TableRow>
        )}
      </React.Fragment>
    );
  }
  return (
    <>
      <NotificationContainer />
      {res.length > 0 && (
        <div style={{ textAlign: 'right', marginBottom: '15px' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Abondened_calls'
                : curentLanguage.locale === 'tr-TR'
                ? 'Cevaplanmayan_cagrilar'
                : curentLanguage.locale === 'nl-NL'
                ? 'Verlaten oproepen'
                : curentLanguage.locale === 'de-DE'
                ? 'Abgebrochene Anrufe'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={res} name="res">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Time of Event'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Etkinlik Zamanƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Tijd van evenement'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Zeitpunkt des Ereignisses'
                    : ''
                }
                value="timeofevent"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'From'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Kimden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Van'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Von'
                    : ''
                }
                value="from_userpart"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Display Name'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Ekran adƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Weergavenaam'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anzeigename'
                    : ''
                }
                value="display_name"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Adet'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Graaf'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anzahl'
                    : ''
                }
                value="count"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      {loading ? (
        <div style={{ textAlign: 'center', marginTop: '10px' }}>
          <CircularProgress />
        </div>
      ) : (
        <Paper className={classes.root}>
          <TableContainer className={classes.container}>
            <Table stickyHeader aria-label="Abondened Calls Table" size="small">
              <TableHead>
                <TableRow>
                  <TableCell align="left">
                    <b>#</b>
                  </TableCell>
                  <TableCell />
                  <TableCell align="center">
                    <IntlMessages id={'abondenedcalls.timeofevent'} />
                  </TableCell>
                  <TableCell align="center">
                    <IntlMessages id={'abondenedcalls.fromuserpart'} />
                  </TableCell>
                  <TableCell align="center">
                    <IntlMessages id={'called'} />
                  </TableCell>
                  <TableCell align="center">
                    <IntlMessages id={'abondenedcalls.displayname'} />
                  </TableCell>
                  <TableCell align="center">
                    <IntlMessages id={'abondenedcalls.count'} />
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {res.length > 0 && res.map((item, index) => <Row key={index} index={index} calls={item} />)}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      )}
    </>
  );
}