import { getDailySummaryModalOutOfTime } from 'api/api';
import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button } from '@mui/material';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getTotalSec } from '@jumbo/utils/dateHelper';

export default function OutOfTimeModal({ date, handleOutOfTimeClose }) {
  let [data, setData] = useState([]);
  useEffect(() => {
    getDailySummaryModalOutOfTime(date).then(result => {
      setData(JSON.parse(result.data.data));
    });
  }, [date]);
  let resp = [];
  let num = 0;
  for (let i = 0; i < data.length; i++) {
    let { ...datas } = data[i];
    if (i === 0) {
      resp.push({
        num: num + 1,
        ...datas,
      });
      num += 1;
    } else if (data[i - 1].call_id === data[i].call_id) {
      resp.push({
        num: resp[i - 1].num,
        ...datas,
      });
    } else {
      resp.push({
        num: num + 1,
        ...datas,
      });
      num += 1;
    }
  }
  return (
    <div>
      <TableContainer style={{ height: '600px' }}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>
                <b>#</b>
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.caller'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.called'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.starttime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.endtime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.ringtime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.talktime'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {resp.map((item, index) => (
              <TableRow key={index}>
                <TableCell>
                  <b>{item.num}</b>
                </TableCell>
                <TableCell>{item.src_display_name}</TableCell>
                <TableCell>{item.dst_display_name}</TableCell>
                <TableCell>{item.start_time}</TableCell>
                <TableCell>{getTotalSec(item.talk_time) === 0 ? item.start_time : item.end_time}</TableCell>
                <TableCell>{item.ring_time}</TableCell>
                <TableCell>{item.talk_time}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div style={{ textAlign: 'right' }} lang="en">
        <Button style={{ backgroundColor: '#3f51b5', color: 'white' }} onClick={() => handleOutOfTimeClose()}>
          <IntlMessages id={'close'} />
        </Button>
      </div>
    </div>
  );
}
