import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import InternalsSummaryReportFilter from './Filter';
import InternalsSummaryReportTable from './Table';

export default function InternalsSummaryReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'internalssummary'} />, isActive: true },
  ];
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  let [data, setData] = useState([]);
  let [startTime, setStartTime] = useState(formatDate(new Date()));
  let [endTime, setEndTime] = useState(formatDate(new Date()));
  const [direction,setDirection] = useState("all");

  return (
    <PageContainer heading={<IntlMessages id={'internalssummary'} />} breadcrumbs={breadcrumbs}>
      <InternalsSummaryReportFilter
        setData={setData}
        startTime={startTime}
        setStartTime={setStartTime}
        endTime={endTime}
        setEndTime={setEndTime}
        direction={direction}
        setDirection={setDirection}
      />
      <InternalsSummaryReportTable data={data} startTime={startTime} endTime={endTime} direction={direction} />
    </PageContainer>
  );
}
