import React, { useEffect, useState } from 'react';
import CounterCard from '../../../../@jumbo/components/Common/CounterCard';
import PhoneIcon from '@material-ui/icons/PhoneOutlined';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
const ActiveCallCount = () => {
  const [response, setactiveCount] = useState();
  const [param] = useState({
    token: localStorage.getItem('token'),
  });

  useEffect(() => {
    api.getActiveCallCount(param).then(resp => setactiveCount(resp.data.data.count));
  }, [param]);

  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <CounterCard
        icon={<PhoneIcon />}
        number={String(response)}
        label={<IntlMessages id={'dashboard.active'} />}
        labelProps={{
          fontSize: 16,
        }}
        backgroundColor={['#EA4C4C -18.96%', '#C14545 108.17%']}
        gradientDirection="180deg"
      />
    </div>
  );
};

export default ActiveCallCount;
