import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import { Button, FormControl, IconButton, InputAdornment, InputLabel, MenuItem, OutlinedInput, Select } from '@mui/material';
import React, { useState } from 'react';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { updateUser } from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function UpdateUserModal({ setReloadValues, updateHandleClose, selectedUser }) {
  let [userName, setUserName] = useState(selectedUser.name);
  const handleChangeUserName = event => {
    setUserName(event.target.value);
  };
  let [password, setPassword] = useState(selectedUser.pass);
  const handleChangePassword = event => {
    setPassword(event.target.value);
  };
  let [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);

  let [userType, setUserType] = useState(selectedUser.type);
  const handleChangeUserType = event => {
    setUserType(event.target.value);
  };

  const handleSubmit = () => {
    if (userName === undefined || userName === '') {
      NotificationManager.error('Kullanƒ±cƒ± adƒ± doƒüru formatta deƒüil', '', 3000);
    } else if (password === undefined || password === '') {
      NotificationManager.error('≈ûifre doƒüru formatta deƒüil', '', 3000);
    } else if (userType === undefined || password === '') {
      NotificationManager.error('User Type doƒüru formatta deƒüil', '', 3000);
    } else {
      updateUser(selectedUser.id, userName, password, userType).then(result => {
        NotificationManager.success('Kullanƒ±cƒ± G√ºncellendi', '', 3000);
        setReloadValues(userName);
        updateHandleClose('');
      });
    }
  };

  return (
    <div>
      <NotificationContainer />
      <GridContainer>
        <Grid item md={6}>
          <FormControl sx={{ m: 1, width: '100%' }} variant="outlined">
            <InputLabel htmlFor="username">
              <IntlMessages id={'username'} />
            </InputLabel>
            <OutlinedInput id="username" type="text" value={userName} onChange={handleChangeUserName} label="User Name" />
          </FormControl>
        </Grid>
        <Grid item md={6}>
          <FormControl sx={{ m: 1, width: '100%' }} variant="outlined">
            <InputLabel htmlFor="outlined-adornment-password">
              <IntlMessages id={'password'} />
            </InputLabel>
            <OutlinedInput
              id="outlined-adornment-password"
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={handleChangePassword}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton aria-label="toggle password visibility" onClick={handleClickShowPassword} edge="end">
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              }
              label="Password"
            />
          </FormControl>
        </Grid>
      </GridContainer>
      <FormControl sx={{ width: '100%', m: 1 }}>
        <InputLabel id="usertype">
          <IntlMessages id={'usertype'} />
        </InputLabel>
        <Select labelId="usertype" label="User Type" id="usertype-select" value={userType} onChange={handleChangeUserType}>
          <MenuItem value={'admin'}>Admin</MenuItem>
          <MenuItem value={'supervisor'}>Supervisor</MenuItem>
        </Select>
      </FormControl>
      <div>
        <Button sx={{ width: '100%' }} color="info" variant="contained" onClick={() => handleSubmit()}>
          <IntlMessages id={'update'} />
        </Button>
      </div>
    </div>
  );
}
