import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import CallManagementFilter from './Filter';
import CallManagementTable from './Table';

export default function CallManagement() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'callmanagement'} />, isActive: true },
  ];

  let [data, setData] = useState([]);
  const [count, setCount] = useState(10);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [reloadValues, setReloadValues] = useState([]);
  const [score, setScore] = useState('');
  const [tagFilter, setTagFilter] = useState('');
  return (
    <div>
      <PageContainer heading={<IntlMessages id={'callmanagement'} />} breadcrumbs={breadcrumbs}>
        <CallManagementFilter
          setData={setData}
          limit={limit}
          offset={offset}
          setPage={setPage}
          setCount={setCount}
          setScore={setScore}
          setTagFilter={setTagFilter}
        />
        <CallManagementTable
          count={count}
          data={data}
          setLimit={setLimit}
          setOffset={setOffset}
          setPage={setPage}
          limit={limit}
          offset={offset}
          page={page}
          reloadValues={reloadValues}
          setReloadValues={setReloadValues}
          scoreFilter={score}
          tagFilter={tagFilter}
        />
      </PageContainer>
    </div>
  );
}
