import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import Graph from './Graph';
import DataTable from './Table';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';

const MonthlyLog = () => {
  const [response, setResponse] = useState([]);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'monthly.log'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'monthly.log'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item md={12} xs={12} lg={12}>
          <Filter setResponse={setResponse}/>
        </Grid>
        <Grid item md={6} xs={12} lg={6}>
          <DataTable response={response} />
        </Grid>
        <Grid item md={6} xs={12} lg={6}>
          <Graph response={response} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default MonthlyLog;
