import React, { useState, useEffect } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import DataTable from './Table';
import { getIYSReport } from 'api/api';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';

const IYS = () => {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  const today = new Date();
  const [selectedStartTime, setSelectedStartTime] = useState(formatDate(today) + ' 00:00:00');
  const [selectedEndTime, setSelectedEndTime] = useState(formatDate(today) + ' 23:59:00');
  const [response, setResponse] = useState([]);
  const [params, setParams] = useState({
    agent: 'All',
    startdate: formatDate(today) + ' 00:00:00',
    enddate: formatDate(today) + ' 23:59:00',
  });
  useEffect(() => {
    getIYSReport(params).then(response => setResponse(response.data));
  }, [params]);

  return (
    <PageContainer>
      <GridContainer>
        <Grid item sm={12} md={12} lg={12}>
          <Filter
            selectedStartTime={selectedStartTime}
            selectedEndTime={selectedEndTime}
            setSelectedEndTime={setSelectedEndTime}
            setSelectedStartTime={setSelectedStartTime}
            setParams={setParams}
          />
        </Grid>
        <Grid item sm={12} md={12} lg={12}>
          <DataTable response={response} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default IYS;
