import React, { useState, useEffect } from 'react';
import IntlMessages from '@jumbo/utils/IntlMessages';
import GridContainer from '@jumbo/components/GridContainer';
import { Typography, Paper, Grid } from '@mui/material';
import { Cell, Pie, PieChart, Tooltip } from 'recharts';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';

export default function Account() {
  const [company, setCompany] = useState('');
  const [user, setUser] = useState('');
  useEffect(() => {
    setCompany(localStorage.getItem('company') || '');
    setUser(localStorage.getItem('username') || '');
  }, []);

  const flexbox = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  };
  const colors = ['#916BBF', '#C6D57E', '#FFC069', '#F6A9A9', '#5C7AEA', '#FFF7AE'];

  return (
    <PageContainer>
      {company && (
        <Typography variant="h3" component="div">
          {company}
        </Typography>
      )}
      <Paper sx={{ py: 3, px: 2, my: 3 }}>
        <Typography variant="h4" component="div">
          <span style={{ fontWeight: '300' }}>
            <IntlMessages id={'welcome'} />,
          </span>{' '}
          {user}
        </Typography>
      </Paper>
      <GridContainer>
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }} style={flexbox}>
            <Typography variant="h6" component="div">
              Yeni Bildirimlerim
            </Typography>
            <PieChart width={250} height={250}>
              <Pie
                data={newNotifications}
                color="#000"
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                isAnimationActive={true}
                outerRadius={95}
                fill="#8884d8">
                {newNotifications.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }} style={flexbox}>
            <Typography variant="h6" component="div">
              Devam Eden Bildirimlerim
            </Typography>
            <PieChart width={250} height={250}>
              <Pie
                data={currentNotifications}
                color="#000"
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                isAnimationActive={true}
                outerRadius={95}
                fill="#8884d8">
                {currentNotifications.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 2 }} style={flexbox}>
            <Typography variant="h6" component="div">
              Gecikmi≈ü Bildirimlerim
            </Typography>
            <PieChart width={250} height={250}>
            <Pie
                data={delayedNotifications}
                color="#000"
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                isAnimationActive={true}
                outerRadius={95}
                fill="#8884d8">
                {delayedNotifications.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </Paper>
        </Grid>
      </GridContainer>
    </PageContainer>
  );
}
const newNotifications = [
  {
    name: 'mesaj',
    value: 30,
  },
  {
    name: 'kuyruk',
    value: 3,
  },
];
const currentNotifications = [
  {
    name: 'mesaj',
    value: 27,
  },
  {
    name: 'kuyruk',
    value: 12,
  },
];
const delayedNotifications = [
  {
    name: 'mesaj',
    value: 1,
  },
  {
    name: 'kuyruk',
    value: 0,
  },
];