import IntlMessages from '@jumbo/utils/IntlMessages';
import { InputLabel, MenuItem, Select, FormControl, Chip, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { getTagsByCallId, ScoreByID } from 'api/api';

export default function CallManagementCallDetail({ callId, setReloadValues }) {
  const [tags, setTags] = useState([]);
  const [rating, setRating] = useState('-1');
  useEffect(() => {
    getTagsByCallId(callId).then(resp => {
      setTags(JSON.parse(resp.data.data));
      setRating(JSON.parse(resp.data.data)[0]?.rating || '-1');
    });
  }, [callId]);
  const handleRating = event => {
    ScoreByID(event.target.value, callId).then(() => setRating(event.target.value));
    setReloadValues(new Date());
  };

  return (
    <div>
      <Typography variant="h5" sx={{ mb: 2 }}>
        <IntlMessages id={'CallLog.detail.callinfo'} />
      </Typography>

      {tags.length > 0 && tags[0]?.tagtext !== '' && (
        <Typography variant="h6">
          <IntlMessages id={'tag'} /> :
          <Chip color="info" label={tags[0]?.tagtext} />
        </Typography>
      )}
      <FormControl variant="standard" fullWidth>
        <InputLabel id="score">
          <IntlMessages id={'score'} />
        </InputLabel>
        <Select labelId="score" label={<IntlMessages id={'score'} />} value={rating} onChange={handleRating}>
          <MenuItem value="-1">
            <br />
          </MenuItem>
          <MenuItem value="5">
            <IntlMessages id={'score.rating5'} />
          </MenuItem>
          <MenuItem value="4">
            <IntlMessages id={'score.rating4'} />
          </MenuItem>
          <MenuItem value="3">
            <IntlMessages id={'score.rating3'} />
          </MenuItem>
          <MenuItem value="2">
            <IntlMessages id={'score.rating2'} />
          </MenuItem>
          <MenuItem value="1">
            <IntlMessages id={'score.rating1'} />
          </MenuItem>
        </Select>
      </FormControl>
    </div>
  );
}
