import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import Chip from '@mui/material/Chip';
import React, { useEffect, useState } from 'react';
import * as api from '../../../../api/api';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { lighten, makeStyles } from '@material-ui/core/styles';
import PersonIcon from '@mui/icons-material/Person';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const AgentConnection = ({ setCallAmaunt, setAgentAmaunt, setAgentNum, setAgentName }) => {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  const [response, setResponse] = useState([]);
  const [queues, setQueues] = useState([]);
  useEffect(() => {
    reloadValues();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    api.getQueueNumber().then(resp => setQueues(JSON.parse(resp.data.data)));
  }, []);
  const reloadValues = () => {
    if (window.location.pathname === '/wallboard') {
      api.getAgentConnection().then(resp => {
        setResponse(resp.data.data);
      });
      setValuesFunction();
    }

    var agentConnectionTimeout = setTimeout(reloadValues, 5000);
    if (window.location.pathname !== '/wallboard') {
      clearTimeout(agentConnectionTimeout);
    }
  };

  const setValuesFunction = () => {
    let callAmaunt = 0;
    for (let i = 0; i < response.length; i++) {
      if (response[i].connectionNumber !== '-') {
        callAmaunt++;
      }
    }

    setCallAmaunt(callAmaunt);
    setAgentAmaunt(response.length);
  };

  const setAgentNumFunction = (agentNum, agentName) => {
    setAgentNum(agentNum);
    setAgentName(agentName);
  };

  const classes = useStyles();

  const column = [
    { id: 'agent_num', headerName: <IntlMessages id={'agent.number'} />, width: '30px' },
    { id: 'agent_name', headerName: <IntlMessages id={'agent'} />, width: '30px' },
    { id: 'direction', headerName: <IntlMessages id={'direction'} />, width: '100px' },
    { id: 'queue', headerName: <IntlMessages id={'queue'} />, width: '100px' },
    { id: 'connection_number', headerName: <IntlMessages id={'connection.number'} />, width: '60px' },
    { id: 'connection_time', headerName: <IntlMessages id={'connection.time'} />, width: '100px' },
  ];

  return (
    <TableContainer
      style={{ maxHeight: '32vh', boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}
      component={Paper}
      className={classes.root}>
      <Table stickyHeader className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell
              key={column.id}
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#2978B5',
                color: 'white',
              }}>
              <IntlMessages id={'internals.summary.agent'} />
            </TableCell>
            <TableCell
              key={column.id}
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#2978B5',
                color: 'white',
              }}>
              <IntlMessages id={'direction'} />
            </TableCell>
            <TableCell
              key={column.id}
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#2978B5',
                color: 'white',
              }}>
              <IntlMessages id={'queue'} />
            </TableCell>
            <TableCell
              key={column.id}
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#2978B5',
                color: 'white',
              }}>
              <IntlMessages id={'connection.number'} />
            </TableCell>
            <TableCell
              key={column.id}
              style={{
                paddingTop: '8px',
                paddingBottom: '8px',
                fontFamily: 'Roboto',
                backgroundColor: '#2978B5',
                color: 'white',
              }}>
              <IntlMessages id={'connection.time'} />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {response.map((row, index) => {
            return row.connectionNumber !== '-' ? (
              <TableRow
                key={index}
                style={{ backgroundColor: '#80ED99', cursor: 'pointer' }}
                hover
                role="checkbox"
                tabIndex={-1}>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{row.agentName}</b>{' '}
                  <b>{'(' + row.agentNumber + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  {row.direction.includes('inbound') ? (
                    <Chip color="success" label={<IntlMessages id={'incomingcall'} />} />
                  ) : row.connectionNumber.length < 10 ? (
                    <Chip color="info" label={<IntlMessages id={'agentedcall'} />} />
                  ) : (
                    <Chip color="error" label={<IntlMessages id={'outgoingcall'} />} />
                  )}
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  {row?.queue !== null ? (
                    row?.queue !== undefined ? (
                      queues.find(e => e.dn === row?.queue?.split('.')[0])?.display_name || <b>-</b>
                    ) : (
                      <b>-</b>
                    )
                  ) : (
                    <b>-</b>
                  )}
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{row.connectionNumber}</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(row.connectionTime !== '-' ? row.connectionTime.substring(0, 8) : '00:00:00') !== undefined
                      ? (row.connectionTime !== '-' ? row.connectionTime.substring(0, 8) : '00:00:00')
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null;
          })}
          {response.map((row, index) => {
            return row.connectionNumber === '-' ? (
              <TableRow key={index} hover role="checkbox" tabIndex={-1} style={{ cursor: 'pointer' }}>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{row.agentName}</b>{' '}
                  <b>{'(' + row.agentNumber + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <b>-</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <b>-</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <b>{row.connectionNumber}</b>
                </TableCell>
                <TableCell
                  onClick={() => setAgentNumFunction(row.agentNumber, row.agentName)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(row.connectionTime !== '-' ? row.connectionTime.substring(0, 8) : '00:00:00') !== undefined
                      ? (row.connectionTime !== '-' ? row.connectionTime.substring(0, 8) : '00:00:00')
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null;
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
export default AgentConnection;
