import React, { useState, useEffect } from 'react';
import CounterCard from '../../../../@jumbo/components/Common/CounterCard';
import { GroupOutlined } from '@material-ui/icons';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const TasksCounterCard = () => {
  const [extensions, setExtensions] = useState('');
  const [param] = useState({ token: localStorage.getItem('token') });
  useEffect(() => {
    api
      .getExtensions(param)
      .then(resp => setExtensions(resp.data.data.activeExtension + '/' + resp.data.data.totalExtension));
  }, [param]);
  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <CounterCard
        icon={<GroupOutlined />}
        number={String(extensions)}
        label={<IntlMessages id={'dashboard.extensions'} />}
        labelProps={{
          fontSize: 16,
        }}
        backgroundColor={['#8AB6D6  -18.96%', '#2978B5 108.17%']}
        gradientDirection="180deg"
      />
    </div>
  );
};

export default TasksCounterCard;
