import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import { Box, Modal, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { getBreakName, getBreakTime } from 'api/api';
import React, { useEffect, useState } from 'react';
import BreaksChangeBreakNameModal from './Modals/BreaksChangeBreakNameModal';
import BreaksChangeBreakTimeModal from './Modals/BreaksChangeBreakTimeModal';
import EditIcon from '@mui/icons-material/Edit';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function EditBreaks() {
  const [breaks, setBreaks] = useState([]);

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };
  const [breakNameModalopen, setBreakNameModalOpen] = useState(false);
  const handleBreakNameModalOpen = () => setBreakNameModalOpen(true);
  const handleBreakNameModalClose = () => setBreakNameModalOpen(false);
  let [selectedBreakId, setSelectedBreakId] = useState('');
  let [selectedBreakName, setSelectedBreakName] = useState('');
  const handleClickName = (id, name) => {
    setSelectedBreakId(id);
    setSelectedBreakName(name);
    handleBreakNameModalOpen();
  };

  const [breakTimeModalopen, setBreakTimeModalOpen] = useState(false);
  const handleBreakTimeModalOpen = () => setBreakTimeModalOpen(true);
  const handleBreakTimeModalClose = () => setBreakTimeModalOpen(false);
  let [selectedLogoutType, setSelectedLogoutType] = useState('');
  let [selectedBreakTime, setSelectedBreakTime] = useState('');
  const handleClickTime = (logoutType, time) => {
    setSelectedLogoutType(logoutType);
    setSelectedBreakTime(time);
    handleBreakTimeModalOpen();
  };

  useEffect(() => {
    getBreakTime().then(resp => {
      var mola = JSON.parse(resp.data.data);
      getBreakName().then(res => {
        var molaAdƒ± = JSON.parse(res.data.data);
        for (var i = 0; i < mola.length; i++) {
          for (var z = 0; z < molaAdƒ±.length; z++) {
            if (mola[i].logouttype === molaAdƒ±[z].value) {
              mola[i].name = molaAdƒ±[z].name;
              mola[i].id = molaAdƒ±[z].iddn;
            }
          }
        }
        setBreaks(mola);
      });
    });
  }, [breakNameModalopen, breakTimeModalopen]);

  return (
    <PageContainer>
      <Paper>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  <IntlMessages id={'edit.breaks.breakcode'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'edit.breaks.breakname'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'edit.breaks.breaktime'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {breaks.map(item => (
                <TableRow key={item.id}>
                  <TableCell>{item.logouttype}</TableCell>
                  <TableCell onClick={() => handleClickName(item.id, item.name)} style={{ cursor: 'pointer' }}>
                    <EditIcon style={{ verticalAlign: '-5px', color: '#1A374D' }} /> {item.name}
                  </TableCell>
                  <TableCell onClick={() => handleClickTime(item.logouttype, item.period)} style={{ cursor: 'pointer' }}>
                    <EditIcon style={{ verticalAlign: '-5px', color: '#1A374D' }} /> {item.period}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      <Modal
        open={breakNameModalopen}
        onClose={handleBreakNameModalClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <BreaksChangeBreakNameModal
            id={selectedBreakId}
            selectedBreakName={selectedBreakName}
            handleBreakNameModalClose={handleBreakNameModalClose}
          />
        </Box>
      </Modal>
      <Modal
        open={breakTimeModalopen}
        onClose={handleBreakTimeModalClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <BreaksChangeBreakTimeModal
            logouttype={selectedLogoutType}
            period={selectedBreakTime}
            handleBreakTimeModalClose={handleBreakTimeModalClose}
          />
        </Box>
      </Modal>
    </PageContainer>
  );
}
