import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import Table from './Table';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Alert } from '@material-ui/lab';

function ServiceLevel() {
  const [response, setResponse] = useState([]);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'service.level'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'service.level'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Filter setResponse={setResponse} />
        </Grid>
        <Grid item xs={12} xl={12}>
          <Table response={response} />
        </Grid>
      </GridContainer>
      <Alert style={{ marginTop: '20px' }} severity="info">
        <IntlMessages id={'servicelevel.info'} />
      </Alert>
    </PageContainer>
  );
}

export default ServiceLevel;
