import React, { useState, useEffect } from 'react';
import Cable from '@material-ui/icons/Link';
import CounterCard from '../../../../@jumbo/components/Common/CounterCard';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Trunks = () => {
  let totalquery = 'select * from public.externalline order by fkiddn ASC';
  let trunksUpQuery = 'SELECT count(ipincontactreg) FROM gateway';

  const [paramtotal] = useState({ token: localStorage.getItem('token'), query: totalquery });
  const [paramTrunksUp] = useState({ token: localStorage.getItem('token'), query: trunksUpQuery });
  const [totalTrunks, setTotalTrunks] = useState('');

  const [trunksUp, setTrunksUp] = useState([{ count: 0 }]);
  useEffect(() => {
    api.getTotalTrunks(paramtotal).then(resp => setTotalTrunks(JSON.parse(resp.data.data)));
    api.getTrunksUp(paramTrunksUp).then(resp => setTrunksUp(JSON.parse(resp.data.data)));
  }, [paramtotal, paramTrunksUp]);

  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <CounterCard
        icon={<Cable />}
        number={String(trunksUp[0].count) + '/' + String(totalTrunks.length)}
        label={<IntlMessages id={'dashboard.trunks'} />}
        labelProps={{
          fontSize: 16,
        }}
        backgroundColor={['#FFF9B0  -18.96%', '#F48B29 108.17%']}
        gradientDirection="180deg"
      />
    </div>
  );
};

export default Trunks;
