import IntlMessages from '@jumbo/utils/IntlMessages';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import dateFormat from 'dateformat';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Chip from '@mui/material/Chip';

export default function QualityTable({ filtered }) {
  var nonRepeatingArr = [];
  var copyArr = JSON.parse(JSON.stringify(filtered));
  for (var i = 0; i < filtered.length; i++) {
    var temp = [];
    for (var j = 0; j < copyArr.length; j++) {
      if (filtered[i].agent === copyArr[j].agent && filtered[i].score === copyArr[j].score) {
        temp.push(filtered[i].ani);
        copyArr[j].score = null;
      }
    }
    if (temp.length > 0) {
      nonRepeatingArr.push({
        agent: filtered[i].agent,
        score: filtered[i].score,
        timeofinsertion: filtered[i].timeofinsertion,
        voters: temp,
      });
    }
  }

  const [averageScores, setAverageScores] = useState([]);
  useEffect(() => {
    const totalScoreByAgent = filtered.reduce((acc, item) => {
      return { ...acc, [item.agent]: (acc[item.agent] || 0) + parseInt(item.score) };
    }, {});
    const totalVotesByAgent = filtered.reduce((acc, item) => {
      return { ...acc, [item.agent]: (acc[item.agent] || 0) + 1 };
    }, {});
    let temp = [];
    for (let i in totalScoreByAgent) {
      temp.push({
        agent: i,
        totalVote: totalVotesByAgent[i],
        averageScore: (totalScoreByAgent[i] / totalVotesByAgent[i]).toFixed(1),
      });
    }
    setAverageScores(temp);
  }, [filtered]);

  const [voters, setVoters] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <TableContainer style={{ maxHeight: '500px' }} component={Paper}>
        <Table stickyHeader aria-label="Scores">
          <TableHead>
            <TableRow>
              <TableCell>
                <b>#</b>
              </TableCell>
              <TableCell>
                <IntlMessages id={'date'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'agent.name'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'votescount'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'score'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {nonRepeatingArr.length > 0 &&
              nonRepeatingArr.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>{dateFormat(item.timeofinsertion, 'yyyy-mm-dd HH:MM:ss')}</TableCell>
                  <TableCell>{item.agent}</TableCell>
                  <TableCell>
                    <span
                      aria-controls={open ? 'voters-menu' : undefined}
                      aria-haspopup="true"
                      style={{ cursor: 'pointer' }}
                      onClick={e => {
                        setVoters(item.voters);
                        setAnchorEl(e.currentTarget);
                      }}>
                      <b>{item.voters.length}</b>
                    </span>
                  </TableCell>
                  <TableCell>{item.score}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>

      {averageScores.length > 0 && (
        <TableContainer style={{ maxHeight: '500px', marginTop: '1rem', width: '500px', float: 'right' }} component={Paper}>
          <Table stickyHeader aria-label="Average Scores">
            <TableHead>
              <TableRow>
                <TableCell align="right">
                  <IntlMessages id={'agent.name'} />
                </TableCell>
                <TableCell align="right">
                  <IntlMessages id={'totalvotes'} />
                </TableCell>
                <TableCell align="right">
                  <IntlMessages id={'average'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {averageScores.map((item, index) => (
                <TableRow key={index}>
                  <TableCell align="right">{item.agent}</TableCell>
                  <TableCell align="right">{item.totalVote}</TableCell>
                  <TableCell align="right">
                    <b>{item.averageScore}</b>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Menu id="voters-menu" anchorEl={anchorEl} open={open} onClose={handleClose}>
        {voters.map((item, index) => (
          <MenuItem key={index}>
            <Chip label={item} variant="outlined" />
          </MenuItem>
        ))}
      </Menu>
    </>
  );
}
