import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { lighten, makeStyles } from '@material-ui/core/styles';

const Model = ({ clickedResponse, responseBreak }) => {

  let keys = [];

  if (clickedResponse.length > 0) {
    keys = Object.keys(clickedResponse[0]);
    for (let i = 0; i < keys.length; i++) {
      for (let j = 0; j < responseBreak.length; j++) {
        if (keys[i].substring(1) === responseBreak[j].value) {
          keys[i] = responseBreak[j].name;
        }
      }
    }
  }

  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));

  const classes = useStyles();

  return (
    <TableContainer className={classes.container}>
      <Table stickyHeader aria-label="sticky table" size="small">
        <TableHead>
          <TableRow>
            <TableCell>Agent</TableCell>
            <TableCell>Logout</TableCell>
            <TableCell>{keys[2]}</TableCell>
            <TableCell>{keys[3]}</TableCell>
            <TableCell>{keys[4]}</TableCell>
            <TableCell>{keys[5]}</TableCell>
            <TableCell>{keys[6]}</TableCell>
            <TableCell>{keys[7]}</TableCell>
            <TableCell>{keys[8]}</TableCell>
            <TableCell>{keys[9]}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {clickedResponse.map((row, index) => {
            return (
              <TableRow key={index}>
                <TableCell style={{ minWidth: '150px' }}>{row.agent}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.logout}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8072}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8073}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8074}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8075}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8076}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8077}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8078}</TableCell>
                <TableCell style={{ minWidth: '150px' }}>{row.m8079}</TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
export default Model;
