import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import React from 'react';
import DashboardTop5AnsweredCalls from './DashboardTop5AnsweredCalls';
import DashboardTop5Inbound from './DashboardTop5Inbound';
import DashboardTop5Outbound from './DashboardTop5Outbound';
import DashboardTop5TalkTime from './DashboardTop5TalkTime';

export default function DashboardTops() {
  return (
    <div>
      <GridContainer>
        <Grid item md={3}>
          <DashboardTop5TalkTime />
        </Grid>
        <Grid item md={3}>
          <DashboardTop5AnsweredCalls />
        </Grid>
        <Grid item md={3}>
          <DashboardTop5Inbound />
        </Grid>
        <Grid item md={3}>
          <DashboardTop5Outbound />
        </Grid>
      </GridContainer>
    </div>
  );
}
