import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import CallLogFilter from './Filter';
import CallLogTable from './Table';

export default function CallLog() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'calllog'} />, isActive: true },
  ];

  let [data, setData] = useState([]);
  const [count, setCount] = useState(10);
  const [totalData, setTotalData] = useState([]);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [responseTotalSummary, setResponseTotalSummary] = useState([]);

  return (
    <div>
      <PageContainer heading={<IntlMessages id={'calllog'} />} breadcrumbs={breadcrumbs}>
        <CallLogFilter
          setData={setData}
          limit={limit}
          offset={offset}
          setPage={setPage}
          setTotalData={setTotalData}
          setCount={setCount}
          setResponseTotalSummary={setResponseTotalSummary}
        />
        <CallLogTable
          count={count}
          totalData={totalData}
          responseTotalSummary={responseTotalSummary}
          data={data}
          setLimit={setLimit}
          setOffset={setOffset}
          setPage={setPage}
          limit={limit}
          offset={offset}
          page={page}
        />
      </PageContainer>
    </div>
  );
}
