import React, { useState, useEffect } from 'react';
import GridContainer from '../../../@jumbo/components/GridContainer';
import { makeStyles } from '@material-ui/styles';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '../../../@jumbo/utils/IntlMessages';
import Grid from '@material-ui/core/Grid';
import MissedCalls from './MissedCalls';
import InboundRules from './InboundRules';
import HeaderChartComponent from './HeaderChartComponent';
import OutboundCall from './OutboundCall';
import Extensions from './Extensions';
import * as api from '../../../api/api';
import Trunks from './TrunksUp';
import ActiveCallCount from './ActiveCallCount';
import { useHistory } from 'react-router';
import { Logout } from 'services/auth/logout';
import DashboardTops from './Tops';
import { getNewDate } from '@jumbo/utils/dateHelper';

const breadcrumbs = [
  { label: <IntlMessages id={'sidebar.main'} />, link: '/' },
  { label: <IntlMessages id={'sidebar.dashboard'} />, isActive: true },
];

const useStyles = makeStyles(theme => ({
  orderLg1: {
    [theme.breakpoints.up('lg')]: {
      order: 1,
    },
  },
}));

const SamplePage = () => {
  let history = useHistory();

  const classes = useStyles();
  let date = new Date();

  let totalInbound = 0;
  let totalOutbound = 0;
  let totalMissed = 0;
  const [param, setParam] = useState(date.getDate());
  const [inboundcalls, setInbound] = useState([]);
  const [outboundcalls, setOutbound] = useState([]);
  const [missedcalls, setMissedCalls] = useState([]);

  const convertToLocalTime = date_ => {
    var date = new Date(date_);
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var dt = date.getDate();

    if (dt < 10) {
      dt = '0' + dt;
    }
    if (month < 10) {
      month = '0' + month;
    }

    return year + '-' + month + '-' + dt;
  };
  const sort = array => {
    return array.sort((a, b) => {
      let da = new Date(a.start_time.split('-')[1] + '/' + a.start_time.split('-')[0] + '/' + a.start_time.split('-')[2]);
      let db = new Date(b.start_time.split('-')[1] + '/' + b.start_time.split('-')[0] + '/' + b.start_time.split('-')[2]);
      return da - db;
    });
  };
  if (missedcalls.length > 0) {
    for (let i = 0; i < missedcalls.length; i++) {
      totalMissed += parseInt(missedcalls[i].count);
    }
  }

  if (outboundcalls.length > 0) {
    for (let i = 0; i < outboundcalls.length; i++) {
      totalOutbound += parseInt(outboundcalls[i].count);
    }
  }

  if (inboundcalls.length > 0) {
    for (let i = 0; i < inboundcalls.length; i++) {
      totalInbound += parseInt(inboundcalls[i].count);
    }
  }

  useEffect(() => {
    api
      .getMissedCalls(param - 1)
      .then(resp => setMissedCalls(JSON.parse(resp.data.data)))
      .catch(resp => {
        if (resp.response?.status === 401) {
          Logout();
          history.push('/signin');
        }
      });
    api
      .getOutboundCalls(param - 1)
      .then(resp => setOutbound(JSON.parse(resp.data.data)))
      .catch(resp => {
        if (resp.response?.status === 401) {
          Logout();
          history.push('/signin');
        }
      });
    api
      .getInboundCalls(param - 1)
      .then(resp => {
        setInbound(JSON.parse(resp.data.data));
      })
      .catch(resp => {
        if (resp.response?.status === 401) {
          Logout();
          history.push('/signin');
        }
      });
  }, [param, history]);

  const inboundObject = inboundcalls.map(data => ({
    start_time: data.start_time,
    count: data.count,
  }));

  const outboundObject = outboundcalls.map(data => ({
    start_time: data.start_time,
    count: data.count,
  }));

  const missedObject = missedcalls.map(data => ({
    start_time: data.times,
    count: data.count,
  }));

  for (let d = new Date(date.getFullYear(), date.getMonth(), 1); d <= date; d.setDate(d.getDate() + 1)) {
    if (!inboundObject.some(data => data.start_time === convertToLocalTime(d))) {
      inboundObject.push({ start_time: convertToLocalTime(d), count: '0' });
    }
    if (!outboundObject.some(data => data.start_time === convertToLocalTime(d))) {
      outboundObject.push({ start_time: convertToLocalTime(d), count: '0' });
    }
    if (!missedObject.some(data => data.start_time === convertToLocalTime(d))) {
      missedObject.push({ start_time: convertToLocalTime(d), count: '0' });
    }
  }

  const fetchData = array => {
    const arr = sort(array);
    if (param <= 7) {
      return arr.slice(Math.max(arr.length - param, 0));
    } else {
      return arr;
    }
  };

  for (let i = 0; i < inboundObject.length; i++) {
    inboundObject[i].start_time = getNewDate(inboundObject[i].start_time);
  }

  for (let i = 0; i < outboundObject.length; i++) {
    outboundObject[i].start_time = getNewDate(outboundObject[i].start_time);
  }
  for (let i = 0; i < missedObject.length; i++) {
    missedObject[i].start_time = getNewDate(missedObject[i].start_time);
  }

  return (
    <PageContainer heading={<IntlMessages id="sidebar.dashboard" />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12} sm={4} md={4}>
          <InboundRules setParam={setParam} date={date} data={fetchData(inboundObject)} total={totalInbound} />
        </Grid>
        <Grid item xs={12} sm={4} md={4}>
          <OutboundCall setParam={setParam} date={date} data={fetchData(outboundObject)} total={totalOutbound} />
        </Grid>
        <Grid item xs={12} sm={4} md={4}>
          <MissedCalls setParam={setParam} date={date} data={fetchData(missedObject)} total={totalMissed} />
        </Grid>
      </GridContainer>
      <Grid item xs={12} xl={12} className={classes.orderLg1}>
        <GridContainer>
          <Grid item xs={12} sm={4} md={4}>
            <ActiveCallCount />
          </Grid>
          <Grid item xs={12} sm={4} md={4}>
            <Extensions />
          </Grid>
          <Grid item xs={12} sm={4} md={4}>
            <Trunks />
          </Grid>
          <Grid item xs={12}>
            <DashboardTops />
          </Grid>
          <Grid item xs={12}>
            <HeaderChartComponent
              date={date}
              setParam={setParam}
              inbound={fetchData(inboundObject)}
              missed={fetchData(missedObject)}
              outbound={fetchData(outboundObject)}
            />
          </Grid>
        </GridContainer>
      </Grid>
    </PageContainer>
  );
};

export default SamplePage;
