import axios from 'axios';
import CryptoJS from 'crypto-js';
const validationUrl = window.appsettings.validationUrl || process.env.REACT_APP_VALIDATION_URL;
const url = window.appsettings.appUrl || process.env.REACT_APP_URL;
const port = window.appsettings.appPort || process.env.REACT_APP_PORT;

const date = new Date();
const day = date.getDate();
const month = date.getMonth() + 1;
const year = date.getFullYear();

const addZero = val => {
  if (val < 0) {
    return '0' + val;
  } else {
    return val;
  }
};
const param = year + '-' + addZero(month) + '-' + addZero(day);
export const getOffset = async () => {
  return await axios.get(`http://${url}:${port}/zone`);
};
const timeOffset = localStorage.getItem('timeoffset') || '+3';

export const getInboundCalls = async param => {
  let query = `SELECT count (distinct(call_id)),
  to_char(start_time::timestamp::date , 'yyyy-mm-dd') as start_time
  FROM cl_segments_view_new
  where  (date_trunc('day', NOW() - INTERVAL '${param} day') <= start_time) 
  and act != 1 
  and dst_dn_type = 0
  and src_dn_type = 1
  group by start_time::timestamp::date
  order by start_time::timestamp::date asc`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};
