export const getDidNumbers = async () => {
  let query = `
  select 
  substring(conditiondata from '[0-9]+')  as conditiondata
  from inboundrule
  where conditiondata != '' 
  and substring(conditiondata from '[0-9]+') is not null
  order by substring(conditiondata from '[0-9]+') asc`;

  return await axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

