export const getRepetetiveCallerDetails = async (param, callerNumber) => {
  const query = `SELECT 
  src_display_name,
  src_did,
  dst_display_name,
  to_char(dst_end_time-dst_answer_time ,'HH24h MIm SSs')as talkingtime,
  to_char(dst_answer_time-dst_start_time,'HH24h MIm SSs') as waitingtime
  FROM  cl_segments_view_new as t
  where dst_end_time - dst_answer_time != interval '00:00:00' 
  and src_dn_type = 1
  and (date_trunc('day', timestamp '${param.paramStart}' - INTERVAL '0 day') <= start_time) 
  and (date_trunc('day', timestamp '${param.paramFinish}' + INTERVAL '1 day') >=  start_time) 
  and src_display_name = '${callerNumber}'`;

  return axios.post(`http://${url}:${port}/queryresult`, { token: localStorage.getItem('token'), query: query });
};

export const getReturnedDropCall = async (limit, offset, agNum, qNum) => {
  const string = `with recursive dropped as (
    select 
    *
    from callcent_queuecalls_view
    where from_userpart != ''
    and(date_trunc('day', NOW() + INTERVAL '1 day') >=  callcent_queuecalls_view.time_start)
    and (date_trunc('day', NOW() - INTERVAL '0 day') <= callcent_queuecalls_view.time_start)
    and callcent_queuecalls_view.is_answered = false  
  )
  select
  cl_segments_view_new.src_dn,
  cl_segments_view_new.src_display_name,
  to_char(cl_segments_view_new.dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as dst_start_time,
  to_char(cl_segments_view_new.dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as dst_end_time,
  cl_segments_view_new.dst_dn,
  dropped.q_num,
  to_char(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_start_time,'HH24:MI:SS') as duration
  from dropped
  left outer join cl_segments_view_new on (dropped.from_userpart = cl_segments_view_new.dst_dn)
  where cl_segments_view_new.dst_start_time > dropped.time_start
  and cl_segments_view_new.src_dn like '%${agNum}'
  and dropped.q_num like '%${qNum}'
  limit ${limit} offset ${offset}`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};
export const getCdr = async string => {
  const response = await axios.get(
    `http://${url}:${port}/api/usersview/?string=` + encodeURIComponent(CryptoJS.AES.encrypt(string, 'Secret Key')),
  );
  return response;
};

export const getMontlyLog = async (paramMonth, paramBack) => {
  const query = `SELECT 
    DATE_TRUNC('month',start_time),
    COUNT (distinct(call_id)) as toplam
    FROM cl_segments_view_new
    where (date_trunc('month', NOW() - INTERVAL '${paramBack + paramMonth - 1} month') <= start_time) 
    and  date_trunc('month',now() - interval '${paramMonth - 2} month') >= start_time
    GROUP BY DATE_TRUNC('month',start_time)
    ORDER BY DATE_TRUNC('month',start_time) desc`;
  return await axios.post(`http://${url}:${port}/queryresult`, { token: localStorage.getItem('token'), query: query });
};
export const getCallComparison = async (paramDate, paramBack) => {
  const string =
    "select count (distinct(call_id)) , cast(sum(end_time-start_time) as text) as total, to_char(date_trunc('hour',  start_time at time zone 'utc' at time zone 'Etc/GMT" +
    timeOffset +
    "') , 'yyyy-mm-dd HH24:MI:ss') as day_log,date_trunc('second' ,sum(end_time-start_time)/count (distinct(call_id))) as act from cl_segments_view_new where (date_trunc('day',  TIMESTAMP '" +
    paramDate +
    "') + '- " +
    paramBack +
    " day' <= start_time) and (date_trunc('day',  TIMESTAMP '" +
    paramDate +
    "')  + '1 day' >= start_time)  and act != 1 and start_time < end_time group by date_trunc('hour',  start_time at time zone 'utc' at time zone 'Etc/GMT" +
    timeOffset +
    "') order by date_trunc('hour',  start_time at time zone 'utc' at time zone 'Etc/GMT" +
    timeOffset +
    "') desc";
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

export const getCallComparisonDaily = async (paramDate, paramBack) => {
  const string = `select count (distinct(call_id)),
   sum(end_time-start_time) as total,
   to_char(date_trunc('day',  start_time) , 'yyyy-mm-dd') as day_log,
   date_trunc('second' ,sum(end_time-start_time)/count (distinct(call_id))) as act
   from cl_segments_view_new
   where (date_trunc('day',  TIMESTAMP '${paramDate}') + '-${paramBack} day' <= start_time) 
   and (date_trunc('day',  TIMESTAMP '${paramDate}')  + '1 day' >= start_time)  
   and act != 1 
   and start_time < end_time
   group by date_trunc('day',  start_time) 
   order by date_trunc('day',  start_time) desc`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

