export const getAbandonedQueueCallsDetails = async (params, queue) => {
  const query = `SELECT 
	date_trunc('day' , callcent_ag_dropped_calls.time_start) as day_abandoned,
	count(*) as cnt,
	sum(callcent_ag_dropped_calls.time_end - callcent_ag_dropped_calls.time_start) as total_waiting,
	to_char(sum(callcent_ag_dropped_calls.time_end - callcent_ag_dropped_calls.time_start)/count(*),'HH24:MI:SS') as act
	FROM 
	callcent_ag_dropped_calls 
	inner join callcent_queuecalls on callcent_ag_dropped_calls.q_call_history_id = callcent_queuecalls.call_history_id 
	where  callcent_ag_dropped_calls.time_start >=  timestamp '${params.startDate}'
	and callcent_ag_dropped_calls.time_start <=  timestamp '${params.endDate}' + interval '1 day' 
	and callcent_ag_dropped_calls.reason_noanswerdesc = 'Cancelled'
  and callcent_ag_dropped_calls.q_num like '%${queue}'
	group by (date_trunc('day' , callcent_ag_dropped_calls.time_start))
	order by (date_trunc('day' , callcent_ag_dropped_calls.time_start)) asc`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

export const getAbandonedQueueCallsDetailsTotal = async (params, queue) => {
  const query = `with recursive cte1 as (
    SELECT 
	date_trunc('day' , callcent_ag_dropped_calls.time_start) as day_abandoned,
	count(*) as cnt,
	sum(callcent_ag_dropped_calls.time_end - callcent_ag_dropped_calls.time_start) as total_waiting,
	sum(callcent_ag_dropped_calls.time_end - callcent_ag_dropped_calls.time_start)/count(*) as act
	FROM 
	callcent_ag_dropped_calls 
	inner join callcent_queuecalls on callcent_ag_dropped_calls.q_call_history_id = callcent_queuecalls.call_history_id 
	where  callcent_ag_dropped_calls.time_start >= timestamp '${params.startDate}'
	and callcent_ag_dropped_calls.time_start <=  timestamp '${params.endDate}' + interval '1 day' 
	and callcent_ag_dropped_calls.reason_noanswerdesc = 'Cancelled'
  and callcent_ag_dropped_calls.q_num like '%${queue}'
	group by (date_trunc('day' , callcent_ag_dropped_calls.time_start))
)
select
count(cte1.day_abandoned) as day_count,
sum(cte1.cnt) as totalcount,
sum(cte1.total_waiting) as totalwaiting,
to_char(sum(cte1.act)/count(cte1.day_abandoned),'HH24:MI:SS') as act
from cte1`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

