export const getAbondenedCallDetails = async (params, from_userpart) => {
  const query = `SELECT '<--' as direction,src_dn,to_char(dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') AS dst_start_time,to_char(dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as dst_end_time,dst_end_time - dst_start_time as calltime from cl_segments_view_new where dst_dn like '%${from_userpart}'
  and (date_trunc('second', timestamp '${params}') <=  start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}') 
    union 
    SELECT  '-->' as direction, 
    from_userpart,
    to_char(time_start at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as time_start,
    to_char(time_end at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as time_end,
    time_end - time_start as calltime 
    from callcent_queuecalls_view where from_userpart like '%${from_userpart}'
    and 
    is_answered = true 
    and (date_trunc('second', timestamp '${params}') <=  time_start at time zone 'Etc/GMT${timeOffset}') 
`;
  return await axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

