export const getAgentStatus = async pass => {
  let query = `WITH RECURSIVE cte1 as (
    select 
    dn.value,
    ivr.name 
    from 
    dn 
    inner join ivr on dn.iddn = ivr.fkiddn
    ) , 
    cte2 as (
    select
    * from callcent_ag_qstatus
    where timeofupdate > date_trunc('day', timestamp '${pass}')
    )
  select
  dn,
  display_name,
  coalesce((
  select 
  case 
  when cte2.is_loggedin_in_queue = 0 then 'Login' 
  when cte2.is_loggedin_in_queue = 1 then 'Logout'
  else cte1.name
  end as status
  from cte2
  left outer join cte1 on cte1.value = cast(cte2.is_loggedin_in_queue as character varying)
  where ag_num = t.dn
  order by cte2.timeofupdate desc
  limit 1  
  ),'State') as states,
  date_trunc('second',coalesce(now()- (
  select 
  timeofupdate
  from cte2
  where ag_num = t.dn
  order by cte2.timeofupdate desc
  limit 1
  ),'10000 hours')) as statusTime
  from users_view t
  order by dn asc`;
  return axios.post(`http://${url}:${port}/queryresult`, { token: localStorage.getItem('token'), query: query });
};
