export const getDailyCallDetails = async mydate => {
  let query = `WITH RECURSIVE cte1 AS (
    select 
    * 
    from cl_segments_view_new
    where 
    start_time > date_trunc('day', timestamp '${mydate}')
    and
	  start_time<end_time
    order by start_time desc
    ), cte2 as (
    select 
    (select count (distinct call_id) from cte1) as total_call,
    (select count (distinct call_id) from cte1 where src_dn_type = 1 and dst_dn_type = 0) as inbound_calls,
    (select count (distinct call_id) from cte1 where src_dn_type = 0 and dst_dn_type = 1) as outbound_calls,
    (select count (distinct call_id) from cte1 where src_dn_type = 0 and dst_dn_type = 0) as agent_to_agent_calls,
    (select count (distinct call_id) from cte1 inner join cte3 on cte1.dst_dn = cte3.dn where src_dn_type = 1 and dst_dn_type != 0) as queue_calls,
    (select count (distinct call_id) from cte1 where src_dn_type = 1 and dst_dn_type = 0 and (act = 5 or act = 6)) as answered_inbound_calls,
    (select sum(end_time-start_time) as totaltime from cte1),
    (select sum(end_time-start_time) as inbound_totaltime from cte1 where src_dn_type = 1 and dst_dn_type = 0),
    (select sum(end_time-start_time) as outbound_totaltime from cte1 where src_dn_type = 0 and dst_dn_type = 1),
    (select sum(end_time-start_time) as agent_to_agent_totaltime from cte1 where src_dn_type = 0 and dst_dn_type = 0),
    (select sum(end_time-start_time) as queue_totaltime from cte1 where src_dn_type = 0 and dst_dn_type != 0),
    (select (date_trunc('second',(date_trunc('second', sum(end_time-start_time))/( select count (distinct cte1.call_id) from cte1 ))))as average from cte1)
	), cte3 as (SELECT * FROM public.queue_view order by dn asc)
	select * from cte2`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

