export const getBreakName = async () => {
  let digits = (localStorage.getItem('digits') || 4).toString();
  let min = '8072';
  let max = '8079';
  switch (digits) {
    case '4':
      min = '8072';
      max = '8079';
      break;
    case '3':
      min = '072';
      max = '079';
      break;
    case '2':
      min = '72';
      max = '79';
      break;
    default:
      min = '8072';
      max = '8079';
      break;
  }
  let query = `select 
  dn.value,
  ivr.name,
  dn.iddn 
  from dn 
  inner join ivr on dn.iddn = ivr.fkiddn
  where cast(substring(dn.value from '[0-9]+') as bigint) >= ${min} 
  and cast(substring(dn.value from '[0-9]+') as bigint) <= ${max}
  order by dn.value asc`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};

