export const getQueueAnswerReport = async (startDate, endDate, queueNum, src, order, limit, offset) => {
  let string = `with recursive cte1 as (
    select 
    call_id,
    src_display_name,
    dst_answer_time-dst_start_time as waiting_time,
    dst_end_time-dst_answer_time  as talking_time,
    dst_dn,
    dst_start_time,
    dst_display_name
    from cl_segments_view_new
    inner join cte2 on cl_segments_view_new.dst_dn = cte2.dn
    where
    dst_start_time >= date_trunc('day',timestamp '${startDate}')
    and
    dst_start_time<=date_trunc('day', timestamp '${endDate}' + interval '1 day')
    and
    src_dn_type != 0
    and
    dst_dn_type != 0
    and
    act = 10
    and
    dst_dn  like '${queueNum}%'
  ), cte2 as (SELECT * FROM public.queue_view order by dn asc)
  select 
  to_char(cte1.dst_start_time,'yyyy-mm-dd') as dst_start_time,
  sum(cte1.talking_time+(cl_segments_view_new.dst_answer_time-cl_segments_view_new.dst_start_time)) as waiting_time,
  sum(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_answer_time) as talking,
  cte1.src_display_name,
  cte1.dst_dn
  from
  cl_segments_view_new
  left outer join cte1 on cl_segments_view_new.call_id = cte1.call_id
  where
  cl_segments_view_new.dst_start_time >= date_trunc('day',timestamp '${startDate}')
  and
  cl_segments_view_new.dst_start_time<=date_trunc('day', timestamp '${endDate}' + interval '1 day')
  and
  src_dn_type != 0
  and
  dst_dn_type = 0
  and
  cte1.src_display_name like '${src}%'
  and
  dst_answer_time is not null
  and
  cl_segments_view_new.call_id = cte1.call_id
  group by cte1.dst_dn,cte1.src_display_name,cte1.dst_start_time
  ORDER BY cte1.dst_start_time ${order}
  limit ${limit} offset ${offset}`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

