export const getQueueAnswerReportSummary = async (startDate, endDate, queueNum, src, order, limit, offset) => {
  let string = `    
    with recursive cte1 as (
    select 
    call_id,
    src_display_name,
    dst_answer_time-dst_start_time as waiting_time,
    dst_end_time-dst_answer_time  as talking_time,
    dst_dn,
    dst_start_time,
    dst_display_name,
	  src_did
    from cl_segments_view_new
    where
    dst_start_time >= date_trunc('day',timestamp '${startDate}')
    and
    dst_start_time<=date_trunc('day', timestamp '${endDate}' + interval '1 day')
    and
    src_dn_type != 0
    and
    dst_dn_type != 0
    and
    act = 10
    and
    dst_dn  like '%${queueNum}'
	and
	src_did != ''
	and
	dst_dn_type != 6
  )
  select
  'Total' as total,
  to_char(date_trunc('day' , cte1.dst_start_time), 'dd-mm-yyyy') as date,
  count(distinct(cte1.call_id)),
  to_char(sum(cte1.talking_time+(cl_segments_view_new.dst_answer_time-cl_segments_view_new.dst_start_time)),'HH24h MIm SSs') as waiting_time,
  to_char(sum(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_answer_time),'HH24h MIm SSs') as talking,
  to_char(sum(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_answer_time)/count(distinct(cte1.call_id)),'HH24h MIm SSs') as act,
  to_char(sum(cl_segments_view_new.dst_answer_time-cl_segments_view_new.dst_start_time)/count(distinct(cte1.call_id)),'HH24h MIm SSs') as waiting_act
  from
  cl_segments_view_new
  left outer join cte1 on cl_segments_view_new.call_id = cte1.call_id
  where
  src_dn_type != 0
  and
  dst_dn_type = 0
  and
  cte1.src_display_name like '%'
  and
  dst_answer_time is not null
  group by date_trunc('day' , cte1.dst_start_time)
  ORDER BY date_trunc('day' , cte1.dst_start_time)  ASC
  `;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

