export const getInternalsSummary = async (startTime, endTime, agentNum) => {
  let string = `with recursive age as (
    select
    dn,
    display_name
    from
    users_view
  ), inb as (
    select 
    dst_dn,
    count(distinct(call_id)) as cntInbound
    from
    cl_segments_view_new
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and dst_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
    group by dst_dn
  ),outb as (
    select
    src_dn,
    count(distinct(call_id)) as cntOutbound
    from
    cl_segments_view_new
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and src_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
    group by src_dn
  ), answerInb as (
    select 
    dst_dn,
    count(distinct(call_id)) as ansInbound
    from
    cl_segments_view_new
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and dst_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
    and dst_answer_time is not null
    group by dst_dn	
  ), answerOutb as (
    select
    src_dn,
    count(distinct(call_id)) as ansOutbound
    from
    cl_segments_view_new
    where start_time >= date_trunc('day',timestamp '${startTime}')
    and src_dn_type = 0
    and start_time <= date_trunc('day',timestamp '${endTime}' + interval '1 day')
    and dst_answer_time is not null
    group by src_dn
  )
  
  select
    age.dn as id,
    age.display_name,
    coalesce(inb.cntInbound,0) as total_inbound,
    coalesce(outb.cntOutbound,0) as total_outbound,
    coalesce(answerInb.ansInbound,0) as answered_inbound,
    coalesce(answerOutb.ansOutbound,0) as answered_outbound
    from age
    left outer join inb on inb.dst_dn = age.dn
    left outer join outb on outb.src_dn = age.dn 
    left outer join answerInb on answerInb.dst_dn = age.dn
    left outer join answerOutb on answerOutb.src_dn = age.dn
    where age.dn like '%${agentNum}'`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

