export const getCallDetail = async callid => {
  /* OLD
with recursive cte1 as (
    select
    call_id,
    dst_start_time,
    dst_answer_time,
    dst_end_time,
    src_display_name,
    dst_display_name,
    act,
    act_dn_type,
    src_did,
    start_time,
    end_time,
    src_dn_type,
    src_dn,
    dst_dn_type
    from
    cl_segments_view_new
    where call_id = '${callid}'
    and act != 1
    )
    select
  */
  let string = `with recursive cte1 as (
    select
    call_id,
    dst_start_time,
    coalesce(dst_answer_time,dst_end_time) as dst_answer_time,
    dst_end_time,
    src_display_name,
    dst_display_name,
    act,
    act_dn_type,
    src_did,
    start_time,
    end_time,
    src_dn_type,
    src_dn,
    dst_dn_type
    from
    cl_segments_view_new
    where call_id = '${callid}'
    and act != 1
    order by seg_order
    )
    select
    to_char(dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','yyyy-mm-dd HH24:MI:SS') as start_time,
    to_char(dst_answer_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}'-dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','HH24h MIm SSs') as ring_time,
    to_char(dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}'-dst_answer_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','HH24h MIm SSs') as duration,
    src_display_name as callers,
    dst_display_name as calleds,
    case when act = 12 then 'Transfer' when act = 10 and act_dn_type != 0 then 'Kar≈üƒ±lama' when act = 10 and act_dn_type = 0 then 'Y√∂nlendirme' when act_dn_type = 4 then 'A√ßƒ±lƒ±≈ü ' else '-' end as states,
    to_char(dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','yyyy-mm-dd HH24:MI:SS') as end_time
    from cte1
    order by dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}' asc`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

