export const getDailySummaryModalTransferCall = async date => {
  let string = `with recursive cte1 as ( 
    select
    *
    from
    cl_segments_view_new
    where 
    dst_dn_type =0
    and dst_start_time >= timestamp '${date}'
    and dst_end_time <= timestamp '${date}' + interval '1 day'
    and act = 12 and dst_dn_type = 0 
    )
    select
    cl_segments_view_new.call_id,
    cl_segments_view_new.src_display_name as caller,
    cte1.src_display_name as transferer,
    cte1.dst_display_name as trasfered,
    to_char(cl_segments_view_new.dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as start_time,
    to_char(cl_segments_view_new.dst_answer_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as answer_time,
    to_char(cl_segments_view_new.dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as end_time,
    to_char(coalesce(cl_segments_view_new.dst_answer_time-cl_segments_view_new.dst_start_time,'0 second'),'HH24h MIm SSs') as ring_time,
    to_char(coalesce(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_answer_time,'0 second'),'HH24h MIm SSs') as talk_time
    from
    cl_segments_view_new
    inner join cte1 on cte1.call_id = cl_segments_view_new.call_id
    where cl_segments_view_new.dst_start_time >= timestamp '${date}'
    and cl_segments_view_new.dst_start_time <= timestamp '${date}' + interval '1 day'
    and cl_segments_view_new.act != 1
    and cl_segments_view_new.act != 12
    order by cl_segments_view_new.call_id,cl_segments_view_new.dst_start_time asc`;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};
