export const getCallTransferReport = async (agent, selectedStartTime, selectedEndTime) => {
  let string = `select
	dst_display_name as transferer,
	act_display_name as trasfered,
	src_display_name as caller,
	to_char(cl_segments_view_new.dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as start_time,
    to_char(cl_segments_view_new.dst_answer_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as answer_time,
    to_char(cl_segments_view_new.dst_end_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}','dd-mm-yyyy HH24:MI:SS') as end_time,
    to_char(coalesce(cl_segments_view_new.dst_answer_time-cl_segments_view_new.dst_start_time,'0 second'),'HH24h MIm SSs') as ring_time,
    to_char(coalesce(cl_segments_view_new.dst_end_time-cl_segments_view_new.dst_answer_time,'0 second'),'HH24h MIm SSs') as talk_time
    from
    cl_segments_view_new
    where 
    src_dn_type = 1
    and 
	dst_start_time >= timestamp '${selectedStartTime}'
    and 
	dst_end_time <= timestamp '${selectedEndTime}' + interval '1 day' 
    and 
	dst_dn_type = 0 
	and 
	dst_answer_time is not null
	and
	act = 10 -- transfer
	and
	act_dn_type != 8 -- not parked
	and
	start_time != end_time
	and
	dst_display_name like '%${agent}%'
	order by call_id,seg_order`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

