export const TagByID = async (tag, id) => {
  const query = `UPDATE tag SET tagtext='${tag}' WHERE call_id=${id};
  INSERT INTO tag (call_id, tagtext)
         SELECT ${id}, '${tag}'
         WHERE NOT EXISTS (SELECT 1 FROM tag WHERE call_id=${id});`;

  const response = axios.post(
    `http://${url}:${port}/insertpostgres`,
    { token: localStorage.getItem('token'), query: query },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );
  return response.data;
};
