export const getDailyActiveCalls = async paramDate => {
  //  acc.action  at time zone 'Etc/GMT${timeOffset}' ,
  //doƒürusu yukarƒ±daki gibi ama 3 saat ilerde getiriyor. nedenine daha sonra bakƒ±lacaktƒ±r
  let string = `select 
  acc.action  at time zone 'utc',
  acc.acc
  from acc 
  where 
  acc.action  > timestamp '${paramDate} 00:00:00' at time zone 'utc' at time zone 'Etc/GMT${timeOffset}'
  and
  acc.action  < timestamp '${paramDate} 23:59:59'  at time zone 'utc' at time zone 'Etc/GMT${timeOffset}'
  order by acc.action asc`;
  return axios.post(`http://${url}:${port}/querypostgres`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

export const getTerminatedCallsByAgent = async (start_time, end_time, limit, offset, callTerminator) => {
  let query = `SELECT * FROM public.cl_segments_view_new
  where
  start_time > '${start_time}'
  and
  end_time < '${end_time}'
  and
  (
  (
  act = 6
  and
  cast(dst_dn_type as text) like '%${callTerminator}'
  )
  or
  (
  act = 5
  and
  cast(src_dn_type as text) like '%${callTerminator}'
  )
  )
  order by call_id, seg_order
  limit ${limit} offset ${offset}`;
  return await axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query,
  });
};
