import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import DataTable from './Table';
import Chart from './Chart';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';

const DailyLog = () => {
  const [response, setResponse] = useState([]);
  const [responseTable, setResponseTable] = useState([]);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'dailylog'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'dailylog'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12} md={12} sm={12} lg={12}>
          <Filter setResponse={setResponse} setResponseTable={setResponseTable} />
        </Grid>
        <Grid item xs={12} md={6} sm={12} lg={6}>
          <DataTable response={response} responseTable={responseTable} />
        </Grid>
        <Grid item xs={12} md={6} sm={12} lg={6}>
          <Chart response={response} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default DailyLog;
