import { Cell, Pie, PieChart, Tooltip } from 'recharts';
import React from 'react';
import Box from '@material-ui/core/Box';
import makeStyles from '@material-ui/core/styles/makeStyles';

const useStyles = makeStyles(theme => ({
  customTooltip: {
    color: theme.palette.text.primary,
    backgroundColor: theme.palette.background.paper,
    padding: 5,
  },
}));
const formatDate = date => {
  let d = new Date(date);
  let month = '' + (d.getMonth() + 1);
  let day = '' + d.getDate();
  let year = d.getFullYear();
  let hour = d.getHours();
  let min = d.getMinutes();

  if (month < 10) month = '0' + month;
  if (day < 10) day = '0' + day;
  if (hour < 10) hour = '0' + hour;
  if (min < 10) min = '0' + min;

  return year + '-' + month;
};
const ApplicationsGraph = ({ pieData }) => {
  const classes = useStyles();
  const CustomTooltip = ({ active, payload }) => {
    if (active) {
      return (
        <Box className={classes.customTooltip}>
          <label>{`${payload[0].name} : ${payload[0].value}`}</label>
        </Box>
      );
    }
    return null;
  };
  const data = pieData.map(data => ({
    name: formatDate(data.date_trunc),
    value: parseInt(data.toplam),
  }));

  const colors = ['#916BBF', '#C6D57E', '#FFC069', '#F6A9A9', '#5C7AEA', '#FFF7AE'];
  return (
    <PieChart width={194} height={194}>
      <Pie
        data={data}
        color="#000000"
        dataKey="value"
        nameKey="name"
        cx="50%"
        cy="50%"
        isAnimationActive={true}
        outerRadius={95}
        fill="#8884d8">
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
        ))}
      </Pie>
      <Tooltip content={<CustomTooltip />} />
    </PieChart>
  );
};

export default ApplicationsGraph;
