import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Paper from '@material-ui/core/Paper';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import DateRangeIcon from '@mui/icons-material/DateRange';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { CloudDownload } from '@material-ui/icons';

const DataTable = ({ response, responseTable }) => {
  let date;
  let totalCall = 0;
  let total = 0;
  let act = 0;

  for (let i = 0; i < responseTable.length; i++) {
    date = responseTable[i].hour_log !== 'undefined' ? responseTable[i].hour_log.split(' ')[0] : '-';
    totalCall += parseInt(responseTable[i].count);
    total = responseTable[i].total;

    act = responseTable[i].act;
  }
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  function createData(date, totalCall, totalMin, act) {
    return { date, totalCall, totalMin, act };
  }
  const classes = useStyles();

  const rows = [createData(date, totalCall, total, act)];

  let currentLanguage = useIntl();

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {rows.length > 0 && (
          <ExcelFile
            filename={
              currentLanguage.locale === 'en-EN'
                ? 'Daily_log_report'
                : currentLanguage.locale === 'tr-TR'
                ? 'Gunluk_yuk_raporu'
                : currentLanguage.locale === 'nl-NL'
                ? 'Dagelijks logboekrapport'
                : currentLanguage.locale === 'de-DE'
                ? 'T√§glicher Protokollbericht'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={rows} name="Data">
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="date"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Call Count'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Arama Adedi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Gesprekstelling'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Anrufz√§hler'
                    : ''
                }
                value="totalCall"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Duration'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'S√ºre'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Looptijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Dauer'
                    : ''
                }
                value="totalMin"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Average Call Time'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Ortalam √áaƒürƒ± S√ºresi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Gemiddelde beltijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliche Anrufzeit'
                    : ''
                }
                value="act"
              />
            </ExcelSheet>
          </ExcelFile>
        )}
      </div>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow style={{ backgroundColor: '#2978B5' }}>
              <TableCell align="center">
                <b style={{ fontFamily: 'Roboto', color: 'white' }}>
                  <IntlMessages id={'filter.menu.date'} />
                </b>
              </TableCell>
              <TableCell align="center">
                <b style={{ fontFamily: 'Roboto', color: 'white' }}>
                  <IntlMessages id={'call.time'} />
                </b>
              </TableCell>
              <TableCell align="center">
                <b style={{ fontFamily: 'Roboto', color: 'white' }}>
                  <IntlMessages id={'Duration'} />
                </b>
              </TableCell>
              <TableCell align="center">
                <b style={{ fontFamily: 'Roboto', color: 'white' }}>
                  <IntlMessages id={'Act'} />
                </b>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={index}>
                <TableCell align="center">
                  <DateRangeIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
                  <b>{row.date !== undefined ? row.date : '-'}</b>
                </TableCell>
                <TableCell align="center">
                  <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{row.totalCall}</b>
                </TableCell>
                <TableCell align="center" style={{ color: '#FF87CA' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                  <b>
                    {(row.totalMin.length > 0
                      ? row.totalMin.split(':')[0].length > 2
                        ? parseInt(row.totalMin?.split(':')[0].split('.')[0]) * 24 +
                          parseInt(row.totalMin?.split(':')[0].split('.')[1]).toString() +
                          ':' +
                          row.totalMin.split(':')[1] +
                          ':' +
                          row.totalMin.split(':')[2]
                        : row.totalMin
                      : row.totalMin
                    )
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </b>
                </TableCell>
                <TableCell align="center" style={{ color: '#FF87CA' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px' }} />{' '}
                  <b>
                    {row.act
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </b>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataTable;
