import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import React, { useState } from 'react';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import Filter from './Filter';
import DataTable from './Table';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { CloudDownload } from '@material-ui/icons';

export default function QueueAnswerReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'queue.answer.report'} />, isActive: true },
  ];

  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  let [data, setData] = useState([]);

  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [summary, setSummary] = useState([]);
  const [summaryTotal, setSummaryTotal] = useState([]);
  const [responseCount, setResponseCount] = useState(10);

  let classes = useStyles();

  let curentLanguage = useIntl();

  return (
    <PageContainer heading={<IntlMessages id={'queue.answer.report'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Filter
            setData={setData}
            rowsPerPage={rowsPerPage}
            page={page}
            setSummary={setSummary}
            setSummaryTotal={setSummaryTotal}
            setResponseCount={setResponseCount}
          />
        </Grid>
        <Grid item xs={12}>
          <DataTable
            data={data}
            setData={setData}
            rowsPerPage={rowsPerPage}
            setRowsPerPage={setRowsPerPage}
            page={page}
            setPage={setPage}
            summaryTotal={summaryTotal}
            responseCount={responseCount}
          />
        </Grid>

        <Grid item xs={12}>
          {summaryTotal.length > 0 && (
            <div style={{ marginTop: '10px', textAlign: 'right' }}>
              <ExcelFile
                filename={`Queue Answer Report Summary`}
                element={
                  <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                    <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                  </Button>
                }>
                <ExcelSheet data={summary} name="data">
                  <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Date' : 'Tarih'} value="date" />
                  <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Call Count' : 'Arama Sayƒ±sƒ±'} value="count" />
                  <ExcelColumn
                    label={curentLanguage.locale === 'en-US' ? 'Waiting Time' : 'Bekleme S√ºresi'}
                    value="waiting_time"
                  />
                  <ExcelColumn
                    label={curentLanguage.locale === 'en-US' ? 'Talking Time' : 'Konu≈üma S√ºresi'}
                    value="talking"
                  />
                  <ExcelColumn
                    label={curentLanguage.locale === 'en-US' ? 'Average Call Time' : 'Ortalama G√∂r√º≈üme S√ºresi'}
                    value="act"
                  />
                </ExcelSheet>
              </ExcelFile>
            </div>
          )}
          <TableContainer component={Paper} className={classes.root}>
            {summary.length > 0 && (
              <Table className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'queueanswerreport.date'} />
                    </TableCell>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'wallboard.total.call'} />
                    </TableCell>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'waiting.time'} />
                    </TableCell>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'queueanswerreport.talkingtime'} />
                    </TableCell>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'wallboard.average.time'} />
                    </TableCell>
                    <TableCell style={{ textAlign: 'center' }}>
                      <IntlMessages id={'average.waiting'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {summary.map((sum,index) => (
                    <TableRow key={index}>
                      <TableCell style={{ textAlign: 'center' }}>{sum.date}</TableCell>
                      <TableCell style={{ textAlign: 'center' }}>{sum.count}</TableCell>
                      <TableCell style={{ textAlign: 'center' }}>{sum.waiting_time}</TableCell>
                      <TableCell style={{ textAlign: 'center' }}>{sum.talking}</TableCell>
                      <TableCell style={{ textAlign: 'center' }}>{sum.act}</TableCell>
                      <TableCell style={{ textAlign: 'center' }}>{sum.waiting_act}</TableCell>
                    </TableRow>
                  ))}
                  {summaryTotal.map((row, index) => {
                    return (
                      <TableRow hover role="checkbox" tabIndex={-1} key={index} style={{ backgroundColor: '#80ED99' }}>
                        <TableCell style={{ textAlign: 'center' }}>
                          <IntlMessages id={'total'} />
                        </TableCell>
                        <TableCell style={{ textAlign: 'center' }}>{row.count1}</TableCell>
                        <TableCell style={{ textAlign: 'center' }}>{row.waiting_time}</TableCell>
                        <TableCell style={{ textAlign: 'center' }}>{row.talking}</TableCell>
                        <TableCell style={{ textAlign: 'center' }}>{row.act}</TableCell>
                        <TableCell style={{ textAlign: 'center' }}>{row.waiting_act}</TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            )}
          </TableContainer>
        </Grid>
      </GridContainer>
    </PageContainer>
  );
}
