import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, TextField } from '@mui/material';
import { setBreakName } from 'api/api';
import React, { useState } from 'react';

export default function BreaksChangeBreakNameModal({ id, selectedBreakName, handleBreakNameModalClose }) {
  let [newName, setNewName] = useState(selectedBreakName);
  const handleSubmit = () => {
    setBreakName(newName, id).then(rep => {
      handleBreakNameModalClose();
    });
  };
  return (
    <div style={{ textAlign: 'center' }}>
      <TextField
        label={<IntlMessages id={'edit.breaks.breakname'} />}
        variant="outlined"
        value={newName}
        onChange={event => setNewName(event.target.value)}
      />
      <div style={{ marginTop: '10px' }}>
        <Button variant="contained" onClick={() => handleSubmit()}>
          <IntlMessages id={'edit.breaks.change'} />
        </Button>
        <Button variant="contained" style={{ marginLeft: '10px' }} onClick={() => handleBreakNameModalClose()}>
          <IntlMessages id={'close'} />
        </Button>
      </div>
    </div>
  );
}
