import React, { useEffect, useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import GridContainer from '@jumbo/components/GridContainer';
import { FormControl, Grid, InputLabel, MenuItem, Select } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import * as api from '../../../../api/api';
import DidReportDateFilter from './DateFilter';

export default function Filter({ setSelectedStartTime, setSelectedEndTime, agent, setAgent }) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));

  const classes = useStyles();

  const [agents, setAgents] = useState([]);

  useEffect(() => {
    api.getAgentNumber().then(resp => {
      setAgents(JSON.parse(resp.data.data));
    });
  }, []);

  const handleChange = event => {
    setAgent(event.target.value);
  };

  return (
    <div>
      <div style={{ marginBottom: '10px' }}>
        <DidReportDateFilter setStartTimeApi={setSelectedStartTime} setEndTimeApi={setSelectedEndTime} />
      </div>
      <Card className={classes.root}>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="select-did-numbers">
                  <IntlMessages id={'calltransferreport.agnet'} />
                </InputLabel>
                <Select
                  labelId="select-did-numbers"
                  id="demo-simple"
                  label={<IntlMessages id={'calltransferreport.agnet'} />}
                  value={agent}
                  onChange={handleChange}>
                  <MenuItem value="">
                    <em>
                      <IntlMessages id={'all'} />
                    </em>
                  </MenuItem>
                  {agents.map((agent, index) => (
                    <MenuItem key={index} value={agent.display_name}>
                      {agent.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
    </div>
  );
}
