import React, { useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getAgentTotalCall } from 'api/api';
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import trLocale from 'date-fns/locale/tr';
import enLocale from 'date-fns/locale/en-US';
import { useIntl } from 'react-intl';
import deLocale from 'date-fns/locale/de';
import nlLocale from 'date-fns/locale/nl';
import dateFormat from 'dateformat';

export default function Filter({ selectedEndTime, setSelectedEndTime, setTotalAgent, setTotalDid, selectedDate }) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));

  const classes = useStyles();

  useEffect(() => {
    getAgentTotalCall(selectedDate, 'cte3').then(resp => {
      setTotalAgent(JSON.parse(resp.data.data));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedDate]);

  useEffect(() => {
    getAgentTotalCall(selectedDate, 'cte2').then(resp => {
      setTotalDid(JSON.parse(resp.data.data));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedDate]);

  let curentLanguage = useIntl();

  return (
    <Card className={classes.root}>
      <CardContent>
        <GridContainer>
          <Grid item md={3}>
            <LocalizationProvider
              dateAdapter={AdapterDateFns}
              locale={
                curentLanguage.locale === 'en-EN'
                  ? enLocale
                  : curentLanguage.locale === 'nl-NL'
                  ? nlLocale
                  : curentLanguage.locale === 'tr-TR'
                  ? trLocale
                  : curentLanguage.locale === 'de-DE'
                  ? deLocale
                  : enLocale
              }>
              {' '}
              <DatePicker
                value={selectedEndTime}
                views={['year', 'month']}
                onChange={newValue => setSelectedEndTime(dateFormat(newValue, 'yyyy-mm-dd'))}
                renderInput={params => <TextField {...params} />}
                inputVariant="outlined"
                format="MM-YYYY"
                maxDate={new Date()}
                label={<IntlMessages id={'filter.menu.date'} />}
              />
            </LocalizationProvider>
          </Grid>
        </GridContainer>
      </CardContent>
    </Card>
  );
}
