import { CircularProgress } from '@material-ui/core';
import { getCallLog, getCallLogTotal, getCallLogCount, getCallLogTotalSummary } from 'api/api';
import React, { useEffect, useState } from 'react';
import CallLogDateFilter from './DateFilter';
import CallLogtherFilters from './Filters';
import dateFormat from 'dateformat';

export default function CallLogFilter({ setData, limit, offset, setPage, setTotalData, setCount, setResponseTotalSummary }) {
  let [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0), 'yyyy-mm-dd HH:MM:ss'));
  let [endTimeApi, setEndTimeApi] = useState(dateFormat(new Date().setHours(23, 59, 59), 'yyyy-mm-dd HH:MM:ss'));
  let [directionApi, setDirectionApi] = useState('');
  let [directionSql, setDirectionSql] = useState('');
  let [sourceFilterText, setSourceFilterText] = useState('');
  let [sourceFilterNumber, setSourceNumber] = useState('');
  let [destinationFilterNumber, setDestinationFilterNumber] = useState('');
  let [destinationFilterText, setDestinationFilterText] = useState('');
  let [didNumber, setDidNumber] = useState('');
  let [sourceDnMin, setSourceDnMin] = useState('0');
  let [sourceDnMax, setSourceDnMax] = useState('99999');
  let [destinationDnMin, setDestinationDnMin] = useState('0');
  let [destinationDnMax, setDestinationDnMax] = useState('99999');
  let [sourceDnLike, setSourceDnLike] = useState('');
  let [destinationDnLike, setDestinationDnLike] = useState('');
  let [answerStatus, setAnswerStatus] = useState('');
  let [answerStatusSql, setAnswerStatusSql] = useState('');
  let [duration, setDuration] = useState('');
  let [durationSql, setDurationSql] = useState('');
  useEffect(() => {
    if (duration === '') {
      setDurationSql('');
    } else if (duration?.durationMin === undefined) {
      setDurationSql(`and 
  (cl_segments_view_new.end_time - cl_segments_view_new.dst_answer_time) <= interval '${duration}' second`);
    } else if (duration.durationMin === '' && duration.durationMax !== '') {
      setDurationSql(`and 
      (cl_segments_view_new.end_time - cl_segments_view_new.dst_answer_time) <= interval '${duration.durationMax}' second`);
    } else if (parseInt(duration.durationMin) <= parseInt(duration.durationMax)) {
      setDurationSql(`
      and
      (cl_segments_view_new.end_time - cl_segments_view_new.dst_answer_time) >= interval '${duration.durationMin}' second
      and 
      (cl_segments_view_new.end_time - cl_segments_view_new.dst_answer_time) <= interval '${duration.durationMax}' second`);
    }
  }, [duration]);

  useEffect(() => {
    if (directionApi === 'all') {
      setDirectionSql('');
    } else if (directionApi === 'inb') {
      setDirectionSql("cast(src_dn_type as text) like '%1' and cast(dst_dn_type as text) like '%0' and");
    } else if (directionApi === 'out') {
      setDirectionSql("cast(src_dn_type as text) like '%0' and cast(dst_dn_type as text) like '%1' and");
    } else if (directionApi === 'agent') {
      setDirectionSql("cast(src_dn_type as text) like '%0' and cast(dst_dn_type as text) like '%0' and");
    } else if (directionApi === 'ques') {
      setDirectionSql("cast(src_dn_type as text) like '%1' and cast(dst_dn_type as text) not like '%0' and");
    }
  }, [directionApi]);

  useEffect(() => {
    if (answerStatus === 'all') {
      setAnswerStatusSql('');
    } else if (answerStatus === 'answered') {
      setAnswerStatusSql("where statusofcall = 'answered'");
    } else if (answerStatus === 'nonanswered') {
      setAnswerStatusSql("where statusofcall = 'nonanswered'");
    } else if (answerStatus === 'transfered') {
      setAnswerStatusSql("where statusofcall = 'transfered'");
    } else if (answerStatus === 'missed') {
      setAnswerStatusSql(
        "where statusofcall != 'transfered' and statusofcall != 'answered' and statusofcall != 'nonanswered'",
      );
    }
  }, [answerStatus]);
  const timeOffset = localStorage.getItem('timeoffset') || '+3';

  let string = `with recursive cte1 as 
  (     
  select  
  split_part(split_part(recording_url, '_', 2),'-',2) as CalledFrom,     
  split_part(split_part(recording_url, '_', 2),'-',1) as TransferedTo,     
  start_time,     
  end_time,      
  recording_url     
  from recordings as records     
  where     
  (date_trunc('minute', TIMESTAMP'${endTimeApi}' + interval '1 day') >=  start_time)      
  and      
  (date_trunc('minute', TIMESTAMP'${startTimeApi}') <= start_time) 
  and
  split_part(split_part(recording_url, '_', 2),'-',1) like '%${sourceDnLike}%'
  and
  split_part(split_part(recording_url, '_', 2),'-',2) like '%${destinationDnLike}%'	
  order by start_time desc
  ), cte2 as( 
  SELECT
  call_id,
  act, 
  src_did,
  case when src_dn_type = 1 then src_display_name else src_dn end as src_dn,
  case when dst_dn_type = 1 then dst_display_name else dst_dn end as dst_dn,
  split_part(to_char(cl_segments_view_new.dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}', 'yyyy-mm-dd ~ HH24:MI:SS'),' ~ ',1) as date,  
  split_part(to_char(cl_segments_view_new.dst_start_time at time zone 'utc' at time zone 'Etc/GMT${timeOffset}', 'yyyy-mm-dd ~ HH24:MI:SS'),' ~ ',2) as hour, 
  cl_segments_view_new.start_time,
  src_display_name,  
  dst_display_name,  
  to_char(coalesce(cl_segments_view_new.end_time - cl_segments_view_new.dst_answer_time,interval '0 second'),'HH24h MIm SSs') as duration,  
  coalesce(cte1.recording_url,'no recording'),  
  case 
  when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) )  then 'answered'
  when act = 2 then 'phoneispickedup'
  when act = 3 then 'nonanswered'
  when act = 7 then 'FailedBXferOfSrc'
  when act = 8  then 'FailedBXferOfDst'
  when act = 9 then 'transfered'
  when act = 10 then 'transfered'
  when act = 12 then 'transfered'
  when act = 15 then 'nonanswered'
  when act = 404 then 'CallerForbidden'
  when act = 408 then 'TargetNotFound'
  when act = 418 then 'nonanswered'
  when act = 400 then 'nonanswered'
  when act = 102 then 'FwdOnBusy'
  when act = 101 then 'nonanswered'
  when act = 409 then 'TerminatedByRule'
  when act = 422 then 'RouteNotFound'
  else 'nonanswered'
  end as statusofcall,
  case 
  when (src_dn_type = 0 and dst_dn_type = 0) then 'dahili' 
  when (src_dn_type = 0 and dst_dn_type =1) then 'dƒ±≈üarƒ±' 
  when (src_dn_type = 1 and dst_dn_type = 0) then 'i√ßeri' 
  when (src_dn_type = 1 and dst_dn_type != 0) then 'kuyruk' 
  when (act >= 400 and dst_dn_type = 13) then 'dƒ±≈üarƒ±' 
  end as direction
  FROM  cl_segments_daily_statics('${startTimeApi}','${endTimeApi}') as cl_segments_view_new  
  left outer join cte1  on 
  cte1.recording_url like ('%' || to_char(cl_segments_view_new.start_time, 'yyyymmddHH24MISS') || '%')
  where  
  ${directionSql} 
  act != 1 
  and  
  dst_display_name like '%${destinationFilterNumber}%'  
  and  
  src_display_name like '%${sourceFilterNumber}%'
  and
  src_did like '%${didNumber}%'
  and
  (  
  (dst_dn like '%${destinationDnLike}%' and src_dn like '%${sourceDnLike}%')   
  and
  cast(substring(src_dn from '[0-9]+') as bigint) >= ${sourceDnMin} and cast(substring(src_dn from '[0-9]+') as bigint) <= ${sourceDnMax}    
  and 
  cast(substring(dst_dn from '[0-9]+') as bigint) >= ${destinationDnMin} and cast(substring(dst_dn from '[0-9+]') as bigint) <= ${destinationDnMax}
  )
  ${durationSql}
  order by dst_start_time,seg_order asc 
  ) 
  select 
  * 
  from cte2 
  ${answerStatusSql}
  limit ${limit} offset ${offset}`;

  let totalString = `
    WITH RECURSIVE cte1 as 
  (     
  select  
  split_part(split_part(recording_url, '_', 2),'-',2) as CalledFrom,     
  split_part(split_part(recording_url, '_', 2),'-',1) as TransferedTo,     
  start_time,     
  end_time,      
  recording_url     
  from recordings as records     
  where     
  (date_trunc('minute', TIMESTAMP'${endTimeApi}' + interval '1 day') >=  start_time)      
  and      
  (date_trunc('minute', TIMESTAMP'${startTimeApi}') <= start_time) 
  and
  split_part(split_part(recording_url, '_', 2),'-',1) like '%${sourceDnLike}%'
  and
  split_part(split_part(recording_url, '_', 2),'-',2) like '%${destinationDnLike}%'	
  order by start_time desc
  ),cte2 as(  
    SELECT
    *,
    case 
    when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) )  then 'answered'
    when act = 2 then 'phoneispickedup'
  when act = 3 then 'nonanswered'
  when act = 7 then 'FailedBXferOfSrc'
  when act = 8  then 'FailedBXferOfDst'
  when act = 9 then 'transfered'
  when act = 10 then 'transfered'
  when act = 12 then 'transfered'
  when act = 15 then 'nonanswered'
  when act = 404 then 'CallerForbidden'
  when act = 408 then 'TargetNotFound'
  when act = 418 then 'nonanswered'
  when act = 400 then 'nonanswered'
  when act = 102 then 'FwdOnBusy'
  when act = 101 then 'nonanswered'
  when act = 409 then 'TerminatedByRule'
  when act = 422 then 'RouteNotFound'
    else 'nonanswered'
    end as statusofcall
    from
    cl_segments_daily_statics('${startTimeApi}','${endTimeApi}') as cl_segments_view_new  
    left outer join cte1  on 
    cte1.recording_url like ('%' || to_char(cl_segments_view_new.start_time, 'yyyymmddHH24MISS') || '%')
    where 
    ${directionSql}
    dst_display_name like '%${destinationFilterNumber}%'
    and
    act != 1
    ${durationSql}
    and
    src_display_name like '%${sourceFilterNumber}%'
    and
    src_did like '%${didNumber}%'
    and(
    (dst_dn like '%${destinationDnLike}%' and src_dn like '%${sourceDnLike}%')
     and
     (cast(substring(src_dn from '[0-9]+') as bigint) >= ${sourceDnMin} and cast(substring(src_dn from '[0-9]+') as bigint) <= ${sourceDnMax}) 
     and cast(substring(dst_dn from '[0-9]+') as bigint) >= ${destinationDnMin} and cast(substring(dst_dn from '[0-9+]') as bigint) <= ${destinationDnMax})
  )
  select
  to_char((date_trunc('day',dst_start_time)),'dd-mm-yyyy'),
  count(*),
  sum(case when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) ) then 1 else 0 end) as answered,
  sum(case when act in (3,15,101,400,418) then 1 else 0 end) as not_answered,
  sum(case when act in (9,10,12) then 1 else 0 end) as transfered,
  count(*)-sum(case when act in (9,10,12) then 1 else 0 end) - sum(case when act in (3,15,101,400,418) then 1 else 0 end) - sum(case when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) ) then 1 else 0 end) as missed,
  to_char(sum(dst_end_time-dst_answer_time),'HH24h MIm SSs') as totalDuration,
  to_char(sum(dst_end_time-dst_answer_time)/count(*),'HH24h MIm SSs') as averageDuration
  from cte2 ${answerStatusSql}
  group by date_trunc('day',dst_start_time) 
  order by date_trunc('day',dst_start_time) asc`;

  let stringCount = `
  WITH RECURSIVE cte1 as
  (    
  select  
  split_part(split_part(recording_url, '_', 2),'-',2) as CalledFrom,    
  split_part(split_part(recording_url, '_', 2),'-',1) as TransferedTo,    
  start_time,    
  end_time,      
  recording_url    
  from recordings as records    
  where    
  (date_trunc('minute', TIMESTAMP'${endTimeApi}' + interval '1 day') >=  start_time)      
  and      
  (date_trunc('minute', TIMESTAMP'${startTimeApi}') <= start_time)
  and
  split_part(split_part(recording_url, '_', 2),'-',1) like '%${sourceDnLike}%'
  and
  split_part(split_part(recording_url, '_', 2),'-',2) like '%${destinationDnLike}%'        
  order by start_time desc    
  ), cte2 as( 
    SELECT
    case 
    when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) )  then 'answered'
    when act = 2 then 'phoneispickedup'
  when act = 3 then 'nonanswered'
  when act = 7 then 'FailedBXferOfSrc'
  when act = 8  then 'FailedBXferOfDst'
  when act = 9 then 'transfered'
  when act = 10 then 'transfered'
  when act = 12 then 'transfered'
  when act = 15 then 'nonanswered'
  when act = 404 then 'CallerForbidden'
  when act = 408 then 'TargetNotFound'
  when act = 418 then 'nonanswered'
  when act = 400 then 'nonanswered'
  when act = 102 then 'FwdOnBusy'
  when act = 101 then 'nonanswered'
  when act = 409 then 'TerminatedByRule'
  when act = 422 then 'RouteNotFound'
    else 'nonanswered'
    end as statusofcall
    FROM  cl_segments_daily_statics('${startTimeApi}','${endTimeApi}') as cl_segments_view_new  
    left outer join cte1  on 
    cte1.recording_url like ('%' || to_char(cl_segments_view_new.start_time, 'yyyymmddHH24MISS') || '%')
    where  
    ${directionSql} 
    act != 1 
    and  
    dst_display_name like '%${destinationFilterNumber}%'  
    and  
    src_display_name like '%${sourceFilterNumber}%'  
    and
    src_did like '%${didNumber}%'
    and
    (  
    (dst_dn like '%${destinationDnLike}%' and src_dn like '%${sourceDnLike}%')   
    and
    cast(substring(src_dn from '[0-9]+') as bigint) >= ${sourceDnMin} and cast(substring(src_dn from '[0-9]+') as bigint) <= ${sourceDnMax}    
    and 
    cast(substring(dst_dn from '[0-9]+') as bigint) >= ${destinationDnMin} and cast(substring(dst_dn from '[0-9+]') as bigint) <= ${destinationDnMax}
    )
    ${durationSql}
    order by dst_start_time,seg_order asc 
    ) 
    select 
    count(*) as cnt
    from cte2 
    ${answerStatusSql}`;

  let stringTotalSummary = `
    WITH RECURSIVE cte1 as 
    (     
    select  
    split_part(split_part(recording_url, '_', 2),'-',2) as CalledFrom,     
    split_part(split_part(recording_url, '_', 2),'-',1) as TransferedTo,     
    start_time,     
    end_time,      
    recording_url     
    from recordings as records     
    where     
    (date_trunc('minute', TIMESTAMP'${endTimeApi}' + interval '1 day') >=  start_time)      
    and      
    (date_trunc('minute', TIMESTAMP'${startTimeApi}') <= start_time) 
    and
    split_part(split_part(recording_url, '_', 2),'-',1) like '%${sourceDnLike}%'
    and
    split_part(split_part(recording_url, '_', 2),'-',2) like '%${destinationDnLike}%'	
    order by start_time desc
    ), cte2 as( 
    SELECT
    *,
    case 
    when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) )  then 'answered'
    when act = 2 then 'phoneispickedup'
    when act = 3 then 'nonanswered'
    when act = 7 then 'FailedBXferOfSrc'
    when act = 8  then 'FailedBXferOfDst'
    when act = 9 then 'transfered'
    when act = 10 then 'transfered'
    when act = 12 then 'transfered'
    when act = 15 then 'nonanswered'
    when act = 404 then 'CallerForbidden'
    when act = 408 then 'TargetNotFound'
    when act = 418 then 'nonanswered'
    when act = 400 then 'nonanswered'
    when act = 102 then 'FwdOnBusy'
    when act = 101 then 'nonanswered'
    when act = 409 then 'TerminatedByRule'
    when act = 422 then 'RouteNotFound'
    else 'nonanswered'
    end as statusofcall
    from
    cl_segments_daily_statics('${startTimeApi}','${endTimeApi}') as cl_segments_view_new  
    left outer join cte1  on 
    cte1.recording_url like ('%' || to_char(cl_segments_view_new.start_time, 'yyyymmddHH24MISS') || '%')
    where 
    ${directionSql}
    dst_display_name like '%${destinationFilterNumber}%'
    and
    act != 1
    ${durationSql}
    and
    src_display_name like '%${sourceFilterNumber}%'
    and
    src_did like '%${didNumber}%'
    and(
    (dst_dn like '%${destinationDnLike}%' and src_dn like '%${sourceDnLike}%')
     and
     (cast(substring(src_dn from '[0-9]+') as bigint) >= ${sourceDnMin} and cast(substring(src_dn from '[0-9]+') as bigint) <= ${sourceDnMax}) 
     and cast(substring(dst_dn from '[0-9]+') as bigint) >= ${destinationDnMin} and cast(substring(dst_dn from '[0-9+]') as bigint) <= ${destinationDnMax})
  )
  select
  count(*),
  sum(case when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) ) then 1 else 0 end) as answered,
  sum(case when act in (3,15,101,400,418) then 1 else 0 end) as not_answered,
  sum(case when act in (9,10,12) then 1 else 0 end) as transfered,
  count(*)-sum(case when act in (9,10,12) then 1 else 0 end) - sum(case when act in (3,15,101,400,418) then 1 else 0 end) - sum(case when ( (src_dn_type = 0) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 1) and (dst_dn_type = 0) and ( act = 5 or act = 6 ) ) or ( (src_dn_type = 0) and (dst_dn_type = 1) and ( act = 5 or act = 6 ) ) then 1 else 0 end) as missed,
  to_char(sum(dst_end_time-dst_answer_time),'HH24h MIm SSs') as totalDuration,
  to_char(sum(dst_end_time-dst_answer_time)/count(*),'HH24h MIm SSs') as averageDuration
  from cte2 ${answerStatusSql}
  `;
  useEffect(() => {
    if (sourceFilterText.startsWith('all')) {
      setSourceNumber('');
      setSourceDnMin('0');
      setSourceDnMax('99999');
      setSourceDnLike('');
      setDidNumber('');
    } else if (sourceFilterText.startsWith('numberfilter====')) {
      setSourceNumber(sourceFilterText.split('====')[1]);
      setDidNumber('');
      setSourceDnMin('0');
      setSourceDnMax('99999');
      setSourceDnLike('');
    } else if (sourceFilterText.startsWith('dnOrDns====')) {
      if (sourceFilterText.includes('-')) {
        setDidNumber('');
        setSourceDnLike('');
        setSourceNumber('');
        setSourceDnMin(sourceFilterText.split('====')[1].split('-')[0]);
        setSourceDnMax(sourceFilterText.split('====')[1].split('-')[1]);
      } else {
        setDidNumber('');
        setSourceDnLike(sourceFilterText.split('====')[1]);
        setSourceDnMin('0');
        setSourceDnMax('99999');
        setSourceNumber('');
      }
    } else if (sourceFilterText.startsWith('didno====')) {
      setDidNumber(sourceFilterText.split('====')[1]);
      setSourceDnLike('');
      setSourceDnMin('0');
      setSourceDnMax('99999');
      setSourceNumber('');
    }
  }, [sourceFilterText]);

  useEffect(() => {
    if (destinationFilterText.startsWith('all')) {
      setDestinationFilterNumber('');
      setDestinationDnLike('');
      setDestinationDnMin('0');
      setDestinationDnMax('99999');
      setDidNumber('');
    } else if (destinationFilterText.startsWith('numberfilter====')) {
      setDestinationFilterNumber(destinationFilterText.split('====')[1]);
      setDidNumber('');
      setDestinationDnLike('');
      setDestinationDnMin('0');
      setDestinationDnMax('99999');
    } else if (destinationFilterText.startsWith('dnOrDns====')) {
      if (destinationFilterText.includes('-')) {
        setDestinationDnLike('');
        setDidNumber('');
        setDestinationFilterNumber('');
        setDestinationDnMin(destinationFilterText.split('====')[1].split('-')[0]);
        setDestinationDnMax(destinationFilterText.split('====')[1].split('-')[1]);
      } else {
        setDestinationDnLike(destinationFilterText.split('====')[1]);
        setDidNumber('');
        setDestinationFilterNumber('');
        setDestinationDnMin('0');
        setDestinationDnMax('99999');
      }
    } else if (destinationFilterText.startsWith('didno====')) {
      setDidNumber(destinationFilterText.split('====')[1]);
      setDestinationFilterNumber('');
      setDestinationDnLike('');
      setDestinationDnMin('0');
      setDestinationDnMax('99999');
    }
  }, [destinationFilterText]);
  let [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    getCallLog(string).then(result => {
      setData(JSON.parse(result.data.data));
      setLoading(false);
    });
  }, [string, setData]);

  useEffect(() => {
    setLoading(true);
    getCallLogTotal(totalString).then(result => {
      setTotalData(JSON.parse(result.data.data));
      setLoading(false);
    });
  }, [totalString, setTotalData]);

  useEffect(() => {
    setLoading(true);
    getCallLogTotalSummary(stringTotalSummary).then(result => {
      setResponseTotalSummary(JSON.parse(result.data.data)[0]);
      setLoading(false);
    });
  }, [stringTotalSummary, setResponseTotalSummary]);

  useEffect(() => {
    setLoading(true);
    getCallLogCount(stringCount).then(result => {
      setCount(parseInt(JSON.parse(result.data.data)[0].cnt));
      setLoading(false);
    });
  }, [stringCount, setCount]);

  return (
    <div>
      <CallLogDateFilter setStartTimeApi={setStartTimeApi} setEndTimeApi={setEndTimeApi} />
      <CallLogtherFilters
        setDirectionApi={setDirectionApi}
        setSourceFilterText={setSourceFilterText}
        setDestinationFilterText={setDestinationFilterText}
        setAnswerStatus={setAnswerStatus}
        setLoading={setLoading}
        setDuration={setDuration}
      />
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '10px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}
