import { localeText } from '@jumbo/utils/DataGridLocales';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Paper } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import { getCallTransferReport } from 'api/api';
import React, { useEffect, useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core';
import { AccessTime, DateRange } from '@material-ui/icons';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';

export default function DataTable({ agent, selectedStartTime, selectedEndTime }) {
  let [data, setData] = useState([]);
  useEffect(() => {
    getCallTransferReport(agent, selectedStartTime, selectedEndTime).then(resp => {
      setData(JSON.parse(resp.data.data));
    });
  }, [agent, selectedStartTime, selectedEndTime]);

  let rows = data.map((item, index) => ({
    num:index+1,
    id: index,
    transferer: item.transferer,
    trasfered: item.trasfered,
    caller: item.caller,
    start_time: item.start_time,
    answer_time: item.answer_time,
    end_time: item.end_time,
    ring_time: item.ring_time,
    talk_time: item.talk_time,
  }));

  const columns = [
    {
      field:'num',
      headerName:"#",
      renderCell:params =>(
        <b>{params.value}</b>
      )
    },
    {
      field: 'transferer',
      headerName: <IntlMessages id={'calltransferreport.transferfrom'} />,
      width: 200,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'trasfered',
      headerName: <IntlMessages id={'calltransferreport.transferto'} />,
      width: 200,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'caller',
      headerName: <IntlMessages id={'calltransferreport.caller'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'start_time',
      headerName: <IntlMessages id={'calltransferreport.starttime'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'answer_time',
      headerName: <IntlMessages id={'calltransferreport.answertime'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'end_time',
      headerName: <IntlMessages id={'calltransferreport.endtime'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'ring_time',
      headerName: <IntlMessages id={'calltransferreport.ringingtime'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
    {
      field: 'talk_time',
      headerName: <IntlMessages id={'calltransferreport.talktime'} />,
      width: 150,
      renderCell: params => <span>{params.value}</span>,
    },
  ];

  const [total, setTotal] = useState({totalRingTime:"00h 00m 00s",totalTalkTime:"00h 00m 00s",totalCount:0});
  useEffect(()=>{
    let totalring = 0;
    let totalTalk = 0;
    data.forEach(async (item)=>{
      totalring+= convertSec(item.ring_time);
      totalTalk+= convertSec(item.talk_time);
    });
    setTotal({
      totalRingTime: convertTime(totalring),
      totalTalkTime: convertTime(totalTalk),
      totalCount:data.length
    })
  },[data])

  return (
    <div>
      <Paper style={{ height: '600px', width: '100%' }}>
        <DataGrid
          rows={rows}
          columns={columns}
          localeText={localeText}
          componentsProps={{
            pagination: {
              labelRowsPerPage: <IntlMessages id={'rows.per.page'} />,
            },
          }}
        />
      </Paper>
      <Paper style={{ marginTop: '15px' }}>
        <TableContainer style={{ maxHeight: '440px' }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell>
                  <IntlMessages id={'dailysummary.date'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'abondenedcalls.count'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'wallboard.agentdetail.timedetails.totalwaitingtime'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'wallboard.call.time'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span style={{ color: '#519259' }}>
                    <DateRange style={{ verticalAlign: '-5px' }} />{' '}
                  </span>
                  <b>
                    <IntlMessages id={'total'} />
                  </b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{total.totalCount}</b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                  <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{total.totalRingTime}</b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                  <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{total.totalTalkTime}</b>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </div>
  );
}

const convertSec = (str) =>{
  var hour = parseInt(str.slice(0,2));
  var min = parseInt(str.slice(3,6));
  var sec = parseInt(str.slice(8,10));
  return hour*3600+min*60+sec;
}
const convertTime = (sec) =>{
  var hours   = Math.floor(sec / 3600);
  var minutes = Math.floor((sec - (hours * 3600)) / 60);
  var seconds = sec - (hours * 3600) - (minutes * 60);
  if (hours   < 10) {hours   = "0"+hours;}
  if (minutes < 10) {minutes = "0"+minutes;}
  if (seconds < 10) {seconds = "0"+seconds;}
  return `${hours}h ${minutes}m ${seconds}s`;
}