import { getNewDate } from '@jumbo/utils/dateHelper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import {
  Button,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from '@material-ui/core';
import {
  AccessTime,
  Check,
  Clear,
  CloudDownload,
  DateRange,
  Person,
  PhoneDisabled,
  PlayArrow,
  PhoneMissed,
} from '@material-ui/icons';
import { getPath, getRecordingBytes, getQueues } from 'api/api';
import React, { useEffect, useState } from 'react';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { DataGrid } from '@mui/x-data-grid';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
import Drawer from '@mui/material/Drawer';
import CallLogCallDetail from '../CallDetails';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { localeText } from '@jumbo/utils/DataGridLocales';
import AudioWaveForm from '@jumbo/utils/AudioWaveForm';
import SendToMobileIcon from '@mui/icons-material/SendToMobile';
import TtyIcon from '@mui/icons-material/Tty';
import { darken, lighten } from '@mui/material/styles';
export default function CallLogTable({
  data,
  limit,
  setLimit,
  setOffset,
  page,
  setPage,
  totalData,
  count,
  responseTotalSummary,
  offset,
}) {
  let [path, setPath] = useState('');

  const [listenedRecords, setListenedRecords] = useState([]);
  const getBackgroundColor = (color, mode) => (mode === 'dark' ? darken(color, 0.6) : lighten(color, 0.6));

  const getHoverBackgroundColor = (color, mode) => (mode === 'dark' ? darken(color, 0.5) : lighten(color, 0.5));
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  const [open, setOpen] = React.useState(false);
  const handleClose = () => setOpen(false);

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const audioModalStyle = {
    boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px',
    borderRadius: '8px',
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: '#ededed',
    width: '600px',
  };
  const [record, setRecord] = useState('');
  const [openAudioModal, setOpenAudioModal] = useState(false);
  const listenRecord = (dir, num) => {
    getRecordingBytes(path, dir)
      .then(resp => resp.arrayBuffer())
      .then(buffer => {
        if (buffer !== null) {
          setRecord(buffer);
          setListenedRecords(prev => [...prev, num]);
          setOpenAudioModal(true);
        } else {
          NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000);
        }
      })
      .catch(err => NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000));
  };

  let curentLanguage = useIntl();

  let tableData = data.map((item, index) => ({
    num: index + 1 + offset,
    callId: item.call_id,
    listen: item.coalesce,
    date: getNewDate(item.date),
    startTime: item.hour,
    srcDid: item.src_did,
    caller:
      item.src_display_name === item.src_dn || item.src_dn === '-'
        ? item.src_display_name
        : item.src_display_name + '(' + item.src_dn + ')',
    called:
      item.dst_display_name === item.dst_dn || item.dst_dn === '-'
        ? item.dst_display_name
        : item.dst_display_name + '(' + item.dst_dn + ')',
    direction: [item.direction, item.dst_dn],
    duration: item.duration,
    salable: item.statusofcall,
    id: index,
  }));
  const [rightMenu, setRightMenu] = useState(false);
  const [callIdEye, setCallIdEye] = useState('');
  const handleClickOpenEye = callId => {
    setCallIdEye(callId);
    setRightMenu(true);
  };

  const [queues, setQueues] = useState([]);
  useEffect(() => {
    getPath().then(res => {
      setPath(res.data);
    });
    getQueues().then(que => setQueues(JSON.parse(que.data.data)));
  }, []);
  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
      width: 40,
    },
    {
      field: 'callId',
      headerName: <IntlMessages id={'detail'} />,
      width: 70,
      renderCell: params => (
        <span onClick={() => handleClickOpenEye(params.value)} style={{ cursor: 'pointer' }}>
          <VisibilityIcon style={{ color: '#FF1700', verticalAlign: '-5px' }} />
        </span>
      ),
    },
    {
      field: 'listen',
      headerName: <IntlMessages id={'listen'} />,
      width: 70,
      renderCell: params => (
        <span style={{ cursor: 'pointer' }}>
          {params.value !== 'no recording' && (
            <IconButton
              onClick={() => listenRecord(params.value, params.row.num)}
              style={{ backgroundColor: '#C6D57E', color: '#406343' }}
              size="small"
              aria-label="listen">
              <PlayArrow />
            </IconButton>
          )}
          {params.value === 'no recording' && (
            <IconButton disabled style={{ backgroundColor: '#FE8F8F', color: '#FF5C58' }} size="small" aria-label="listen">
              <Clear />
            </IconButton>
          )}
        </span>
      ),
    },
    {
      field: 'date',
      headerName: <IntlMessages id={'date'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#519259' }}>
            <DateRange style={{ verticalAlign: '-5px' }} />{' '}
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'startTime',
      headerName: <IntlMessages id={'starttime'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: '#DB6B97' }}>
          <AccessTime style={{ verticalAlign: '-5px' }} /> {params.value}
        </span>
      ),
    },
    {
      field: 'caller',
      headerName: <IntlMessages id={'callers'} />,
      width: 250,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#98BAE7' }}>
            <Person style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'called',
      headerName: <IntlMessages id={'calledd'} />,
      width: 250,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#9AE66E' }}>
            <Person style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'srcDid',
      headerName: <IntlMessages id={'didnumber'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#d6851a' }}>
            <TtyIcon style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value || '-'}
        </span>
      ),
    },
    {
      field: 'direction',
      headerName: <IntlMessages id={'direction'} />,
      width: 110,
      renderCell: params => (
        <span>
          {params.value[0] === 'i√ßeri' && (
            <span
              style={{
                backgroundColor: '#9AE66E',
                color: '#497c4f',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'incomingcall'} />
            </span>
          )}
          {params.value[0] === 'dƒ±≈üarƒ±' && (
            <span
              style={{
                backgroundColor: '#FF6D6D',
                color: '#AE431E',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'outgoingcall'} />
            </span>
          )}
          {params.value[0] === 'dahili' && (
            <span
              style={{
                backgroundColor: '#FFBC97',
                color: '#FF7800',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'agentedcall'} />
            </span>
          )}
          {params.value[0] === 'kuyruk' && (
            <span
              style={{
                backgroundColor: '#a3b8ff',
                color: '#053cf2',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              {queues.find(q => q.dn === params.value[1])?.display_name || <IntlMessages id="queue" />}
            </span>
          )}
        </span>
      ),
    },
    {
      field: 'duration',
      headerName: <IntlMessages id={'duration'} />,
      width: 130,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: '#DB6B97' }}>
          <AccessTime style={{ verticalAlign: '-5px' }} /> {params.value}
        </span>
      ),
    },
    {
      field: 'salable',
      headerName: <IntlMessages id={'state'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif' }}>
          {params.value === 'answered' ? (
            <span
              style={{
                backgroundColor: '#B1E693',
                color: '#1C7947',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <Check fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'endingcall'} />
            </span>
          ) : params.value === 'nonanswered' ? (
            <span
              style={{
                backgroundColor: '#FF7777',
                color: '#852747',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <PhoneDisabled fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'notanswered'} />
            </span>
          ) : params.value === 'transfered' ? (
            <span
              style={{
                backgroundColor: '#bd88f7',
                color: '#27034f',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <SendToMobileIcon fontSize="small" style={{ verticalAlign: '-5px' }} />
              <IntlMessages id={'dailysummary.transfer'} />
            </span>
          ) : (
            <span
              style={{
                backgroundColor: '#f3bbfc',
                color: '#a402bd',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <PhoneMissed fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'missedcall'} />
            </span>
          )}
        </span>
      ),
    },
  ];

  return (
    <>
      <NotificationContainer />
      {data.length > 0 && (
        <div style={{ marginTop: '10px', textAlign: 'right' }}>
          {
            <ExcelFile
              filename={
                curentLanguage.locale === 'en-EN'
                  ? 'Call_Log'
                  : curentLanguage.locale === 'tr-TR'
                  ? 'Cagri_raporu'
                  : curentLanguage.locale === 'nl-NL'
                  ? 'Oproep_Log'
                  : curentLanguage.locale === 'de-DE'
                  ? 'Anrufliste'
                  : ''
              }
              element={
                <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              }>
              <ExcelSheet data={data} name="data">
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'Date'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Tarih'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Datum'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Datum'
                      : ''
                  }
                  value="date"
                />
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'Hour'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Saat'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Uur'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Stunde'
                      : ''
                  }
                  value="hour"
                />
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'From'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Kimden'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Van'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Von'
                      : ''
                  }
                  value="src_display_name"
                />
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'Destination'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Hedef'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Bestemming'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Ziel'
                      : ''
                  }
                  value="dst_display_name"
                />
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'Direction'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Y√∂n'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Richting'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Genaamd'
                      : ''
                  }
                  value="direction"
                />
                <ExcelColumn
                  label={
                    curentLanguage.locale === 'en-EN'
                      ? 'Duration'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'S√ºre'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Looptijd'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Dauer'
                      : ''
                  }
                  value="duration"
                />
                <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Case' : 'Durum'} value="salable" />
              </ExcelSheet>
            </ExcelFile>
          }
        </div>
      )}
      <Drawer anchor={'right'} open={rightMenu} onClose={() => setRightMenu(false)}>
        <div style={{ width: '600px' }}>
          <CallLogCallDetail callId={callIdEye} />
        </div>
      </Drawer>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          {data.length < 1 && (
            <div style={{ textAlign: 'center' }}>
              <CircularProgress />
              <p>Veriler hazirlanƒ±yor...</p>
            </div>
          )}
          <div style={{ textAlign: 'center' }}>
            {data.length < 1 && (
              <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            )}
            {data.length > 0 && (
              <ExcelFile
                filename={
                  curentLanguage.locale === 'en-EN'
                    ? 'Call_Log'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cagri_raporu'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Oproep_Log'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anrufliste'
                    : ''
                }
                element={
                  <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                    <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                  </Button>
                }>
                <ExcelSheet data={data} name="data">
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Date'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Tarih'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Datum'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Datum'
                        : ''
                    }
                    value="date"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Hour'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Saat'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Uur'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Stunde'
                        : ''
                    }
                    value="hour"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Caller'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Arayan'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Beller'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Anruferin'
                        : ''
                    }
                    value="src_display_name"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Called'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Aranan'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Genaamd'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Namens'
                        : ''
                    }
                    value="dst_display_name"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Direction'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Y√∂n'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Richting'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Genaamd'
                        : ''
                    }
                    value="direction"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'Duration'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'S√ºre'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Looptijd'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Dauer'
                        : ''
                    }
                    value="duration"
                  />
                  <ExcelColumn
                    label={
                      curentLanguage.locale === 'en-EN'
                        ? 'State'
                        : curentLanguage.locale === 'tr-TR'
                        ? 'Durum'
                        : curentLanguage.locale === 'nl-NL'
                        ? 'Staat'
                        : curentLanguage.locale === 'de-DE'
                        ? 'Bundesland'
                        : ''
                    }
                    value="salable"
                  />
                </ExcelSheet>
              </ExcelFile>
            )}
          </div>
        </Box>
      </Modal>
      <Paper>
        <Box
          style={{ height: '440px', width: '100%' }}
          sx={{
            '& .Calllog-Table-Listened': {
              bgcolor: getBackgroundColor('#ffeb7a', 'light'),
              '&:hover': {
                bgcolor: getHoverBackgroundColor('#ffeb7a', 'light'),
              },
            },
          }}>
          <DataGrid
            disableSelectionOnClick
            getRowClassName={params => listenedRecords.includes(params.row.num) && `Calllog-Table-Listened`}
            rows={tableData}
            columns={columns}
            rowHeight={38}
            hideFooter
            localeText={localeText}
          />
        </Box>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={count}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={tableData.length < limit || tableData.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          backIconButtonText={String(tableData.length)}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      {totalData.length > 0 && (
        <div style={{ marginTop: '10px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Call_Log_Summary'
                : curentLanguage.locale === 'tr-TR'
                ? 'Cagri_raporu_ozet'
                : curentLanguage.locale === 'nl-NL'
                ? 'Oproep_Log Samenvatting'
                : curentLanguage.locale === 'de-DE'
                ? 'Zusammenfassung des Anrufprotokolls'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={totalData} name="data">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="to_char"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Total Call Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Toplam Arama Adedi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Totaal aantal gesprekken'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Gesamtzahl der Anrufe'
                    : ''
                }
                value="count"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Answered Call Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevaplanmƒ±≈ü √áaƒürƒ± Adedi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Aantal beantwoorde oproepen'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Z√§hlung angenommener Anrufe'
                    : ''
                }
                value="answered"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Unanswered Call Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevaplanmamƒ±≈ü √áaƒürƒ± Adedi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Aantal onbeantwoorde oproepen'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anzahl unbeantworteter Anrufe'
                    : ''
                }
                value="not_answered"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Duration'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'S√ºre'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Looptijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Dauer'
                    : ''
                }
                value="totalduration"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Average Call Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Ortalama √áaƒürƒ± S√ºresi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Gemiddelde beltijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliche Anrufzeit'
                    : ''
                }
                value="averageduration"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}

      <Paper style={{ marginTop: '15px' }}>
        <TableContainer style={{ maxHeight: '440px' }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell>
                  <IntlMessages id={'dailysummary.date'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'abondenedcalls.count'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'wallboard.answered'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'wallboard.unanswered'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'missedcallcount'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'transferedcallcount'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'Duration'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'wallboard.average.time'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {totalData.map((total, index) => (
                <TableRow key={index}>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span style={{ color: '#519259' }}>
                      <DateRange style={{ verticalAlign: '-5px' }} />{' '}
                    </span>
                    <b>{total.to_char}</b>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{total.count}</b>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span
                      style={{
                        color: '#1C7947',
                      }}>
                      <Check fontSize="small" style={{ verticalAlign: '-5px' }} />
                      <b>{total.answered}</b>
                    </span>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span
                      style={{
                        color: '#852747',
                      }}>
                      <PhoneDisabled fontSize="small" style={{ verticalAlign: '-5px' }} />
                      <b>{total.not_answered}</b>
                    </span>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span
                      style={{
                        color: '#a402bd',
                      }}>
                      <PhoneMissed fontSize="small" style={{ verticalAlign: '-5px' }} />
                      <b>{total.missed}</b>
                    </span>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span
                      style={{
                        color: '#27034f',
                      }}>
                      <SendToMobileIcon fontSize="small" style={{ verticalAlign: '-5px' }} />
                      <b>{total.transfered}</b>
                    </span>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                    <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{total.totalduration}</b>
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                    <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{total.averageduration}</b>
                  </TableCell>
                </TableRow>
              ))}
              <TableRow>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span style={{ color: '#519259' }}>
                    <DateRange style={{ verticalAlign: '-5px' }} />{' '}
                  </span>
                  <b>
                    <IntlMessages id={'total'} />
                  </b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{responseTotalSummary.count}</b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span
                    style={{
                      color: '#1C7947',
                    }}>
                    <Check fontSize="small" style={{ verticalAlign: '-5px' }} /> <b>{responseTotalSummary.answered}</b>
                  </span>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span
                    style={{
                      color: '#852747',
                    }}>
                    <PhoneDisabled fontSize="small" style={{ verticalAlign: '-5px' }} />{' '}
                    <b>{responseTotalSummary.not_answered}</b>
                  </span>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span
                    style={{
                      color: '#a402bd',
                    }}>
                    <PhoneMissed fontSize="small" style={{ verticalAlign: '-5px' }} /> <b>{responseTotalSummary.missed}</b>
                  </span>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                  <span
                    style={{
                      color: '#27034f',
                    }}>
                    <SendToMobileIcon fontSize="small" style={{ verticalAlign: '-5px' }} />
                    <b>{responseTotalSummary.transfered}</b>
                  </span>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                  <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{responseTotalSummary.totalduration}</b>
                </TableCell>
                <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto', color: '#DB6B97' }}>
                  <AccessTime style={{ verticalAlign: '-5px' }} /> <b>{responseTotalSummary.averageduration}</b>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
        <Modal
          open={openAudioModal}
          onClose={() => setOpenAudioModal(false)}
          aria-labelledby="Audio"
          aria-describedby="Audio Player Modal">
          <Box style={audioModalStyle}>
            <Paper
              style={{
                display: 'flex',
                justifyContent: 'flex-end',
                borderRadius: 'inherit',
                borderBottomLeftRadius: '0',
                borderBottomRightRadius: '0',
              }}>
              <Button onClick={() => setOpenAudioModal(false)}>X</Button>
            </Paper>
            <Box style={{ padding: '12px 8px' }}>
              <AudioWaveForm buffer={record} />
            </Box>
          </Box>
        </Modal>
      </Paper>
    </>
  );
}
