import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import DataTable from './Table';
import { Grid } from '@material-ui/core';
import Graph from './Graph';
import Filter from './Filter';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';

const CallComparison = () => {
  const [response, setResponse] = useState([]);
  const [responseDaily, setResponseDaily] = useState([]);
  const [graphData, setGraphData] = useState([]);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'call.comparison'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'call.comparison'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12} sm={12} md={12} lg={12}>
          <Filter
            setGraphData={setGraphData}
            response={response}
            setResponse={setResponse}
            setResponseDaily={setResponseDaily}
          />
        </Grid>
        <Grid item xs={12} sm={12} md={6} lg={6}>
          <DataTable response={responseDaily} />
        </Grid>
        <Grid item xs={12} sm={12} md={6} lg={6}>
          <Graph array={graphData} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default CallComparison;
