import React, { useState, useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { getDailyLog, getDailyLogTable } from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid, CircularProgress } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import enLocale from 'date-fns/locale/en-US';
import trLocale from 'date-fns/locale/tr';
import { useIntl } from 'react-intl';
import deLocale from 'date-fns/locale/de';
import nlLocale from 'date-fns/locale/nl';

function Filter({ setResponse, setResponseTable }) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const classes = useStyles();
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  const today = new Date();

  const [endDate, setEndDate] = useState(formatDate(today));
  const [params, setParams] = useState({ date: formatDate(today) });

  let [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    getDailyLog(params.date).then(response => {
      setResponse(JSON.parse(response.data.data));
      setLoading(false);
    });
  }, [params.date, setResponse]);

  useEffect(() => {
    getDailyLogTable(params.date).then(resp => {
      setResponseTable(JSON.parse(resp.data.data));
    });
  }, [params.date, setResponseTable]);

  let curentLanguage = useIntl();

  useEffect(()=>{
    setParams({
      date: formatDate(endDate),
    });
  },[endDate])
  return (
    <div>
      <Card className={classes.root}>
        <CardContent>
          <GridContainer>
            <Grid item md={5}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                locale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <DatePicker
                  value={endDate}
                  onChange={newValue => setEndDate(newValue)}
                  renderInput={params => <TextField {...params} />}
                  inputVariant="outlined"
                  format="DD-MM-YYYY"
                  maxDate={new Date()}
                  label={<IntlMessages id={'filter.menu.date'} />}
                />
              </LocalizationProvider>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
