import React, { useEffect, useState } from 'react';
import { Box, Paper, Tab, Tabs, FormControl, MenuItem, Select } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import dateFormat from 'dateformat';
import { DateRange } from '@material-ui/icons';

export default function AgentDetail({ agentNum, agentName }) {
  const [agentDetail, setAgentDetail] = useState({
    dst_display_name: '-',
    totalcall: '0',
    answered_call: '0',
    inbound_call_to_agent: '0',
    outbound_call_to_agent: '0',
    totaltime: '00:00:00',
    average: '00:00:00',
    inboundtotaltime: '00:00:00',
    outboundtotaltime: '00:00:00',
    inboundaverage: '00:00:00',
    outboundaverage: '00:00:00',
  });
  let [voiceMail, setVoiceMail] = useState('0');
  let [longTime, setLongTime] = useState('00:00:00');
  let [waitingTime, setWaitingTime] = useState('00:00:00');

  const [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date(), 'yyyy-mm-dd'));
  const [dateFilter, setDateFilter] = useState('daily');

  const handleChangeFilter = e => {
    setDateFilter(e.target.value);
    switch (e.target.value) {
      case 'weekly':
        setStartTimeApi(dateFormat(new Date(new Date().setDate(new Date().getDate() - 7)), 'yyyy-mm-dd'));
        break;
      case 'daily':
        setStartTimeApi(dateFormat(new Date(), 'yyyy-mm-dd'));
        break;
      case 'monthly':
        setStartTimeApi(dateFormat(new Date(new Date().setDate(new Date().getDate() - 30)), 'yyyy-mm-dd'));
        break;
      default:
        setStartTimeApi(dateFormat(new Date(), 'yyyy-mm-dd'));
        break;
    }
  };
  let [longestCalleds, setLongestCalleds] = useState([]);
  let [longestCallers, setLongestCallers] = useState([]);
  let [longestCalledAmount, setLongestAmount] = useState([]);
  let [longestCallerAmount, setLongestCallerAmount] = useState([]);
  useEffect(() => {
    if (agentNum !== '0') {
      api.getInternalsSummaryLongestCalled(startTimeApi, dateFormat(new Date(), 'yyyy-mm-dd'), agentNum).then(result => {
        setLongestCalleds(JSON.parse(result.data.data));
      });
      api.getInternalsSummaryLongestCaller(startTimeApi, dateFormat(new Date(), 'yyyy-mm-dd'), agentNum).then(result => {
        setLongestCallers(JSON.parse(result.data.data));
      });
      api
        .getInternalsSummaryLongestCalledAmount(startTimeApi, dateFormat(new Date(), 'yyyy-mm-dd'), agentNum)
        .then(result => {
          setLongestAmount(JSON.parse(result.data.data));
        });
      api
        .getInternalsSummaryLongestCallerAmount(startTimeApi, dateFormat(new Date(), 'yyyy-mm-dd'), agentNum)
        .then(result => {
          setLongestCallerAmount(JSON.parse(result.data.data));
        });
    }
  }, [agentNum, startTimeApi]);

  useEffect(() => {
    if (agentNum !== '0') {
      api.getAgentDetails(agentNum, startTimeApi).then(res => {
        if (JSON.parse(res.data.data).length === 0) {
          setAgentDetail({
            dst_display_name: agentName,
            totalcall: '0',
            answered_call: '0',
            inbound_call_to_agent: '0',
            outbound_call_to_agent: '0',
            totaltime: '00:00:00',
            average: '00:00:00',
            inboundtotaltime: '00:00:00',
            outboundtotaltime: '00:00:00',
            inboundaverage: '00:00:00',
            outboundaverage: '00:00:00',
          });
        } else {
          setAgentDetail(JSON.parse(res.data.data)[JSON.parse(res.data.data).length-1]);
        }
      });
    }
  }, [agentNum, agentName, startTimeApi]);

  useEffect(() => {
    if (agentNum !== '0') {
      api.getWallBoardVoiceMail(agentNum).then(response => {
        setVoiceMail(JSON.parse(response.data.data)[0].count);
      });
    }
  }, [agentNum]);

  useEffect(() => {
    if (agentNum !== '0') {
      api.getWallBoardLongTime(agentNum).then(response => {
        setLongTime(
          JSON.parse(response.data.data)[0] !== undefined ? JSON.parse(response.data.data)[0].duration : '00:00:00',
        );
      });
    }
  }, [agentNum]);

  useEffect(() => {
    if (agentNum !== '0') {
      api.getWallBoardWaitingTime(agentNum).then(response => {
        setWaitingTime(
          JSON.parse(response.data.data)[0] !== undefined ? JSON.parse(response.data.data)[0].duration : '00:00:00',
        );
      });
    }
  }, [agentNum]);

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
      <Paper>
        <Box style={{ borderBottom: 1, borderColor: 'divider', backgroundColor: '#ADCE74', color: 'white' }}>
          <FormControl fullWidth variant="standard">
            <Select id="direction-select" value={dateFilter} onChange={handleChangeFilter}>
              <MenuItem value="daily">
                <DateRange />
                <IntlMessages id="daily" />
              </MenuItem>
              <MenuItem value="weekly">
                <DateRange />
                <IntlMessages id="select.menu.weekly" />
              </MenuItem>
              <MenuItem value="monthly">
                <DateRange />
                <IntlMessages id="select.menu.monthly" />
              </MenuItem>
            </Select>
          </FormControl>
        </Box>
        <div style={{ minHeight: '33vh', justifyContent: 'center', alignItems: 'center' }}>
          <Box style={{ width: '100%' }}>
            <Box style={{ borderBottom: 1, borderColor: 'divider', backgroundColor: '#ADCE74', color: 'white' }}>
              <Tabs value={value} onChange={handleChange}>
                <Tab style={{ borderRight: '3px solid #a1bf6e' }} label={<IntlMessages id={'Total'} />} {...a11yProps(0)} />
                <Tab
                  style={{ borderRight: '3px solid #a1bf6e' }}
                  label={<IntlMessages id={'Inbound'} />}
                  {...a11yProps(1)}
                />
                <Tab
                  style={{ borderRight: '3px solid #a1bf6e' }}
                  label={<IntlMessages id={'Outbound'} />}
                  {...a11yProps(2)}
                />
              </Tabs>
            </Box>
            <TabPanel value={value} index={0}>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.number'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentNum}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.name'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.dst_display_name}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.total.call'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.totalcall}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'total.time'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.totaltime
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'average'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.average
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.agentdetail.timedetails.longesttime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {longTime
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.answered'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.answered_call}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.unanswered'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.inbound_call_to_agent - agentDetail?.answered_call}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.agentdetail.timedetails.voicemail'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {voiceMail}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'internalssummary.mostcalledamount'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                      display: 'flex',
                      justifyContent: 'space-between',
                    }}>
                    <span>{longestCalledAmount[0] !== undefined ? longestCalledAmount[0].en_cok_aranan_numara : '-'}</span>
                    <b>{longestCalledAmount[0] !== undefined ? longestCalledAmount[0].count : '-'}</b>
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'internalssummary.mostcalleramount'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                      display: 'flex',
                      justifyContent: 'space-between',
                    }}>
                    <span> {longestCallerAmount[0] !== undefined ? longestCallerAmount[0].en_cok_arayan_numara : '-'}</span>
                    <b>{longestCallerAmount[0] !== undefined ? longestCallerAmount[0].count : '-'}</b>
                  </div>
                </Grid>
              </GridContainer>
            </TabPanel>
            <TabPanel value={value} index={1}>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.number'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentNum}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.name'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.dst_display_name}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'TotalInbound'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.inbound_call_to_agent}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'TotalInboundTime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.inboundtotaltime
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'AvaregeInboundTime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.inboundaverage
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'wallboard.agentdetail.timedetails.totalwaitingtime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {waitingTime
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={8}>
                  <p style={{ opacity: '.5', display: 'flex', justifyContent: 'space-between' }}>
                    <span>
                      <IntlMessages id={'internalssummary.longestcalledtime'} />
                    </span>
                    <span>
                      <IntlMessages id={'internalssummary.callednumber'} />
                    </span>
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                      display: 'flex',
                      justifyContent: 'space-between',
                    }}>
                    <b>
                      {longestCalleds[0] !== undefined
                        ? longestCalleds[0].en_uzun_aranan_s√ºre
                            .toString()
                            .replace(':', 'h ')
                            .replace(':', 'm ') + 's'
                        : '-'}
                    </b>
                    <span>{longestCalleds[0] !== undefined ? longestCalleds[0].en_uzun_aranan_numara : '-'}</span>
                  </div>
                </Grid>
              </GridContainer>
            </TabPanel>
            <TabPanel value={value} index={2}>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.number'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentNum}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'agent.name'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.dst_display_name}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'TotalOutbound'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.outbound_call_to_agent}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'TotalOutboundTime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.outboundtotaltime
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
                <Grid item md={4}>
                  <p style={{ opacity: '.5' }}>
                    <IntlMessages id={'AvaregeOutboundTime'} />
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                    }}>
                    {agentDetail?.outboundaverage
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'}
                  </div>
                </Grid>
              </GridContainer>
              <GridContainer>
                <Grid item md={8}>
                  <p style={{ opacity: '.5', display: 'flex', justifyContent: 'space-between' }}>
                    <span>
                      <IntlMessages id={'internalssummary.longestcallertime'} />
                    </span>
                    <span>
                      <IntlMessages id={'internalssummary.callernumber'} />
                    </span>
                  </p>
                  <div
                    style={{
                      borderColor: 'black',
                      borderStyle: 'solid',
                      borderWidth: '1px',
                      borderRadius: '3px',
                      padding: '10px',
                      display: 'flex',
                      justifyContent: 'space-between',
                    }}>
                    <b>
                      {longestCallers[0] !== undefined
                        ? longestCallers[0].en_uzun_aranan_s√ºre
                            .toString()
                            .replace(':', 'h ')
                            .replace(':', 'm ') + 's'
                        : '-'}
                    </b>
                    <span>{longestCallers[0] !== undefined ? longestCallers[0].en_uzun_arayan_numara : '-'}</span>
                  </div>
                </Grid>
              </GridContainer>
            </TabPanel>
          </Box>
        </div>
      </Paper>
    </div>
  );
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}>
      {value === index && (
        <Box sx={{ p: 3 }}>
          <>{children}</>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}
