import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import DateRangeIcon from '@mui/icons-material/DateRange';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { toInteger } from 'lodash';

const DataTable = ({ response }) => {
  let months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];

  let aylar = [
    'Ocak',
    '≈ûubat',
    'Mart',
    'Nisan',
    'Mayƒ±s',
    'Haziran',
    'Temmuz',
    'Aƒüustos',
    'Eyl√ºl',
    'Ekim',
    'Kasƒ±m',
    'Aralƒ±k',
  ];
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 200,
    },
  }));
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + d.getMonth();

    return { month: <IntlMessages id={`${months[parseInt(month)]}`} />, year: d.getFullYear() };
  };
  function createData(date, total) {
    return { date, total };
  }
  const rows = response.map(data => createData(formatDate(data.date_trunc), data.toplam));
  const classes = useStyles();

  let currentLanguage = useIntl();

  const colors = ['#916BBF', '#C6D57E', '#FFC069', '#F6A9A9', '#5C7AEA', '#FFF7AE'];

  function formatDateExcel(param) {
    let d = new Date(param);
    let month = parseInt(d.getMonth());

    let returnedMonth = currentLanguage.locale === 'en-US' ? months[month] : aylar[month];

    return returnedMonth + '-' + d.getFullYear();
  }

  let data = response.map(unit => ({
    date: formatDateExcel(unit.date_trunc),
    total: unit.toplam,
  }));

  let totalCall = 0;
  for (let i = 0; i < rows.length; i++) {
    totalCall += toInteger(rows[i].total);
  }

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {data.length > 0 && (
          <ExcelFile
            filename={
              currentLanguage.locale === 'en-EN'
                ? 'Monthly_log_report'
                : currentLanguage.locale === 'tr-TR'
                ? 'Aylƒ±k_yuk_raporu'
                : currentLanguage.locale === 'nl-NL'
                ? 'Maandelijks logboekrapport'
                : currentLanguage.locale === 'de-DE'
                ? 'Monatlicher Protokollbericht'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={data} name="Data">
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="date"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Total Call Count'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Toplam Arama Adedi'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Totaal aantal gesprekken'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Gesamtzahl der Anrufe'
                    : ''
                }
                value="total"
              />
            </ExcelSheet>
          </ExcelFile>
        )}
      </div>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow style={{ backgroundColor: '#2978B5', color: 'white' }}>
              <TableCell align="center">
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'filter.menu.date'} />
                </b>
              </TableCell>
              <TableCell align="center">
                <b style={{ color: 'white', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'wallboard.total.call'} />
                </b>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow style={{ backgroundColor: colors[index] }} key={index}>
                <TableCell align="center">
                  <DateRangeIcon style={{ verticalAlign: '-5px', color: 'white' }} />{' '}
                  <b style={{ fontFamily: 'Roboto' }}>
                    {row.date.month}-{row.date.year}
                  </b>
                </TableCell>
                <TableCell align="center">
                  <AddIcCallIcon style={{ verticalAlign: '-5px', color: 'white' }} />{' '}
                  <b style={{ fontFamily: 'Roboto' }}>{row.total}</b>
                </TableCell>
              </TableRow>
            ))}
            {rows.length > 1 && (
              <TableRow>
                <TableCell align="center">
                  <b>
                    <IntlMessages id={'total'} />
                  </b>
                </TableCell>
                <TableCell align="center">
                  <AddIcCallIcon style={{ verticalAlign: '-5px' }} /> <b>{totalCall}</b>
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataTable;
