import { Box, Button, Modal, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { getTagTypes, deleteTag } from 'api/api';
import React, { useEffect, useState } from 'react';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import AddTagModal from './AddTagModal';
import UpdateTagModal from './UpdateTagModal';
import DeleteIcon from '@mui/icons-material/Delete';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function TagTable() {
  let [tags, setTags] = useState([]);
  let [reloadTags,setReloadTags] = useState(0);
  useEffect(() => {
    getTagTypes().then(resp => {
        setTags(JSON.parse(resp.data.data));
    });
  }, [reloadTags]);

  const [addModal, setAddModal] = useState(false);
  const addHandleOpen = () => setAddModal(true);
  const addHandleClose = () => setAddModal(false);

  const [updateModal, setUpdateModal] = useState(false);
   let [selectedTag, setSelectedTag] = useState({ department: '', department_id: '',tagmassage:'' });
  const updateHandleOpen = () => setUpdateModal(true);
  const updateHandleClose = () => setUpdateModal(false);
  const handleClickEdit = tag => {
    setSelectedTag(tag);
    updateHandleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '50%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const handleDeleteClick = (tag) => {
    deleteTag(tag).then(() => {
      setReloadTags((prev)=> prev+1);
    });
  };

  return (
    <>
      <div style={{ display: 'flex',alignItems:"center",justifyContent:"space-between", margin: '10px 0' }}>
        <h3><IntlMessages id={'tag'} /></h3>
        <Button sx={{ backgroundColor: '#3E7C17', color: 'white' }} onClick={() => addHandleOpen()}>
          <AddIcon sx={{ verticalAlign: '-5px' }} /> <IntlMessages id={'addtag'} />
        </Button>
      </div>
      <TableContainer sx={{ maxHeight: '500px' }} component={Paper}>
        <Table stickyHeader aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>
                <IntlMessages id={'departmentname'} />
              </TableCell>
              <TableCell>
              <IntlMessages id={'tag'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'edit'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'delete'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {tags.map((item,index) => (
              <TableRow key={index}>
                <TableCell>{item.department}</TableCell>
                <TableCell>{item.tagmassage}</TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleClickEdit(item)}>
                  <EditIcon sx={{ color: '#B33030' }} />
                </TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleDeleteClick(item.tagtypes_id)}>
                  <DeleteIcon sx={{ color: '#B33030' }} />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal
        open={addModal}
        onClose={addHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <AddTagModal addHandleClose={addHandleClose} setReloadTags={setReloadTags} />
        </Box>
      </Modal>
      <Modal
        open={updateModal}
        onClose={updateHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <UpdateTagModal
            selectedTag={selectedTag}
            setReloadTags={setReloadTags}
            updateHandleClose={updateHandleClose}
          />
        </Box>
      </Modal>
    </>
  );
}
