import React, { useState, useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core';
import { getQueue, getServiceLevel } from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import { Formik, Form } from 'formik';
import { Grid } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ServiceLevelDateFilter from './DateFilter';

function Filter({ setResponse }) {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const classes = useStyles();
  const today = new Date();
  const yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [startDate, setStartDate] = useState(formatDate(yesterday));
  const [endDate, setEndDate] = useState(formatDate(today));
  const [queuelist, setQueueList] = useState([]);
  const [params, setParams] = useState({ startDate: formatDate(yesterday), endDate: formatDate(today) });
  const [queue, setQueue] = useState('');
  let string = `SELECT '10' as ring,count(q_num) as cnt FROM callcent_queuecalls_view where time_start >= '${params.startDate} 00:00:00' and time_start <= '${params.endDate} 23:59:59'
    and ring_time > interval '0 seconds' and ring_time < interval '10 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` union SELECT '20' as ring,count(q_num) FROM callcent_queuecalls_view where 
    time_start >= '${params.startDate} 00:00:00' and time_start <= '${params.endDate} 23:59:59' and ring_time > interval '10 seconds' and ring_time < interval '20 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` union
    SELECT '30' as ring,count(q_num) FROM callcent_queuecalls_view where time_start >= '${params.startDate} 00:00:00' and time_start <= '${params.endDate} 23:59:59' and
    ring_time > interval '20 seconds' and ring_time < interval '30 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` union SELECT '40' as ring,count(q_num) FROM callcent_queuecalls_view where time_start >= '${params.startDate} 00:00:00'
    and time_start <= '${params.endDate} 23:59:59' and ring_time > interval '30 seconds' and ring_time < interval '40 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` union SELECT '50' as ring,count(q_num) FROM 
    callcent_queuecalls_view where time_start >= '${params.startDate} 00:00:00' and time_start <= '${params.endDate} 23:59:59' and ring_time > interval '40 seconds' and ring_time < interval '50 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` union SELECT '60' as ring,count(q_num) FROM callcent_queuecalls_view where time_start >= '${params.startDate} 00:00:00' and time_start <= '${params.endDate} 23:59:59' and ring_time > interval '50 seconds'
    and ring_time < interval '60 seconds'`;
  if (queue !== '') {
    string += ` and q_num = '${queue}'`;
  }
  string += ` order by ring asc`;
  useEffect(() => {
    getServiceLevel(string).then(response => setResponse(JSON.parse(response.data.data)));
  }, [string, setResponse]);
  useEffect(() => {
    getQueue().then(response => setQueueList(JSON.parse(response.data.data)));
  }, []);
  const handleChangeQueue = event => {
    setQueue(event.target.value);
  };
  useEffect(() => {
    setParams({ startDate: formatDate(startDate), endDate: formatDate(endDate) });
  }, [startDate, endDate]);
  return (
    <div>
      <div style={{ marginBottom: '10px' }}>
        <ServiceLevelDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
      </div>
      <Card className={classes.root}>
        <Formik
          initialValues={{
            queueCheck: '',
          }}
          onSubmit={values => {
            setQueue(values.queueCheck);
          }}>
          {({ values, handleChange, handleSubmit }) => (
            <>
              <Form onSubmit={handleSubmit}>
                <CardContent>
                  <GridContainer>
                    <Grid item md={3} xs={3}>
                      <FormControl variant="outlined" style={{ width: '100%' }}>
                        <InputLabel id="queue-select-label">
                          <IntlMessages id={'queue.answer.report.queues'} />
                        </InputLabel>
                        <Select
                          labelId="queue-select-label"
                          id="queue-select"
                          label={<IntlMessages id={'queue.answer.report.queues'} />}
                          value={queue}
                          onChange={handleChangeQueue}>
                          <MenuItem value={''}>
                            <IntlMessages id={'all'} />
                          </MenuItem>
                          {queuelist.map((queue, index) => (
                            <MenuItem key={index} value={queue.dn}>
                              {queue.dn} {queue.display_name}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                  </GridContainer>
                </CardContent>
              </Form>
            </>
          )}
        </Formik>
      </Card>
    </div>
  );
}

export default Filter;
