import { Button, InputLabel, Select, MenuItem, Stack, OutlinedInput, FormControl } from '@mui/material';
import React, { useState, useEffect } from 'react';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { addTagTypes, getDepartmants } from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function AddTagModal({ setReloadTags, addHandleClose }) {
  const [departments, setDepartments] = useState([]);

  useEffect(() => {
    getDepartmants().then(result => {
      setDepartments(JSON.parse(result.data.data));
    });
  }, []);

  const [depId, setDepId] = useState('');
  const [depName, setDepName] = useState('');
  const [tagName, setTagName] = useState('');

  const handleDepChange = e => {
    var departmentName = departments.find(item => item.id === e.target.value).name;
    setDepName(departmentName);
    setDepId(e.target.value);
  };
  const handleSubmit = () => {
    if ((tagName !== undefined || tagName !== '') && (depId !== undefined || depId !== '')) {
      addTagTypes(depName, tagName, depId).then(() => {
        NotificationManager.success('Etiket Eklendi', '', 3000);
        setReloadTags(prev => prev + 1);
        addHandleClose();
      });
    } else {
      NotificationManager.error('Veriler doƒüru formatta deƒüil', '', 3000);
    }
  };
  return (
    <div>
      <NotificationContainer />
      <Stack direction="row" spacing={2}>
        <FormControl fullWidth>
          <InputLabel htmlFor="tag">
            <IntlMessages id={'tag'} />
          </InputLabel>
          <OutlinedInput
            id="tag"
            type="text"
            value={tagName}
            onChange={e => setTagName(e.target.value)}
            label="Etiket Adƒ±"
          />
        </FormControl>
        <FormControl fullWidth>
          <InputLabel id="depNameAddLbl">
            <IntlMessages id={'departmentname'} />
          </InputLabel>
          <Select labelId="depNameAddLbl" fullWidth value={depId} onChange={handleDepChange}>
            {departments &&
              departments.map((item, index) => (
                <MenuItem key={index} value={item.id}>
                  {item.name}
                </MenuItem>
              ))}
          </Select>
        </FormControl>
      </Stack>
      <div>
        <Button sx={{ width: '100%', marginTop: '1rem' }} color="info" variant="contained" onClick={() => handleSubmit()}>
          <IntlMessages id={'add'} />
        </Button>
      </div>
    </div>
  );
}
