import IntlMessages from '@jumbo/utils/IntlMessages';
import { Typography, TextField } from '@mui/material';
import React, { useState } from 'react';
import { sendMail } from 'api/api';
import { Box } from '@mui/system';
import { NotificationManager } from 'react-notifications';
import SendIcon from '@mui/icons-material/Send';
import LoadingButton from '@mui/lab/LoadingButton';
import { useIntl } from 'react-intl';

export default function SendAudioRecords({ path, dirs }) {
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  let curentLanguage = useIntl();
  const handleSendClick = () => {
    setLoading(true);
    if (/\S+@\S+\.\S+/.test(email)) {
      let mailSubject =
        curentLanguage.locale === 'en-EN'
          ? 'Recording(s)'
          : curentLanguage.locale === 'tr-TR'
          ? 'Kayƒ±t(lar)'
          : curentLanguage.locale === 'nl-NL'
          ? 'Opname(s)'
          : curentLanguage.locale === 'de-DE'
          ? 'Aufnahme(n)'
          : 'Recording(s)';
      let mailBody =
        curentLanguage.locale === 'en-EN'
          ? 'Recording(s) was/were attached.'
          : curentLanguage.locale === 'tr-TR'
          ? 'Kayƒ±t(lar) ektedir.'
          : curentLanguage.locale === 'nl-NL'
          ? 'Opname(s) was/waren bijgevoegd.'
          : curentLanguage.locale === 'de-DE'
          ? 'Aufzeichnung(en) war/waren angeh√§ngt.'
          : 'Recording(s) was/were attached.';
      sendMail(email, path, dirs, mailSubject, mailBody)
        .then(resp => {
          if (resp.status === 200) {
            if (resp.data.message === 'Send') {
              NotificationManager.success(<IntlMessages id={'sendingSuccess'} />, '', 3000);
              setEmail('');
              setLoading(false);
            }
          } else {
            NotificationManager.error(resp.data.message, '', 3000);
            setLoading(false);
          }
        })
        .catch(err => {
          NotificationManager.error(err.message, '', 3000);
          setLoading(false);
        });
    } else {
      NotificationManager.warning(<IntlMessages id={'invalidemail'} />, '', 3000);
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        '& .MuiTextField-root': {
          my: 1,
        },
        '&': {
          p: 1,
        },
      }}>
      <Typography variant="h5" sx={{ textAlign: 'center' }}>
        <IntlMessages id={'sendrecordtoemail'} />
      </Typography>
      <Typography variant="h6" sx={{ textAlign: 'center' }}>
        <IntlMessages id={'totalRecord'} values={{ records: dirs.length }} />
      </Typography>
      <TextField
        required
        fullWidth
        label={<IntlMessages id={'email'} />}
        type="email"
        value={email}
        onChange={e => setEmail(e.target.value)}
        variant="standard"
      />
      <LoadingButton
        size="large"
        fullWidth
        variant="contained"
        color="secondary"
        onClick={handleSendClick}
        endIcon={<SendIcon />}
        loading={loading}
        loadingPosition="end"
        sx={{ mt: 1 }}>
        <IntlMessages id={'send'} />
      </LoadingButton>
    </Box>
  );
}
