import React from 'react';
import { Line } from 'react-chartjs-2';

const HeaderChart = props => {
  const data = canvas => {
    const ctx = canvas.getContext('2d');
    const _stroke = ctx.stroke;
    let label = [];
    let countInbound = [];
    let countOutbound = [];
    let countMissed = [];
    ctx.stroke = function() {
      ctx.save();
      ctx.shadowColor = '#4C4C4C';
      ctx.shadowBlur = 10;
      ctx.shadowOffsetX = 2;
      ctx.shadowOffsetY = 2;
      _stroke.apply(this, arguments);
      ctx.restore();
    };
    const inbound = props.inbound;
    const outbound = props.outbound;
    const missed = props.missed;
    for (var i = 0; i < inbound.length; i++) {
      label[i] = inbound[i].start_time;
      countInbound[i] = inbound[i].count;
      countOutbound[i] = outbound[i] !== undefined ? outbound[i].count : 0;
      countMissed[i] = missed[i] !== undefined ? missed[i].count : 0;
    }

    return {
      labels: label,
      datasets: [
        {
          label: 'Inbound Calls',
          data: countInbound,
          borderColor: '#7F39FB',
          borderWidth: 2,
          pointBackgroundColor: '#7F39FB',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#7F39FB',
          pointHoverBorderColor: '#fff',
          pointRadius: 6,
          pointHoverRadius: 8,
          fill: false,
        },
        {
          label: 'Outbound Calls',
          data: countOutbound,
          borderColor: '#FF8C00',
          borderWidth: 2,
          pointBackgroundColor: '#FF8C00',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#FF8C00',
          pointHoverBorderColor: '#fff',
          pointRadius: 6,
          pointHoverRadius: 8,
          fill: false,
        },
        {
          label: 'Missed Calls',
          data: countMissed,
          borderColor: '#0795F4',
          borderWidth: 2,
          pointBackgroundColor: '#0795F4',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#0795F4',
          pointHoverBorderColor: '#fff',
          pointRadius: 6,
          pointHoverRadius: 8,
          fill: false,
        },
      ],
    };
  };

  const options = {
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          display: true,
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  return <Line data={data} height={100} options={options} />;
};

export default HeaderChart;
