import React from 'react';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import { Check, CloudDownload, DateRange } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { DataGrid } from '@mui/x-data-grid';
import DateRangeIcon from '@mui/icons-material/DateRange';
import PersonIcon from '@mui/icons-material/Person';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';
import RingVolumeIcon from '@mui/icons-material/RingVolume';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
import { localeText } from '@jumbo/utils/DataGridLocales';

function DataTable({ datalist, setResponse, setPage, setLimit, setOffset, limit, offset, page, count, responseSummary }) {
  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
    },
    {
      field: 'start_time',
      headerName: <IntlMessages id={'filter.menu.startdate'} />,
      width: 250,
      renderCell: params => (
        <span>
          <DateRangeIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'end_time',
      headerName: <IntlMessages id={'filter.menu.enddate'} />,
      width: 250,
      renderCell: params => (
        <span>
          <DateRangeIcon style={{ verticalAlign: '-5px', color: '#DE834D' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'from_userpart',
      headerName: <IntlMessages id={'from'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PersonIcon style={{ verticalAlign: '-5px', color: '#5584AC' }} />{' '}
          <b style={{ color: '#5584AC' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'q_num',
      headerName: <IntlMessages id={'queue'} />,
      width: 250,
      renderCell: params => (
        <span>
          <ContactPhoneIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
          <b style={{ color: '#064635' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'ringing_time',
      headerName: <IntlMessages id={'ringingtime'} />,
      width: 250,
      renderCell: params => (
        <span>
          <RingVolumeIcon style={{ verticalAlign: '-5px', color: '#A3423C' }} />{' '}
          <b style={{ color: '#781D42' }}>{params.value}</b>
        </span>
      ),
    },
  ];

  function createData(num, id, q_num, start_time, end_time, ringing_time, from_userpart) {
    return { num, id, q_num, start_time, end_time, ringing_time, from_userpart };
  }

  const rows = datalist.map((data, index) =>
    createData(
      index + 1 + offset,
      index,
      data.q_num,
      data.time_start,
      data.time_end,
      data.ring_time
        .split('.')[0]
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      data.from_userpart,
    ),
  );

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDowlandButtonClick = () => {
    setLimit(count.count);
    setPage(0);
    setOffset(0);
    setResponse([]);
    handleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  let curentLanguage = useIntl();
  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {rows.length > 0 && (
          <Button onClick={() => handleDowlandButtonClick()} style={{ backgroundColor: '#3f51b5', color: 'white' }}>
            <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          </Button>
        )}
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description">
          <Box sx={style}>
            {rows.length < 1 && (
              <div style={{ textAlign: 'center' }}>
                <CircularProgress />
                <p>Veriler hazirlanƒ±yor...</p>
              </div>
            )}
            <div style={{ textAlign: 'center' }}>
              {rows.length < 1 && (
                <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              )}
              {rows.length > 0 && (
                <ExcelFile
                  filename={
                    curentLanguage.locale === 'en-EN'
                      ? 'Dropped_call'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Kacan_cagri_raporu'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Afgebroken oproep'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Aufgelegter Anruf'
                      : ''
                  }
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={rows} name="Data">
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Queue Number'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Kuyruk Numarasƒ±'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Wachtrijnummer'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Warteschlangennummer'
                          : ''
                      }
                      value="q_num"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Start Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Ba≈ülangƒ±√ß Zamanƒ±'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Starttijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Startzeit'
                          : ''
                      }
                      value="start_time"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'End Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Biti≈ü Zamanƒ±'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Starttijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Eindtijd'
                          : ''
                      }
                      value="end_time"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Ringing Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? '√áalma S√ºresi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Beltijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Klingelzeit'
                          : ''
                      }
                      value="ringing_time"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'From Userpart'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Arayan Numara'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Van gebruikersgedeelte'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Vom Benutzerteil'
                          : ''
                      }
                      value="from_userpart"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
          </Box>
        </Modal>
      </div>
      <Paper>
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid rows={rows} columns={columns} hideFooter localeText={localeText} />
        </div>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={parseInt(count.count)}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={datalist.length < limit || datalist.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          backIconButtonText={String(rows.length)}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>

      <Paper style={{ marginTop: '15px' }}>
        <TableContainer style={{ maxHeight: '440px' }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell>
                  <IntlMessages id={'dailysummary.date'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'abondenedcalls.count'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'average_ring_time'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {responseSummary.map((total, index) => (
                <TableRow key={index}>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    {' '}
                    <span style={{ color: '#519259' }}>
                      <DateRange style={{ verticalAlign: '-5px' }} />{' '}
                    </span>
                    {total.to_char}
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    {total.count}
                  </TableCell>
                  <TableCell style={{ paddingTop: '2px', paddingBottom: '2px', fontFamily: 'Roboto' }}>
                    <span
                      style={{
                        backgroundColor: '#B1E693',
                        color: '#1C7947',
                        padding: '5px',
                        borderRadius: '7px',
                        fontSize: '12px',
                        fontWeight: '800',
                      }}>
                      <Check fontSize="small" style={{ verticalAlign: '-5px' }} />
                      {total.ring_time}
                    </span>{' '}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </>
  );
}

export default DataTable;
