import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import * as api from '../../../../api/api';
import Paper from '@material-ui/core/Paper';
import { lighten, makeStyles } from '@material-ui/core/styles';
import IntlMessages from '@jumbo/utils/IntlMessages';
import PersonIcon from '@mui/icons-material/Person';
import TimerIcon from '@mui/icons-material/Timer';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const AgentStatus = ({ setBreakeAgentAmount, setLoginAgentAmount, setAgentNum, setAgentName }) => {
  var now = new Date();
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  const [response, setResponse] = useState([]);

  useEffect(() => {
    reloadValues();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const classes = useStyles();

  const convertToLocalTime = date_ => {
    var date = new Date(date_);
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var dt = date.getDate();

    if (dt < 10) {
      dt = '0' + dt;
    }
    if (month < 10) {
      month = '0' + month;
    }

    return year + '-' + month + '-' + dt;
  };

  const reloadValues = () => {
    if (window.location.pathname === '/wallboard') {
      api.getAgentStatus(convertToLocalTime(now)).then(resp => {
        setResponse(JSON.parse(resp.data.data));
      });
      setValuesFunction();
    }
    var agentStatusTimeout = setTimeout(reloadValues, 5000);
    if (window.location.pathname !== '/wallboard') {
      clearTimeout(agentStatusTimeout);
    }
  };

  const setValuesFunction = () => {
    let breakeAgenAmaunt = 0;
    let loginAgentAmaunt = 0;
    for (let i = 0; i < response.length; i++) {
      if (response[i].states !== 'Login' && response[i].states !== 'State' && response[i].states !== 'Logout') {
        breakeAgenAmaunt++;
      } else if (response[i].states === 'Login') {
        loginAgentAmaunt++;
      }
    }
    setBreakeAgentAmount(breakeAgenAmaunt);
    setLoginAgentAmount(loginAgentAmaunt);
  };

  const onClickUptateValuesFunction = (agentNum, agentName) => {
    setAgentNum(agentNum);
    setAgentName(agentName);
  };

  const column = [
    { id: 'agent_name', headerName: <IntlMessages id={'internals.summary.agent'} />, width: '30px' },
    { id: 'states', headerName: <IntlMessages id={'state'} />, width: '60px' },
    { id: 'statustime', headerName: <IntlMessages id={'state.time'} />, width: '100px' },
  ];

  return (
    <TableContainer
      style={{ maxHeight: '33vh', boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}
      component={Paper}
      className={classes.root}>
      <Table stickyHeader className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
        <TableHead>
          <TableRow>
            {column.map(column => (
              <TableCell
                key={column.id}
                style={{
                  paddingTop: '8px',
                  paddingBottom: '8px',
                  fontFamily: 'Roboto',
                  color: 'white',
                  backgroundColor: '#FA744F',
                }}>
                {column.headerName}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {response.map((res, index) =>
            res.states === 'Login' ? (
              <TableRow key={index} style={{ backgroundColor: '#80ED99', cursor: 'pointer' }}>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{res.display_name}</b>{' '}
                  <b>{'(' + res.dn + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <TimerIcon style={{ verticalAlign: '-5px', color: '#35589A' }} /> <b>{res.states}</b>
                </TableCell>

                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime) !== undefined
                      ? (res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime)
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null,
          )}
        </TableBody>
        <TableBody>
          {response.map((res, index) =>
            res.states !== 'Login' && res.states !== 'State' && res.states !== 'Logout' ? (
              <TableRow
                key={index}
                style={{
                  backgroundColor:
                    res.states === 'YEMEK' && parseInt(res.statustime.split(':')[0]) >= 1
                      ? '#DD4A48'
                      : res.states === 'WC' && parseInt(res.statustime.split(':')[1]) >= 5
                      ? '#DD4A48'
                      : '#F3950D',
                  cursor: 'pointer',
                }}>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{res.display_name}</b>{' '}
                  <b>{'(' + res.dn + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <TimerIcon style={{ verticalAlign: '-5px', color: '#35589A' }} /> <b>{res.states}</b>
                </TableCell>

                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime) !== undefined
                      ? (res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime)
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null,
          )}
        </TableBody>

        <TableBody>
          {response.map((res, index) =>
            res.states === 'Logout' ? (
              <TableRow key={index} style={{ backgroundColor: '#DE834D', cursor: 'pointer' }}>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{res.display_name}</b>{' '}
                  <b>{'(' + res.dn + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <TimerIcon style={{ verticalAlign: '-5px', color: '#35589A' }} /> <b>{res.states}</b>
                </TableCell>

                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime) !== undefined
                      ? (res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime)
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null,
          )}
        </TableBody>

        <TableBody>
          {response.map((res, index) =>
            res.states === 'State' ? (
              <TableRow key={index} style={{ cursor: 'pointer' }}>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <PersonIcon style={{ verticalAlign: '-5px', color: '#FF5959' }} /> <b>{res.display_name}</b>{' '}
                  <b>{'(' + res.dn + ')'}</b>
                </TableCell>
                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <TimerIcon style={{ verticalAlign: '-5px', color: '#35589A' }} />{' '}
                  <b>{res.states === 'State' ? <IntlMessages id={'passive'} /> : res.states}</b>
                </TableCell>

                <TableCell
                  onClick={() => onClickUptateValuesFunction(res.dn, res.display_name)}
                  style={{ paddingTop: '1px', paddingBottom: '1px', fontFamily: 'Roboto' }}>
                  <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#F2789F' }} />{' '}
                  <b>
                    {(res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime) !== undefined
                      ? (res.statustime === '416.16:00:00' ? '00:00:00' : res.statustime)
                          .toString()
                          .replace(':', 'h ')
                          .replace(':', 'm ') + 's'
                      : null}
                  </b>
                </TableCell>
              </TableRow>
            ) : null,
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
export default AgentStatus;
