import React, { useState, useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import {
  getQueueNumber,
  getRepetetiveAll,
  getRepetetiveAnswered,
  getDidNumbers,
  getRepetetiveAllCount,
  getRepetetiveAnsweredCount,
} from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { CircularProgress, FormControl, Grid, InputLabel, MenuItem, Select } from '@material-ui/core';
import { getRepetetiveCallerDetails } from 'api/api';
import RepadativeCallsDateFilter from './DateFilter';

function Filter({
  setallcalls,
  limit,
  allCallOffset,
  answeredLimit,
  answeredCallsOffset,
  setAnsweredCalls,
  callerNumber,
  setResponseCaller,
  setAllCallCount,
  setAnsweredCallCount,
}) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const classes = useStyles();
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  const today = new Date();
  const yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [startDate, setStartDate] = useState(formatDate(yesterday));
  const [endDate, setEndDate] = useState(formatDate(today));
  const [params, setParams] = useState({
    paramStart: formatDate(yesterday),
    paramFinish: formatDate(today),
  });

  const [queue, setQueue] = useState('');
  let [loading, setLoading] = useState(true);
  const [queuelist, setQueueList] = useState([]);
  const [didList, setDidList] = useState([]);
  const [didNumber, setDidNumber] = useState('');

  useEffect(() => {
    setLoading(true);
    getRepetetiveAll(params, limit, allCallOffset, queue, didNumber).then(response => {
      setallcalls(JSON.parse(response.data.data));
      setLoading(false);
    });
    getRepetetiveAllCount(params, limit, allCallOffset, queue, didNumber).then(response => {
      setAllCallCount(parseInt(JSON.parse(response.data.data)[0].cnt));
    });
  }, [params, limit, allCallOffset, queue, setallcalls, setAllCallCount, didNumber]);
  useEffect(() => {
    getRepetetiveAnswered(params, answeredLimit, answeredCallsOffset, queue).then(response =>
      setAnsweredCalls(JSON.parse(response.data.data)),
    );

    getRepetetiveAnsweredCount(params).then(response => {
      setAnsweredCallCount(parseInt(JSON.parse(response.data.data)[0].cnt));
    });
  }, [params, answeredLimit, answeredCallsOffset, setAnsweredCalls, setAnsweredCallCount, queue]);

  useEffect(() => {
    getQueueNumber().then(response => {
      setQueueList(JSON.parse(response.data.data));
    });
  }, []);

  useEffect(() => {
    getDidNumbers().then(resp => {
      setDidList(JSON.parse(resp.data.data));
    });
  }, []);

  useEffect(() => {
    getRepetetiveCallerDetails(params, callerNumber).then(resp => {
      setResponseCaller(JSON.parse(resp.data.data));
    });
  }, [params, callerNumber, setQueueList, setDidList, setResponseCaller]);

  useEffect(()=>{
    setParams({
      paramStart: formatDate(startDate),
      paramFinish: formatDate(endDate),
    });
  },[startDate,endDate])
  const handleChangeQueueNumber = event => {
    setQueue(event.target.value);
  };

  const handleChangeDidNumber = event => {
    setDidNumber(event.target.value);
  };

  return (
    <div>
      <RepadativeCallsDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
      <div style={{ marginTop: '10px' }}>
        <Card className={classes.root}>
          <CardContent>
            <GridContainer>
              <Grid item md={3}>
                <FormControl fullWidth variant="outlined">
                  <InputLabel id="agent-select-label">
                    <IntlMessages id={'queue'} />
                  </InputLabel>
                  <Select
                    labelId="agent-select-label"
                    id="agent-select"
                    value={queue}
                    onChange={handleChangeQueueNumber}
                    label={<IntlMessages id={'queue'} />}>
                    <MenuItem value="">
                      <IntlMessages id={'all'} />
                    </MenuItem>
                    {queuelist.map((queue, index) => (
                      <MenuItem key={index} value={queue.dn}>
                        {queue.dn} - {queue.display_name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              <Grid item md={3}>
                <FormControl fullWidth variant="outlined">
                  <InputLabel id="agent-select-label">
                    <IntlMessages id={'mountlydetails.didnumber'} />
                  </InputLabel>
                  <Select
                    labelId="agent-select-label"
                    id="agent-select"
                    value={didNumber}
                    onChange={handleChangeDidNumber}
                    label={<IntlMessages id={'mountlydetails.didnumber'} />}>
                    <MenuItem value="">
                      <IntlMessages id={'all'} />
                    </MenuItem>
                    {didList.map((did, index) => (
                      <MenuItem key={index} value={did.conditiondata}>
                        {did.conditiondata}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            </GridContainer>
          </CardContent>
        </Card>
      </div>

      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
