import { Box, Button, Modal, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { deleteDepOwnedAllQueues, deleteDepWithDepId, deleteUserAllDepsWithDepId, getDepartmants } from 'api/api';
import React, { useEffect, useState } from 'react';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import AddDepartmanModal from './AddDepartmanModal';
import UpdateDepartmanModal from './UpdateDepartmanModal';
import DeleteIcon from '@mui/icons-material/Delete';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function DepartmantTable({ reloadValues, setReloadValues }) {
  let [deps, setDeps] = useState([]);
  useEffect(() => {
    getDepartmants().then(result => {
      setDeps(JSON.parse(result.data.data));
    });
  }, [reloadValues]);

  const [addModal, setAddModal] = useState(false);
  const addHandleOpen = () => setAddModal(true);
  const addHandleClose = () => setAddModal(false);

  const [updateModal, setUpdateModal] = useState(false);
  let [selectedDep, setSelectedDep] = useState({ id: 0, name: '' });
  const updateHandleOpen = () => setUpdateModal(true);
  const updateHandleClose = () => setUpdateModal(false);
  const handleClickEdit = dep => {
    setSelectedDep(dep);
    updateHandleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '50%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const handleDeleteClick = depId => {
    deleteDepOwnedAllQueues(depId).then(res1 => {
      deleteUserAllDepsWithDepId(depId).then(res2 => {
        deleteDepWithDepId(depId).then(res3 => {
          setReloadValues(depId.toString());
        });
      });
    });
  };

  return (
    <>
      <div style={{ textAlign: 'right', marginBottom: '10px' }}>
        <Button sx={{ backgroundColor: '#3E7C17', color: 'white' }} onClick={() => addHandleOpen()}>
          <AddIcon sx={{ verticalAlign: '-5px' }} /> <IntlMessages id={'adddepartment'} />
        </Button>
      </div>
      <TableContainer component={Paper}>
        <Table sx={{ maxHeight: '500px' }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>
                <IntlMessages id={'departmentname'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'edit'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'delete'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {deps.map(dep => (
              <TableRow key={dep.id}>
                <TableCell>{dep.name}</TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleClickEdit(dep)}>
                  <EditIcon sx={{ color: '#B33030' }} />
                </TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleDeleteClick(dep.id)}>
                  <DeleteIcon sx={{ color: '#B33030' }} />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal
        open={addModal}
        onClose={addHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <AddDepartmanModal addHandleClose={addHandleClose} setReloadValues={setReloadValues} />
        </Box>
      </Modal>
      <Modal
        open={updateModal}
        onClose={updateHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <UpdateDepartmanModal
            selectedDep={selectedDep}
            setReloadValues={setReloadValues}
            updateHandleClose={updateHandleClose}
          />
        </Box>
      </Modal>
    </>
  );
}
