import React, { useEffect, useState } from 'react';
import { getTerminatedCallsByAgent, getTerminatedCallsByAgentCount } from 'api/api';
import CallTerminationReportDateFilter from './DateFilter';
import CallTerminationReportFilters from './Filters';
import dateFormat from 'dateformat';

export default function CallTerminationReportFilter({ setData, setTotalCount, limit, offset }) {
  const [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0), 'yyyy-mm-dd HH:MM:ss'));
  const [endTimeApi, setEndTimeApi] = useState(dateFormat(new Date().setHours(23, 59, 59), 'yyyy-mm-dd HH:MM:ss'));

  const [callTerminator, setCallTerminator] = useState('');
  useEffect(() => {
    getTerminatedCallsByAgent(startTimeApi, endTimeApi, limit, offset, callTerminator).then(resp =>
      setData(JSON.parse(resp.data.data)),
    );
    getTerminatedCallsByAgentCount(startTimeApi, endTimeApi, callTerminator).then(resp => {
      setTotalCount(parseInt(JSON.parse(resp.data.data)[0].count));
    });
  }, [startTimeApi, endTimeApi, setData, setTotalCount, limit, offset, callTerminator]);
  return (
    <div style={{ margin: '20px 0' }}>
      <CallTerminationReportDateFilter setStartTimeApi={setStartTimeApi} setEndTimeApi={setEndTimeApi} />
      <CallTerminationReportFilters setCallTerminator={setCallTerminator} />
    </div>
  );
}
