import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, Card, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import React, { useState } from 'react';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';
import { DataGrid } from '@mui/x-data-grid';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import PermPhoneMsgIcon from '@mui/icons-material/PermPhoneMsg';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PersonOffIcon from '@mui/icons-material/PersonOff';
import TransferWithinAStationIcon from '@mui/icons-material/TransferWithinAStation';
import PhoneForwardedIcon from '@mui/icons-material/PhoneForwarded';
import CallIcon from '@mui/icons-material/Call';
import TotalCallModal from '../Modals/TotalCallModal';
import { Box, Modal } from '@mui/material';
import AnsweredCallModal from '../Modals/AnsweredCallModal';
import AbondenedCallModal from '../Modals/AbondenedCallModal';
import TransferCallModal from '../Modals/TransferCallModal';
import OutOfTimeModal from '../Modals/OutOfTimeModal';
import RonaCallModla from '../Modals/RonaCallModla';
import OutCallModal from '../Modals/OutCallModal';
import { localeText } from '@jumbo/utils/DataGridLocales';
import dateFormat from 'dateformat';
export default function DailySummaryReportTable({ data, dataTotal }) {
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '80%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  let csvData = data.map(dataUnit => ({
    datetime: dateFormat(dataUnit.datetime, 'yyyy-mm-dd'),
    totalcall: dataUnit.totalcall,
    answered: dataUnit.answered,
    inboundtime: dataUnit.inboundtime,
    waitingtime: dataUnit.waitingtime,
    averageconnection: dataUnit.averageconnection,
    abondened: dataUnit.abondened,
    transfer: dataUnit.transfer,
    long_time: dataUnit.long_time,
    zdisi: dataUnit.zdisi,
    average_abondened_waiting: dataUnit.average_abondened_waiting,
    rona: dataUnit.rona,
    outbound: dataUnit.outbound,
    outbound_time: dataUnit.outbound_time,
    average_outbound_time: dataUnit.average_outbound_time,
  }));

  let currentLanguage = useIntl();

  let dataGridRows = data.map((row, index) => ({
    num: index + 1,
    id: index,
    date: dateFormat(row.datetime, 'dd-mm-yyyy'),
    totalCall: parseInt(row.totalcall),
    answered: parseInt(row.answered),
    inboundTime: row.inboundtime.replace(':', 'h ').replace(':', 'm ') + 's',
    waitingTime: row.waitingtime.replace(':', 'h ').replace(':', 'm ') + 's',
    averageTime: row.averageconnection.replace(':', 'h ').replace(':', 'm ') + 's',
    abondened: parseInt(row.abondened),
    transfer: parseInt(row.transfer),
    longestTime: row.long_time.replace(':', 'h ').replace(':', 'm ') + 's',
    outOfTime: parseInt(row.zdisi),
    averageAbondenedWaiting: row.average_abondened_waiting.replace(':', 'h ').replace(':', 'm ') + 's',
    rona: parseInt(row.rona),
    outbound: parseInt(row.outbound),
    outboundTotalTime: row.outbound_time.replace(':', 'h ').replace(':', 'm ') + 's',
    outboundAverageTime: row.average_outbound_time.replace(':', 'h ').replace(':', 'm ') + 's',
  }));

  const [totalCallModalOpen, setTotalCallOpen] = useState(false);
  const [totalCallDate, setTotalCallDate] = useState('');
  const handleTotalCallOpen = () => setTotalCallOpen(true);
  const handleTotalCallClose = () => setTotalCallOpen(false);
  const handleTotalCallClick = date => {
    setTotalCallDate(date);
    handleTotalCallOpen();
  };
  const [answeredCallModalOpen, setAnsweredCallOpen] = useState(false);
  const [answeredCallDate, setAnsweredCallDate] = useState('');
  const handleAnsweredCallOpen = () => setAnsweredCallOpen(true);
  const handleAnsweredCallClose = () => setAnsweredCallOpen(false);
  const handleAnsweredCallClick = date => {
    setAnsweredCallDate(date);
    handleAnsweredCallOpen();
  };
  const [abondenedCallModalOpen, setAbondenedCallOpen] = useState(false);
  const [abondenedCallDate, setAbondenedCallDate] = useState('');
  const handleAbondenedCallOpen = () => setAbondenedCallOpen(true);
  const handleAbondenedCallClose = () => setAbondenedCallOpen(false);
  const handleAbondenedCallClick = date => {
    setAbondenedCallDate(date);
    handleAbondenedCallOpen();
  };
  const [transferCallModalOpen, setTransferCallOpen] = useState(false);
  const [transferCallDate, setTransferCallDate] = useState('');
  const handleTransferCallOpen = () => setTransferCallOpen(true);
  const handleTransferCallClose = () => setTransferCallOpen(false);
  const handleTransferCallClick = date => {
    setTransferCallDate(date);
    handleTransferCallOpen();
  };

  const [outOfTimeModalOpen, setOutOfTimeModalOpen] = useState(false);
  const [outOfTimeDate, setOutOfTimeDate] = useState('');
  const handleOutOfTimeOpen = () => setOutOfTimeModalOpen(true);
  const handleOutOfTimeClose = () => setOutOfTimeModalOpen(false);
  const handleOutOfTimeClick = date => {
    setOutOfTimeDate(date);
    handleOutOfTimeOpen();
  };
  const [ronaCallModalOpen, setRonaCallOpen] = useState(false);
  const [ronaCallDate, setRonaCallDate] = useState('');
  const handleRonaCallOpen = () => setRonaCallOpen(true);
  const handleRonaCallClose = () => setRonaCallOpen(false);
  const handleRonaCallClick = date => {
    setRonaCallDate(date);
    handleRonaCallOpen();
  };
  const [outCallModalOpen, setOutCallOpen] = useState(false);
  const [outCallDate, setOutCallDate] = useState('');
  const handleOutCallOpen = () => setOutCallOpen(true);
  const handleOutCallClose = () => setOutCallOpen(false);
  const handleOutCallClick = date => {
    setOutCallDate(date);
    handleOutCallOpen();
  };

  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
    },
    {
      field: 'date',
      headerName: <IntlMessages id={'dailysummary.date'} />,
      width: 150,
      renderCell: params => (
        <span>
          <CalendarTodayIcon style={{ verticalAlign: '-5px', color: '#6998AB' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'totalCall',
      headerName: <IntlMessages id={'dailysummary.totalcall'} />,
      width: 170,
      renderCell: params => (
        <span
          onClick={() =>
            handleTotalCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ cursor: 'pointer' }}>
          <AddIcCallIcon style={{ verticalAlign: '-5px', color: '#406882' }} />{' '}
          <b style={{ color: '#406882' }}>{params.row.totalCall}</b>
        </span>
      ),
    },
    {
      field: 'answered',
      headerName: <IntlMessages id={'dailysummary.answered'} />,
      width: 190,
      renderCell: params => (
        <span
          onClick={() =>
            handleAnsweredCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ cursor: 'pointer' }}>
          <PermPhoneMsgIcon style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
          <b style={{ color: '#519259' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'inboundTime',
      headerName: <IntlMessages id={'dailysummary.inboundtime'} />,
      width: 200,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'waitingTime',
      headerName: <IntlMessages id={'dailysummary.waitingtime'} />,
      width: 190,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'averageTime',
      headerName: <IntlMessages id={'dailysummary.avaragetime'} />,
      width: 190,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'abondened',
      headerName: <IntlMessages id={'dailysummary.abondened'} />,
      width: 170,
      renderCell: params => (
        <span
          onClick={() =>
            handleAbondenedCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ cursor: 'pointer', color: '#DD4A48' }}>
          <PersonOffIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'transfer',
      headerName: <IntlMessages id={'dailysummary.transfer'} />,
      renderCell: params => (
        <span
          onClick={() =>
            handleTransferCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ cursor: 'pointer', color: '#396EB0' }}>
          <TransferWithinAStationIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'longestTime',
      headerName: <IntlMessages id={'dailysummary.longesttime'} />,
      width: 150,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'outOfTime',
      headerName: <IntlMessages id={'dailysummary.outoftime'} />,
      width: 230,
      renderCell: params => (
        <span
          onClick={() =>
            handleOutOfTimeClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ color: '#C85C5C', cursor: 'pointer' }}>
          <PhoneForwardedIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'averageAbondenedWaiting',
      headerName: <IntlMessages id={'dailysummary.averageabondenedwaiting'} />,
      width: 260,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'rona',
      headerName: <IntlMessages id={'dailysummary.rona'} />,
      renderCell: params => (
        <span
          onClick={() =>
            handleRonaCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ color: '#C85C5C', cursor: 'pointer' }}>
          <PhoneForwardedIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'outbound',
      headerName: <IntlMessages id={'dailysummary.outboundcount'} />,
      width: 170,
      renderCell: params => (
        <span
          onClick={() =>
            handleOutCallClick(
              params.row.date.split('-')[2] + '-' + params.row.date.split('-')[1] + '-' + params.row.date.split('-')[0],
            )
          }
          style={{ color: '#519259', cursor: 'pointer' }}>
          <CallIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'outboundTotalTime',
      headerName: <IntlMessages id={'dailysummary.outboundtotaltime'} />,
      width: 200,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'outboundAverageTime',
      headerName: <IntlMessages id={'dailysummary.outboundaveragetime'} />,
      width: 220,
      renderCell: params => (
        <span style={{ color: '#DB6B97' }}>
          <AccessTimeIcon style={{ verticalAlign: '-5px' }} /> <b>{params.value}</b>
        </span>
      ),
    },
  ];
  let curentLanguage = useIntl();

  return (
    <div style={{ marginTop: '20px' }}>
      {csvData.length > 0 && (
        <div style={{ marginBottom: '20px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Daily_Summary'
                : curentLanguage.locale === 'tr-TR'
                ? 'Gunluk_ozet'
                : curentLanguage.locale === 'nl-NL'
                ? 'Dagelijkse samenvatting'
                : curentLanguage.locale === 'de-DE'
                ? 'T√§gliche Zusammenfassung'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={csvData} name="Data">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="datetime"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Total Call'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Toplam √áaƒürƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Totaal gesprek'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anruf insgesamt'
                    : ''
                }
                value="totalcall"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Answered Call Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Cevaplanmƒ±≈ü √áaƒürƒ± Adedi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Aantal beantwoorde oproepen'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Z√§hlung angenommener Anrufe'
                    : ''
                }
                value="answered"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Inbound Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Gelen √áaƒürƒ± S√ºresi Toplamƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Inkomende tijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Eingangszeit'
                    : ''
                }
                value="inboundtime"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Waiting Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Bekleme S√ºresi Toplamƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Wachttijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Wartezeit'
                    : ''
                }
                value="waitingtime"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Average Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Ortalama √áaƒürƒ± S√ºresi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Gemiddelde tijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliche Zeit'
                    : ''
                }
                value="averageconnection"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Abandoned'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Terkeden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Verlaten'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Verlassen'
                    : ''
                }
                value="abondened"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Transfer'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Transfer'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Transfer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Transfer'
                    : ''
                }
                value="transfer"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Longest Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'En uzun s√ºre'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Langste tijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'L√§ngste Zeit'
                    : ''
                }
                value="long_time"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Out Of Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Kuyruk dƒ±≈üƒ± ta≈üanlar'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Geen tijd meer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Au√üerhalb der Zeit'
                    : ''
                }
                value="zdisi"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Average Abadoned Waiting'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Ortalama Terkeden Bekleme'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Gemiddeld verlaten wachten'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliches verlassenes Warten'
                    : ''
                }
                value="average_abondened_waiting"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Rona'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Rona'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Rona'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Rona'
                    : ''
                }
                value="rona"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Outbound Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Giden √áaƒürƒ± Sayƒ±sƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Uitgaande telling'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Ausgehender Z√§hler'
                    : ''
                }
                value="outbound"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Outbound Total Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Giden √áaƒürƒ± toplam s√ºresi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Uitgaande totale tijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Ausgehend Gesamtzeit'
                    : ''
                }
                value="outbound_time"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Outbound Average Time'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Giden √ßaƒürƒ± ortalama s√ºresi'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Gemiddelde tijd uitgaan'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Durchschnittliche ausgehende Zeit'
                    : ''
                }
                value="average_outbound_time"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Card style={{ marginBottom: '15px' }}>
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid
            rows={dataGridRows}
            columns={columns}
            localeText={localeText}
            componentsProps={{
              pagination: {
                labelRowsPerPage: <IntlMessages id={'rows.per.page'} />,
              },
            }}
          />
        </div>
      </Card>
      <Card>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 150 }}>
                  <IntlMessages id={'dailysummary.date'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 170 }}>
                  <IntlMessages id={'dailysummary.totalcall'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 190 }}>
                  <IntlMessages id={'dailysummary.answered'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 200 }}>
                  <IntlMessages id={'dailysummary.inboundtime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 190 }}>
                  <IntlMessages id={'dailysummary.waitingtime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 190 }}>
                  <IntlMessages id={'dailysummary.avaragetime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 170 }}>
                  <IntlMessages id={'dailysummary.abondened'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'dailysummary.transfer'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 150 }}>
                  <IntlMessages id={'dailysummary.longesttime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 230 }}>
                  <IntlMessages id={'dailysummary.outoftime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 260 }}>
                  <IntlMessages id={'dailysummary.averageabondenedwaiting'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto' }}>
                  <IntlMessages id={'dailysummary.rona'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 170 }}>
                  <IntlMessages id={'dailysummary.outboundcount'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 200 }}>
                  <IntlMessages id={'dailysummary.outboundtotaltime'} />
                </TableCell>
                <TableCell style={{ fontWeight: '800', fontFamily: 'Roboto', minWidth: 220 }}>
                  <IntlMessages id={'dailysummary.outboundaveragetime'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {dataTotal.map((data, index) => (
                <TableRow key={index} style={{ backgroundColor: '#80ED99' }}>
                  <TableCell>
                    {' '}
                    <IntlMessages id={'total'} />{' '}
                  </TableCell>
                  <TableCell>{data.totalcall}</TableCell>
                  <TableCell>{data.answered}</TableCell>
                  <TableCell>{data.inboundtime.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.waitingtime.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.averageconnection.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.abondened}</TableCell>
                  <TableCell>{data.transfer}</TableCell>
                  <TableCell>{data.long_time.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.zdisi}</TableCell>
                  <TableCell>{data.average_abondened_waiting.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.rona}</TableCell>
                  <TableCell>{data.outbound}</TableCell>
                  <TableCell>{data.outbound_time.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                  <TableCell>{data.average_outbound_time.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Card>
      <Modal
        open={totalCallModalOpen}
        onClose={handleTotalCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <TotalCallModal date={totalCallDate} handleTotalCallClose={handleTotalCallClose} />
        </Box>
      </Modal>
      <Modal
        open={answeredCallModalOpen}
        onClose={handleAnsweredCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <AnsweredCallModal date={answeredCallDate} handleAnsweredCallClose={handleAnsweredCallClose} />
        </Box>
      </Modal>
      <Modal
        open={abondenedCallModalOpen}
        onClose={handleAbondenedCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <AbondenedCallModal date={abondenedCallDate} handleAbondenedCallClose={handleAbondenedCallClose} />
        </Box>
      </Modal>
      <Modal
        open={outOfTimeModalOpen}
        onClose={handleOutOfTimeClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <OutOfTimeModal date={outOfTimeDate} handleOutOfTimeClose={handleOutOfTimeClose} />
        </Box>
      </Modal>
      <Modal
        open={ronaCallModalOpen}
        onClose={handleRonaCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <RonaCallModla date={ronaCallDate} handleRonaCallClose={handleRonaCallClose} />
        </Box>
      </Modal>
      <Modal
        open={transferCallModalOpen}
        onClose={handleTransferCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <TransferCallModal date={transferCallDate} handleTransferCallClose={handleTransferCallClose} />
        </Box>
      </Modal>
      <Modal
        open={outCallModalOpen}
        onClose={handleOutCallClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <OutCallModal date={outCallDate} handleOutCallClose={handleOutCallClose} />
        </Box>
      </Modal>
    </div>
  );
}
