import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import DepartmentQueues from './DepartmentQueues/DepartmentQueues';
import DepartmantTable from './DeportmentTable/DepartmantTable';

export default function DepartmansSettigns() {
  const breadcrumbs = [
    { label: <IntlMessages id={'settings'} />, link: '/' },
    { label: <IntlMessages id={'departmentsettings'} />, isActive: true },
  ];
  let [reloadValues, setReloadValues] = useState('');
  return (
    <div>
      <PageContainer heading={<IntlMessages id={'departmentsettings'} />} breadcrumbs={breadcrumbs}>
        <DepartmantTable reloadValues={reloadValues} setReloadValues={setReloadValues} />
        <DepartmentQueues reloadValues={reloadValues} />
      </PageContainer>
    </div>
  );
}
