import React, { useState, useEffect } from 'react';
import { getDropCall, getDropCallCount, getDropCallSummary, getQueueNumber } from 'api/api';
import { CircularProgress } from '@material-ui/core';
import DropCallLogDateFilter from './DateFilter';
function Filter({ setResponse, limit, offset, setCount, setResponseSummary }) {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  const dateDifference = param => {
    var today = new Date();
    var paramDate = new Date(param);
    var diff = today.getTime() - paramDate.getTime();
    diff = Math.round(diff / (1000 * 3600 * 24));
    return diff - 1;
  };
  const today = new Date();
  const yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [startDate, setStartDate] = useState(formatDate(today));
  const [endDate, setEndDate] = useState(formatDate(today));
  const [params, setParams] = useState({
    paramStart: dateDifference(formatDate(today)),
    paramFinish: dateDifference(formatDate(today)),
  });

  let [loading, setLoading] = useState(true);

  useEffect(()=>{
      setParams({
        paramStart: dateDifference(startDate),
        paramFinish: dateDifference(endDate),
      });
  },[startDate,endDate]);
  useEffect(() => {
    setLoading(true);
    getDropCall(params.paramStart, params.paramFinish, limit, offset).then(resp => {
      var list = JSON.parse(resp.data.data);
      getQueueNumber().then(queueList => {
        for (let i = 0; i < list.length; i++) {
          JSON.parse(queueList.data.data).map(queue => {
            if (list[i].q_num === queue.dn) {
              list[i].q_num = queue.dn + '-' + queue.display_name;
            }
            return null;
          });
        }
        setResponse(list);
      });
      setLoading(false);
    });

    getDropCallSummary(params.paramStart, params.paramFinish).then(resp => {
      setResponseSummary(JSON.parse(resp.data.data));
    });

    getDropCallCount(params.paramStart, params.paramFinish).then(resp => {
      setCount(JSON.parse(resp.data.data)[0]);
    });
  }, [params, offset, limit, setCount, setResponse, setResponseSummary]);

  return (
    <div>
      <DropCallLogDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
