import { getNewDate } from '@jumbo/utils/dateHelper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Button, IconButton, Paper, TablePagination } from '@material-ui/core';
import {
  AccessTime,
  Check,
  CloudDownload,
  DateRange,
  Person,
  PhoneDisabled,
  PlayArrow,
  GetApp,
  PhoneMissed,
} from '@material-ui/icons';
import { getPath, getTagTypes, getRecordingBytes, getQueues, getTagsByCallId } from 'api/api';
import React, { useEffect, useState, useContext } from 'react';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { DataGrid } from '@mui/x-data-grid';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CallManagementCallDetail from '../CallDetails';
import CallSurvey from '../CallSurvey';
import VisibilityIcon from '@mui/icons-material/Visibility';
import Tag from './Tag';
import Score from './Score';
import SurveyScore from './SurveyScore';
import { localeText } from '@jumbo/utils/DataGridLocales';
import AudioWaveForm from '@jumbo/utils/AudioWaveForm';
import LinearProgress from '@mui/material/LinearProgress';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
import SendToMobileIcon from '@mui/icons-material/SendToMobile';
import TtyIcon from '@mui/icons-material/Tty';
import { darken, lighten } from '@mui/material/styles';

export default function CallManagementTable({
  data,
  limit,
  setLimit,
  setOffset,
  page,
  setPage,
  count,
  setReloadValues,
  reloadValues,
  offset,
  scoreFilter,
  tagFilter,
}) {
  const [listenedRecords, setListenedRecords] = useState([]);
  const getBackgroundColor = (color, mode) => (mode === 'dark' ? darken(color, 0.6) : lighten(color, 0.6));

  const getHoverBackgroundColor = (color, mode) => (mode === 'dark' ? darken(color, 0.5) : lighten(color, 0.5));

  let [path, setPath] = useState('');
  const [queues, setQueues] = useState([]);
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setPage(0);
  };

  const audioModalStyle = {
    boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px',
    borderRadius: '8px',
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: '#ededed',
    width: '600px',
  };
  const { recordLoading, setRecordLoading } = useContext(AppContext);
  const [record, setRecord] = useState(null);
  const [openAudioModal, setOpenAudioModal] = useState(false);
  const listenRecord = (dir, num) => {
    setRecordLoading(true);
    document.body.style.cursor = 'wait';
    getRecordingBytes(path, dir)
      .then(resp => resp.arrayBuffer())
      .then(buffer => {
        if (buffer.byteLength > 0) {
          setRecord(buffer);
          setListenedRecords(prev => [...prev, num]);
          setOpenAudioModal(true);
        } else {
          setRecordLoading(false);
          document.body.style.cursor = 'default';
          NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000);
        }
      })
      .catch(err => {
        setRecordLoading(false);
        document.body.style.cursor = 'default';
        NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000);
      });
  };
  const downloadRecord = dir => {
    getRecordingBytes(path, dir)
      .then(resp => resp.blob())
      .then(blob => {
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = getCorrectFilesHour(dir);
        link.click();
        link.remove();
      })
      .catch(err => NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000));
  };

  const getCorrectFilesHour = dir => {
    var timeOffset = localStorage.getItem('timeoffset');
    var copyarr = [...dir.split('')];
    var hour = dir.split('_')[2].slice(8, 10);
    var hourIndex = dir.lastIndexOf('_') + 9;
    var newtime = 0;
    if (timeOffset[0] === '+') {
      newtime = parseInt(hour) + parseInt(timeOffset.slice(1));
    } else {
      newtime = parseInt(hour) - parseInt(timeOffset.slice(1));
    }
    if (newtime < 10) {
      newtime = `0${newtime}`;
    } else {
      newtime.toString();
    }
    copyarr.splice(hourIndex, 2, newtime);
    return copyarr.join('');
  };

  let curentLanguage = useIntl();

  const [rightMenu, setRightMenu] = useState(false);
  const [callIdEye, setCallIdEye] = useState('');
  const handleClickOpenEye = callId => {
    setCallIdEye(callId);
    setRightMenu(true);
  };

  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
      width: 40,
    },
    {
      field: 'callId',
      headerName: <IntlMessages id={'detail'} />,
      width: 70,
      renderCell: params => (
        <span onClick={() => handleClickOpenEye(params.value)} style={{ cursor: 'pointer' }}>
          <VisibilityIcon style={{ color: '#FF1700', verticalAlign: '-5px' }} />
        </span>
      ),
    },
    {
      field: 'listen',
      headerName: <IntlMessages id={'listen'} />,
      width: 70,
      renderCell: params => (
        <span style={{ cursor: 'pointer' }}>
          {params.value !== 'no recording' && (
            <IconButton
              onClick={() => listenRecord(params.value, params.row.num)}
              style={{ backgroundColor: '#C6D57E', color: '#406343' }}
              size="small"
              aria-label="listen">
              <PlayArrow />
            </IconButton>
          )}
        </span>
      ),
    },
    {
      field: 'download',
      headerName: <IntlMessages id={'download'} />,
      width: 70,
      renderCell: params => (
        <span style={{ cursor: 'pointer' }}>
          {params.value !== 'no recording' && (
            <IconButton
              onClick={() => downloadRecord(params.value)}
              style={{ backgroundColor: '#1769aa', color: '#fff' }}
              size="small"
              aria-label="listen">
              <GetApp />
            </IconButton>
          )}
        </span>
      ),
    },
    {
      field: 'date',
      headerName: <IntlMessages id={'date'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#519259' }}>
            <DateRange style={{ verticalAlign: '-5px' }} />{' '}
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'startTime',
      headerName: <IntlMessages id={'starttime'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: '#DB6B97' }}>
          <AccessTime style={{ verticalAlign: '-5px' }} /> {params.value}
        </span>
      ),
    },
    {
      field: 'caller',
      headerName: <IntlMessages id={'callers'} />,
      width: 250,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#98BAE7' }}>
            <Person style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'called',
      headerName: <IntlMessages id={'calledd'} />,
      width: 250,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#9AE66E' }}>
            <Person style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value}
        </span>
      ),
    },
    {
      field: 'srcDid',
      headerName: <IntlMessages id={'didnumber'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: 'gray' }}>
          <span style={{ color: '#d6851a' }}>
            <TtyIcon style={{ verticalAlign: '-5px' }} />
          </span>
          {params.value || '-'}
        </span>
      ),
    },
    {
      field: 'direction',
      headerName: <IntlMessages id={'direction'} />,
      width: 110,
      renderCell: params => (
        <span>
          {params.value[0] === 'i√ßeri' && (
            <span
              style={{
                backgroundColor: '#9AE66E',
                color: '#497c4f',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'incomingcall'} />
            </span>
          )}
          {params.value[0] === 'dƒ±≈üarƒ±' && (
            <span
              style={{
                backgroundColor: '#FF6D6D',
                color: '#AE431E',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'outgoingcall'} />
            </span>
          )}
          {params.value[0] === 'dahili' && (
            <span
              style={{
                backgroundColor: '#FFBC97',
                color: '#FF7800',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <IntlMessages id={'agentedcall'} />
            </span>
          )}
          {params.value[0] === 'kuyruk' && (
            <span
              style={{
                backgroundColor: '#a3b8ff',
                color: '#053cf2',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              {queues.find(q => (q.dn = params.value[1]))?.display_name || <IntlMessages id="queue" />}
            </span>
          )}
        </span>
      ),
    },
    {
      field: 'duration',
      headerName: <IntlMessages id={'duration'} />,
      width: 130,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif', fontWeight: '600', color: '#DB6B97' }}>
          <AccessTime style={{ verticalAlign: '-5px' }} /> {params.value}
        </span>
      ),
    },
    {
      field: 'salable',
      headerName: <IntlMessages id={'state'} />,
      width: 150,
      renderCell: params => (
        <span style={{ fontFamily: 'sans-serif' }}>
          {params.value === 'answered' ? (
            <span
              style={{
                backgroundColor: '#B1E693',
                color: '#1C7947',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <Check fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'endingcall'} />
            </span>
          ) : params.value === 'nonanswered' ? (
            <span
              style={{
                backgroundColor: '#FF7777',
                color: '#852747',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <PhoneDisabled fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'notanswered'} />
            </span>
          ) : params.value === 'transfered' ? (
            <span
              style={{
                backgroundColor: '#bd88f7',
                color: '#27034f',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <SendToMobileIcon fontSize="small" style={{ verticalAlign: '-5px' }} />
              <IntlMessages id={'dailysummary.transfer'} />
            </span>
          ) : (
            <span
              style={{
                backgroundColor: '#f3bbfc',
                color: '#a402bd',
                padding: '5px',
                borderRadius: '7px',
                fontSize: '10px',
                fontWeight: '800',
              }}>
              <PhoneMissed fontSize="small" style={{ verticalAlign: '-5px' }} /> <IntlMessages id={'missedcall'} />
            </span>
          )}
        </span>
      ),
    },
    {
      field: 'Tag',
      headerName: <IntlMessages id={'tag'} />,
      width: 200,
      renderCell: params => (
        <div style={{ width: '100%' }}>
          <Tag call_id={params.value} tagsList={params.value !== '' ? tagsList : []} />
        </div>
      ),
    },
    {
      field: 'score',
      headerName: <IntlMessages id={'score'} />,
      width: 150,
      renderCell: params => (
        <div style={{ width: '100%' }}>
          <Score call_id={params.value} reloadValues={reloadValues} />
        </div>
      ),
    },
    {
      field: 'surveyScore',
      headerName: <IntlMessages id={'surveyScore'} />,
      width: 150,
      renderCell: params => (
        <div style={{ width: '100%' }}>
          <SurveyScore call_id={params.value} />
        </div>
      ),
    }
  ];

  const [tableData, setTableData] = useState([]);

  useEffect(() => {
    let canListable = id => {
      return new Promise((resolve, reject) => {
        getTagsByCallId(id).then(resp => {
          let data = JSON.parse(resp.data.data)[0];
          let result = false;
          if (data) {
            if (tagFilter !== '' && scoreFilter !== '') {
              if (data.tagtext === tagFilter && data.rating === scoreFilter) {
                result = true;
              }
            } else {
              if (tagFilter !== '' && data.tagtext === tagFilter) {
                result = true;
              }
              if (scoreFilter !== '' && data.rating === scoreFilter) {
                result = true;
              }
            }
          }
          resolve(result ? id : '');
        });
      });
    };
    if (tagFilter !== '' || scoreFilter !== '') {
      let allRequests = [];
      data.forEach(item => allRequests.push(canListable(item.call_id)));
      Promise.all(allRequests).then(ids => {
        let tabledata = data.reduce((filtered, item, index) => {
          if (ids.includes(item.call_id)) {
            let value = {
              num: index + 1 + offset,
              callId: item.call_id,
              listen: item.recording_url,
              download: item.recording_url,
              date: getNewDate(item.date),
              startTime: item.hour,
              srcDid: item.src_did,
              caller:
                item.src_display_name === item.src_dn || item.src_dn === '-'
                  ? item.src_display_name
                  : item.src_display_name + '(' + item.src_dn + ')',
              called:
                item.dst_display_name === item.dst_dn || item.dst_dn === '-'
                  ? item.dst_display_name
                  : item.dst_display_name + '(' + item.dst_dn + ')',
              direction: [item.direction, item.dst_dn],
              duration: item.duration,
              salable: item.statusofcall,
              id: index,
              Tag: isTaggable(item.direction, item.statusofcall) ? item.call_id : '',
              score: item.call_id,
              surveyScore:item.call_id
            };
            filtered.push(value);
          }
          return filtered;
        }, []);
        setTableData(tabledata);
      });
    } else {
      let tabledata = data.map((item, index) => ({
        num: index + 1 + offset,
        callId: item.call_id,
        listen: item.recording_url,
        download: item.recording_url,
        date: getNewDate(item.date),
        startTime: item.hour,
        srcDid: item.src_did,
        caller:
          item.src_display_name === item.src_dn || item.src_dn === '-'
            ? item.src_display_name
            : item.src_display_name + '(' + item.src_dn + ')',
        called:
          item.dst_display_name === item.dst_dn || item.dst_dn === '-'
            ? item.dst_display_name
            : item.dst_display_name + '(' + item.dst_dn + ')',
        direction: [item.direction, item.dst_dn],
        duration: item.duration,
        salable: item.statusofcall,
        id: index,
        Tag: isTaggable(item.direction, item.statusofcall) ? item.call_id : '',
        score: item.call_id,
        surveyScore:item.call_id
      }));
      setTableData(tabledata);
    }
  }, [tagFilter, scoreFilter, data, offset]);

  const [tagsList, setTagsList] = useState([]);
  useEffect(() => {
    getTagTypes().then(resp => setTagsList(JSON.parse(resp.data.data)));
    getPath().then(res => {
      setPath(res.data);
    });
    getQueues().then(que => setQueues(JSON.parse(que.data.data)));
  }, []);

  return (
    <>
      <NotificationContainer />
      {data.length > 0 && (
        <div style={{ marginTop: '10px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Call_Log'
                : curentLanguage.locale === 'tr-TR'
                ? 'Cagri_raporu'
                : curentLanguage.locale === 'nl-NL'
                ? 'Oproep_Log'
                : curentLanguage.locale === 'de-DE'
                ? 'Anrufliste'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={data} name="data">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="date"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'From'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Kimden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Van'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Von'
                    : ''
                }
                value="src_display_name"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Destination'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Hedef'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Bestemming'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Ziel'
                    : ''
                }
                value="dst_display_name"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Direction'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Y√∂n'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Richting'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Genaamd'
                    : ''
                }
                value="direction"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Duration'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'S√ºre'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Looptijd'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Dauer'
                    : ''
                }
                value="duration"
              />
              <ExcelColumn label={curentLanguage.locale === 'en-US' ? 'Case' : 'Durum'} value="salable" />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Drawer anchor={'right'} open={rightMenu} onClose={() => setRightMenu(false)}>
        <div style={{ width: '600px', padding: '2rem 1.5rem' }}>
          <CallManagementCallDetail callId={callIdEye} setReloadValues={setReloadValues} />
          <CallSurvey callId={callIdEye} />
        </div>
      </Drawer>
      <Modal
        open={openAudioModal}
        onClose={() => setOpenAudioModal(false)}
        aria-labelledby="Audio"
        aria-describedby="Audio Player Modal">
        <Box style={audioModalStyle}>
          <Paper
            style={{
              display: 'flex',
              justifyContent: 'flex-end',
              borderRadius: 'inherit',
              borderBottomLeftRadius: '0',
              borderBottomRightRadius: '0',
            }}>
            <Button onClick={() => setOpenAudioModal(false)}>X</Button>
          </Paper>
          <Box style={{ padding: '12px 8px' }}>
            <AudioWaveForm buffer={record} />
          </Box>
        </Box>
      </Modal>
      <Paper style={{ marginTop: '.5rem' }}>
        {recordLoading && (
          <div style={{ width: '100%', marginTop: '8px' }}>
            <LinearProgress />
          </div>
        )}
        <Box
          style={{ height: '440px', width: '100%' }}
          sx={{
            '& .Calllog-Table-Listened': {
              bgcolor: getBackgroundColor('#ffeb7a', 'light'),
              '&:hover': {
                bgcolor: getHoverBackgroundColor('#ffeb7a', 'light'),
              },
            },
          }}>
          <DataGrid
            disableSelectionOnClick
            getRowClassName={params => listenedRecords.includes(params.row.num) && `Calllog-Table-Listened`}
            rows={tableData}
            columns={columns}
            rowHeight={38}
            hideFooter
            localeText={localeText}
          />
        </Box>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={count}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={tableData.length < limit || tableData.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          backIconButtonText={String(tableData.length)}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
}
const nonTaggableCallTypes = ['dahili', ''];
const isTaggable = (callType, status) => {
  if (status === 'answered') {
    if (!nonTaggableCallTypes.includes(callType)) {
      return true;
    }
  }
  return false;
};
