import { Box, Button, Modal, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { deleteUserByUserId, deleteUserOwndedDepsByUserId, getUsers } from '../../../../api/api';
import AddIcon from '@mui/icons-material/Add';
import AddUserModal from './AddUserModal';
import EditIcon from '@mui/icons-material/Edit';
import UpdateUserModal from './UpdateUserModal';
import DeleteIcon from '@mui/icons-material/Delete';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function UserTable({ reloadValues, setReloadValues }) {
  let [users, setUsers] = useState([]);
  useEffect(() => {
    getUsers().then(result => {
      setUsers(JSON.parse(result.data.data));
    });
  }, [reloadValues]);

  const [addModal, setAddModal] = useState(false);
  const addHandleOpen = () => setAddModal(true);
  const addHandleClose = () => setAddModal(false);

  const [updateModal, setUpdateModal] = useState(false);
  const [selectedUser, setSelectedUser] = useState({ id: '0', name: '', pass: '', type: '' });
  const updateHandleOpen = () => setUpdateModal(true);
  const updateHandleClose = () => setUpdateModal(false);
  const handleClickEdit = user => {
    setSelectedUser(user);
    updateHandleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '50%',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const handleDeleteClick = userId => {
    deleteUserOwndedDepsByUserId(userId).then(res1 => {
      deleteUserByUserId(userId).then(res2 => {
        setReloadValues(userId.toString());
      });
    });
  };
  const username = localStorage.getItem("username") || "admin";

  return (
    <>
      <div style={{marginBottom: '10px',display:'flex',justifyContent:'space-between' }}>
        <h2 style={{fontWeight:'300'}}><IntlMessages id={'welcome'} />, <b>{username}</b></h2>
        <Button sx={{ backgroundColor: '#3E7C17', color: 'white' }} onClick={() => addHandleOpen()}>
          <AddIcon sx={{ verticalAlign: '-5px' }} /> <IntlMessages id={'adduser'} />
        </Button>
      </div>
      <TableContainer component={Paper}>
        <Table sx={{ maxHeight: '500px' }}>
          <TableHead>
            <TableRow>
              <TableCell>
                <IntlMessages id={'username'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'password'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'usertype'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'edit'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'delete'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map(user => (
              <TableRow key={user.id}>
                <TableCell>{user.name}</TableCell>
                <TableCell>********</TableCell>
                <TableCell>{user.type}</TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleClickEdit(user)}>
                  <EditIcon sx={{ color: '#B33030' }} />
                </TableCell>
                <TableCell sx={{ cursor: 'pointer' }} onClick={() => handleDeleteClick(user.id)}>
                  <DeleteIcon sx={{ color: '#B33030' }} />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal
        open={addModal}
        onClose={addHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <AddUserModal addHandleClose={addHandleClose} setReloadValues={setReloadValues} />
        </Box>
      </Modal>
      <Modal
        open={updateModal}
        onClose={updateHandleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <UpdateUserModal
            selectedUser={selectedUser}
            setReloadValues={setReloadValues}
            updateHandleClose={updateHandleClose}
          />
        </Box>
      </Modal>
    </>
  );
}
